import * as React from 'react';
import styled from 'styled-components';
import director from '../director';
import autoBind from 'auto-bind';
import layers from '../constants/layers';
import config from '../constants/config';
import moment from 'moment-timezone';
import { TimelineLite, Power2 } from 'gsap';

const Outer = styled.div`
  position: absolute;
  left: ${(p: { left: number, top: number }) => p.left}px;
  top: ${(p) => p.top}px;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-around;
  z-index: ${layers.foreground + 5};
  font-size: 35px;
  font-weight: 600;
  height: 74px;
  width: 1300px;
`;

const WorldTime = styled.div`
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  align-items: center;
`;

interface Props {
  actorName: string;
  entryDuration?: number;
  top: number;
  left: number;
}

interface State {
  times: string[];
}

class WorldClocks extends React.Component<Props, State>  {
  container: Element;
  children: Element[];
  tl: TimelineLite;

  constructor(props: Props) {
    super(props);
    autoBind(this);
    this.children = [];
    director.registerMotions(this, props.actorName,
      {
        enter: this.enter,
        exit: this.exit,
      }
    );
    this.state = {
      times: config.timezones.map((tz) => moment().tz(tz.key).format('HH:mm')),
    };
  }

  enter(duration: number, tl: TimelineLite) {
    const { children } = this;
    const dur = duration * 3 / children.length;
    const shrinkDur = dur * 0.7;
    const delayShrink = dur * 0.3;
    const stagger = (duration - dur) / children.length;
    const now = tl.time();
    tl.staggerFrom(children, dur, {
      opacity: 0,
      ease: Power2.easeIn,
    }, stagger, now)
      .staggerFrom(children, shrinkDur, {
        scale: 1.4,
        ease: Power2.easeInOut,
      }, stagger, now + delayShrink);
    this.startTimeUpdates();
  }

  startTimeUpdates() {
    const tl = new TimelineLite();
    tl.call(this.timeUpdateRAF, [], this, 3);
    tl.eventCallback('onComplete', tl.restart, [], tl);
    this.tl = tl;
    director.addActorTimeline(this.props.actorName, tl);
  }

  exit(duration: number, tl: TimelineLite) {
    const { children } = this;
    const dur = duration * 3 / children.length;
    const growDur = dur * 0.7;
    const stagger = (duration - dur) / children.length;
    const now = tl.time();
    tl.staggerTo(children, dur, {
      opacity: 0,
      ease: Power2.easeOut,
    }, stagger, now)
      .staggerTo(children, growDur, {
        scale: 1.4,
        ease: Power2.easeInOut,
      }, stagger, now);
  }

  timeUpdate() {
    const times = config.timezones.map((tz) => moment().tz(tz.key).format('HH:mm'));
    this.setState({ times });
  }

  timeUpdateRAF() {
    requestAnimationFrame(this.timeUpdate);
  }

  render() {
    const { left, top } = this.props;
    const { times } = this.state;
    return (
      <Outer
        innerRef={ref => { if (ref) this.container = ref; }}
        top={top}
        left={left}
      >
        {config.timezones.map((tz, i) => {
          const time = times[i];
          return (
            <WorldTime key={i} innerRef={r => { if (r) this.children.push(r); }}>
              <div style={{ color: '#888', fontWeight: 600 }}>{tz.display}</div>
              <div style={{ color: '#FFFFFF' }}>{time}</div>
            </WorldTime>
          );
        })}
      </Outer >
    );
  }
}

export default WorldClocks;



