import * as React from 'react';
import styled from 'styled-components';
import Icon from 'react-icons/lib/md/account-circle';
import autoBind from 'auto-bind';
import layers from '../constants/layers';
import theme from '../constants/theme';

const Outer = styled.div`
  width: 523px;
  height: 100px;
  display: flex;
  flex-flow: column nowrap;
  margin: 12px;
  margin-top: 45px;
  transform: translate(0, 0);
  z-index: ${layers.foreground};
  ${(p: { overlay?: boolean }) => p.overlay ? `
  background-color: ${theme.backgroundOverlay};
  box-shadow: 0px 0px 49px 70px ${theme.backgroundOverlay};
  border-radius: 40px;` : ''}
`;

const AccountIcon = styled(Icon) `
  position: absolute;
  left: -47px;
  top: 9px;
`;

const Row = styled.div`
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
`;

const Name = styled.div`
  margin: 5px;
  font-size: 35px;
  font-weight: 300;
  margin-right: 10px;
`;

const Role = styled.div`
  margin: 5px;
  font-size: 25px;
  font-weight: 400;
`;

const Browser = styled.div`
  margin: 5px;
  font-size: 25px;
  font-weight: 400;
`;

const Vulnerabilities = styled.div`
  margin: 5px;
  font-size: 25px;
  font-weight: 600;
  color: ${theme.warningRed};
`;

interface Props {
  actorName: string;
  entryDuration: number;
  delay: number;
  name: string;
  role: string;
  browser: string;
  vulnerabilities: string;
  overlay?: boolean;
  setRef: any;
}

class HostItem extends React.Component<Props>  {
  container: Element;

  constructor(props: Props) {
    super(props);
    autoBind(this);
  }

  render() {
    const { name, browser, role, vulnerabilities, setRef, overlay } = this.props;
    return (
      <Outer innerRef={setRef} overlay={overlay}>
        <AccountIcon color={theme.hostItemIcon} size="41px" />
        <Row>
          <Name>{name}</Name>
          <Browser>{role}</Browser>
        </Row>
        <Row>
          <Role>{browser}</Role>
          <Vulnerabilities>{vulnerabilities}</Vulnerabilities>
        </Row>
      </Outer>
    );
  }
}

export default HostItem;



