import styled from 'styled-components';
import layers from '../constants/layers';
import director from '../director';
import React, { Component } from 'react';
import autoBind from 'auto-bind';
import { simpleEnter, simpleExit } from '../motions';
import { TimelineLite, Power2 } from 'gsap';
import spacing from '../constants/spacing';
import theme from '../constants/theme';

interface Props {
  actorName: string;
  entryDuration?: number;
  width: number;
}

const StyledDiv = styled.div`
  position: absolute;
  top: 0;
  width: ${(p: { width: number }) => p.width}px;
  height: 100%;
  background: ${theme.sidePanel};
  z-index:${layers.midground};
`;

class SidePanel extends Component<Props> {
  container: Element;
  children: Element[];

  constructor(p: Props) {
    super(p);
    autoBind(this);
    director.registerMotions(this, p.actorName,
      {
        enter: simpleEnter(this, 0, 0),
        exit: simpleExit(this, 0, 0),
        toFullCover: this.toFullCover,
        toPartialReveal: this.toPartialReveal,
        toPartialCover: this.toPartialCover,
        toFullReveal: this.toFullReveal,
      });
  }

  // only leaving the right panel open
  toFullCover(dur: number, tl: TimelineLite) {
    tl.to(this.container, dur, {
      width: spacing.rightPanelStart,
      ease: Power2.easeInOut,
    }, tl.time());
  }

  // open a little room to the left of the right panel
  toPartialReveal(dur: number, tl: TimelineLite) {
    tl.to(this.container, dur, {
      width: spacing.leftPanelEndAlt2,
      ease: Power2.easeInOut,
    }, tl.time());
  }

  // open but leave a little blue to the right of the map
  toPartialCover(dur: number, tl: TimelineLite) {
    tl.to(this.container, dur, {
      width: spacing.leftPanelEndAlt1,
      ease: Power2.easeInOut,
    }, tl.time());
  }

  // leave only the map section blue
  toFullReveal(dur: number, tl: TimelineLite) {
    tl.to(this.container, dur, {
      width: spacing.leftPanelEnd,
      ease: Power2.easeInOut,
    }, tl.time());
  }

  render() {
    const { width } = this.props;
    return (
      <StyledDiv
        width={width}
        innerRef={r => { if (r) this.container = r; }}
      />
    );
  }
}

export default SidePanel;


