import React, { SFC } from 'react';
import styled from 'styled-components';
import IconUser from 'react-icons/lib/md/account-circle';
import layers from '../constants/layers';
import AgentImg from '../assets/Mack.png';
import theme from '../constants/theme';

const Root = styled.div`
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: ${layers.foreground + 1};
`;

const _iconWidth = 110;

const IconOuter = styled.div`
  position: absolute;
  top: -80px;
  right: -20px;
  width: ${_iconWidth}px;
  height: ${_iconWidth}px;
  border-radius: 50%;
  background: ${(p: { color: string }) => p.color};
  display: flex;
  justify-content: center;
  align-items: center;
`;

const IconCircle = styled.div`
  width: ${_iconWidth - 24}px;
  height: ${_iconWidth - 24}px;
  border-radius: 50%;
  border: 5px solid white;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const _leftText = 35;
const Title = styled.div`
  position:absolute;
  top: 38px;
  left: ${_leftText + 97}px;
  font-size: 42px; font-weight: 500;
`;

const Avatar = styled.img`
  position:absolute;
  height: 78px;
  width: 78px;
  top: 25px;
  left: ${_leftText}px;
`;

interface Props {
  title: string;
}
const CardAgentFeatures: SFC<Props> = props => {
  const { title } = props;
  return (
    <Root>
      <IconOuter color={theme.cardAgentIconOuter}>
        <IconCircle>
          <IconUser size="124px" color={theme.cardAgentIcon} />
        </IconCircle>
      </IconOuter>
      <Avatar src={AgentImg} />
      <Title>{title}</Title>
    </Root >
  );
};

export default CardAgentFeatures;



