import * as React from 'react';
import styled from 'styled-components';
import director from '../director';
import { TimelineLite, Power2, Back } from 'gsap';
import autoBind from 'auto-bind';
import layers from '../constants/layers';
import { simpleExit } from '../motions';
import Svg from './Svg';
import theme from '../constants/theme';

const IconGroup = styled.g`
  color: ${theme.iconArray};
  transform: ${(p: { x: number, y: number }) => `translate(${p.x}px, ${p.y}px) scale(0.3)`};
`;

interface Props {
  actorName: string;
  entryDuration?: number;
  left: number;
  iconType: string;
  childEntryAmt: number;
  positions: ({
    x: number;
    y: number;
    start: number;
  })[];
}

class IconArray extends React.Component<Props>  {
  container: Element;
  children: Element[];
  infectedNode: Element;

  constructor(props: Props) {
    super(props);
    autoBind(this);
    this.children = [];

    director.registerMotions(this, props.actorName,
      {
        enter: this.enter,
        exit: simpleExit(this, 50, 0),
        infect: this.infect,
      }
    );
  }

  enter(dur: number, tl: TimelineLite) {
    const { children } = this;
    const { childEntryAmt, positions } = this.props;
    const childEntryDur = childEntryAmt * dur;
    const now = tl.time();

    positions.forEach((pos, i) => {
      tl.from(children[i], childEntryDur, {
        opacity: 0,
        scale: 0,
        ease: Back.easeOut,
      }, pos.start * dur + now);
    });

  }

  infect(dur: number, tl: TimelineLite) {
    if (!this.infectedNode) return;
    const now = tl.time();
    tl.to(this.infectedNode, dur, {
      color: theme.warningRed,
      scale: 1.2,
      ease: Power2.easeInOut,
      transformOrigin: 'center center'
    }, now);
  }

  addChildRef(ref: SVGLineElement) {
    if (ref)
      this.children.push(ref);
  }

  render() {
    const { left, positions } = this.props;
    return (
      <Svg
        width="3000"
        height="1000"
        left={left}
        top={0}
        zIndex={layers.foreground + 2}
        innerRef={ref => { if (ref) this.container = ref; }}
      >
        <defs>
          <path d="M176 72.27l1.91 70.298-83.27 52.577-83.461-52.577L13 95.267v-29.69L94.64 13 176 65.577v6.692z" id="iconpath-a" />
        </defs>
        {positions.map((p, n) => (
          <IconGroup
            x={p.x}
            y={p.y - 5}
            key={n}
            innerRef={this.addChildRef}
            fill="none"
            fillRule="evenodd"
          >
            <g ref={n === 3 ? (r) => { if (r) this.infectedNode = r; } : undefined}>
              <use fill="currentColor" xlinkHref="#iconpath-a" />
              <path stroke={theme.iconArrayInner} strokeWidth="7" d="M173.5 66.938L94.638 15.975 15.5 66.941l-.002 28.422-1.766 45.86L94.64 192.19l80.733-50.976-1.871-68.877-.001-.068v-5.331z" />
              <path stroke="currentColor" strokeWidth="10.5" d="M181.25 72.198l1.989 73.214-88.595 55.94-88.828-55.957 1.934-50.23V62.714l86.895-55.96 86.605 55.966v9.479zM7.75 95.166l.004-.101-.004.202v-.101z" />
            </g>
          </IconGroup>
        ))}
      </Svg>
    );
  }
}

export default IconArray;



