import * as React from 'react';
import * as ReactDOM from 'react-dom';
import g, { getPresentation } from './constants/config';
import Stage from './stage';
import director from './director';
import registerServiceWorker from './registerServiceWorker';
import getScript from './script';
import 'gsap/TextPlugin';
import 'gsap/ModifiersPlugin';
import './global.css';
import { setResizing } from './utils/resize';
import { setFPS } from './utils/configGSAP';
import './director/controller';
import './utils/positioning';
import { setCurrentSetupSelections } from './utils/clientSetup';
import { addVerticalOptionsToSelect } from './utils/threatActors';
import { startSocket, startControllerSocket } from './sockets';
import controller from './director/controller';

// change the presentation and restart if needed
export const setPresentation = (presentationName: string) => {
  const cur = getPresentation();
  if (presentationName === cur) return;
  controller.pause();
  director.clearActors();
  director.tl.clear();
  director.tl.seek(0, true);
  // hardcode to lateral presentation
  director.setScript(getScript(), ['useCases', 'threatActors', 'lateral']);
  g.presentation = 'lateral';

  const el = document.getElementById('controlPlay');
  if (el)
    el.textContent = 'PLAY';
};

if (!g.isController) {
  ReactDOM.render(
    <Stage />,
    document.getElementById('root') as HTMLElement
  );
  // initialize the director with the generated script
  director.setScript(getScript(), ['useCases', 'threatActors', g.presentation]);

  setFPS();
  setResizing();
}

addVerticalOptionsToSelect();
setCurrentSetupSelections();

// subscribe to control group events or connect as a control group Controller
if (g.controlGroup > -1) {
  if (g.isController)
    startControllerSocket();
  else
    startSocket();
}
// this is from CRA
registerServiceWorker();


