import * as React from 'react';
import styled from 'styled-components';
import director from '../director';
import autoBind from 'auto-bind';
import layers from '../constants/layers';
import { simpleEnter, simpleExit, typeText, moveBy } from '../motions';
import { TimelineLite, Power2 } from 'gsap';
import CardAgentFeatures from './CardAgentFeatures';
import t from '../constants/theme';

const _defaultWidth = 900;
const _defaultHeight = 410;

const Root = styled.div`
  position: absolute;
  left: ${(p: { top: number, left: number, height: number, width: number }) => p.left}px;
  top: ${(p) => p.top}px;
  width: ${p => p.width}px;
  height: ${p => p.height}px;
  z-index: ${layers.foreground};
  filter: drop-shadow(5px 12px 17px ${t.cardAgentDropShadow});
`;

const CardRect = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 20px;
  padding-left: 50px;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  background: ${t.cardBackground};
  border: 5.2px solid ${t.cardBackground};
`;

const _leftText = 35;

const Detail = styled.div`
  position:absolute;
  top: 105px;
  left: ${_leftText}px;
  color: ${t.cardAgentDetail}; font-size: 34px; font-weight: 500;
`;

const Code = styled.div`
  position:absolute;
  left: ${_leftText}px;
  color: ${(p) => p.codeStyle && p.codeStyle.color ? p.codeStyle.color : t.cardAgentCodeDefault}; ; 
  font-size: ${(p: { codeStyle: any }) => p.codeStyle && p.codeStyle.fontSize ? p.codeStyle.fontSize : 26}px; 
  font-weight: ${(p: { codeStyle: any }) => p.codeStyle && p.codeStyle.fontWeight ? p.codeStyle.fontWeight : 500}; 
  top: ${p => p.codeStyle && p.codeStyle.top ? p.codeStyle.top : 223}px;
  font-family: Consolas,Lucida Console,monospace;
  display: flex;
  flex-flow: column nowrap;
`;

const CodeLine = styled.div`
  padding-left: ${(p: { offset: number }) => p.offset * 10}px;  
`;

interface Props {
  actorName: string;
  entryDuration?: number;
  top: number;
  left: number;
  title: string;
  detail: string;
  codeLines: string[];
  codeOffsets: number[];
  height?: number;
  width?: number;
  codeStyle?: {
    fontSize: number;
    color: number;
  };
}

class CardAgent extends React.Component<Props>  {
  container: Element;
  children: Element[];

  constructor(props: Props) {
    super(props);
    autoBind(this);
    director.registerMotions(this, props.actorName,
      {
        enter: simpleEnter(this, '-=50', '+=0'),
        toHeight: this.toHeight,
        moveBy: this.moveBy,
        typeCode: this.typeCode,
        exit: simpleExit(this, '+=0', '-=50'),
      }
    );
    this.children = [];
  }

  typeCode(dur: number, tl: TimelineLite) {
    const { codeLines } = this.props;
    const totalChars = codeLines.reduce((prev, cur) => prev + cur.length, 0);
    let delay = 0;
    codeLines.forEach((line, i) => {
      const duration = ((line.length) / totalChars) * dur;
      if (!this.children[i]) return;
      typeText(this, this.children[i], line, delay)(duration, tl);
      delay += duration;
    });
  }

  toHeight(dur: number, tl: TimelineLite, payload: { height: number }) {
    tl.to(this.container, dur, {
      height: payload.height,
      ease: Power2.easeInOut
    }, tl.time());
  }

  moveBy(dur: number, tl: TimelineLite, payload: { x: number, y: number }) {
    moveBy(this, payload.x, payload.y)(dur, tl);
  }

  render() {
    const { title, detail, codeLines, codeOffsets, top, left, height, width, codeStyle } = this.props;
    return (
      <Root
        innerRef={ref => { if (ref) this.container = ref; }}
        top={top}
        left={left}
        height={height || _defaultHeight}
        width={width || _defaultWidth}
      >
        <CardAgentFeatures title={title} />
        <CardRect>
          <Detail>{detail}</Detail>
          <Code
            codeStyle={codeStyle}
          >
            {codeLines.map((code: string, i: number) => (
              <CodeLine
                key={i}
                innerRef={ref => { if (ref) this.children.push(ref); }}
                offset={codeOffsets[i] || 0}
              />
            ))}
          </Code>
        </CardRect>
      </Root >
    );
  }
}

export default CardAgent;



