import * as React from 'react';
import actorComponentAndProps from './props';
import director from '../director';
const autoBind = require('auto-bind');
import styled from 'styled-components';
import layers from '../constants/layers';
import spacing from '../constants/spacing';
import UseCaseZoomContainer from './UseCaseZoomContainer';
import theme from '../constants/theme';
import { Power1, TweenLite } from 'gsap';
// 'Stage' component - adds and removes Actors to the stage.

const Outer = styled.div`
  width: ${spacing.fullWidth}px;
  height: ${spacing.fullHeight}px;
  background: ${theme.sidePanel};
  position: relative;
  z-index: ${layers.background - 1};
`;

interface State {
  actors: string[];
  lastDuration?: number;
}

class Stage extends React.Component<{}, State> {
  container: Element;

  constructor(props: {}) {
    super(props);
    autoBind(this);

    this.state = {
      actors: [],
    };
    // subscribe to actions from the director
    director.subscribe({ actorName: 'Stage', actionHandler: this.onChange });
  }
  onChange(action: { type: string, payload?: any }) {
    const actors = this.state.actors;
    if (action.type === 'enter' && !actors.includes(action.payload.actor)) {
      const added = this.state.actors.concat(action.payload.actor as string);
      this.setState({ actors: added, lastDuration: action.payload.duration });
    } else if (action.type === 'remove') {
      const filtered = this.state.actors.filter(actor => actor !== action.payload.actor);
      this.setState({ actors: filtered, lastDuration: action.payload.duration });
      director.cleanSubscriptions(filtered);
    } else if (action.type === 'begin') {
      TweenLite.to(this.container, 2, {
        backgroundColor: theme.normalBackground,
        ease: Power1.easeInOut,
      });
    } else if (action.type === 'end') {
      TweenLite.to(this.container, 2, {
        backgroundColor: theme.sidePanel,
        ease: Power1.easeInOut,
      });
    }
    if (action.type === 'clearStage') {
      this.setState({ actors: [] });
      director.cleanSubscriptions([]);
    }
    if (action.type === 'removeNonSceneActors') {
      // remove actors that don't belong at the beginning of this scene,
      // leaving the ones that may remain if already there (persistActors)
      const filtered = this.state.actors.filter(actor => action.payload.startActors.includes(actor) || action.payload.persistActors.includes(actor));
      this.setState({ actors: filtered, lastDuration: 0.1 });
      director.cleanSubscriptions(filtered);
    }
  }

  render() {
    const { actors } = this.state;
    return (
      <Outer id="wallOuter" innerRef={r => { if (r) this.container = r; }}>
        {actors.filter(name => (actorComponentAndProps[name] !== undefined)
          && (actorComponentAndProps[name].renderIn === undefined))
          .map((name) => {
            const Actor = actorComponentAndProps[name];
            return <Actor.component
              entryDuration={this.state.lastDuration}
              key={name}
              actorName={name}
              {...Actor.props}
            />;
          })
        }
        <UseCaseZoomContainer actorName="UseCaseZoomContainer" entryDuration={0}>
          {actors.filter(name => (actorComponentAndProps[name] !== undefined)
            && (actorComponentAndProps[name].renderIn === 'UseCaseZoomContainer'))
            .map((name) => {
              const Actor = actorComponentAndProps[name];
              return <Actor.component
                entryDuration={this.state.lastDuration}
                key={name}
                actorName={name}
                {...Actor.props}
              />;
            })
          }
        </UseCaseZoomContainer>
      </Outer>
    );
  }
}

export default Stage;



