import * as React from 'react';
import styled from 'styled-components';
import director from '../director';
import { simpleExit } from '../motions';
import autoBind from 'auto-bind';
import layers from '../constants/layers';
import { TimelineLite, Power1 } from 'gsap';

const Outer = styled.div`
  position: absolute;
  left: ${(p: { left: number, top: number, disabled?: boolean }) => p.left}px;
  top: ${(p) => p.top}px;
  width: 320px;
  height: auto;
  z-index: ${layers.foreground + 5};
  padding: 28px;
  box-shadow: 13px 12px 60px -8px rgba(50, 50, 50, 0.5);
  border-radius: 35px;
  background-color: ${p => p.disabled ? '#2e6c85' : '#FFF'};
  overflow: hidden;
  ${p => p.disabled ? 'transform: scale(0.8); color: #0a335096' : ''};
`;

const Title = styled.div`
  font-size: 35px;
  font-weight: 400;
`;

const Item = styled.div`
  font-size: 23px;
  font-weight: 300;
  margin: 10px;
`;

interface Props {
  actorName: string;
  entryDuration?: number;
  title: string;
  items: string[];
  top: number;
  left: number;
  rootTop: number;
  rootLeft: number;
  disabled?: boolean;
}

class UseCaseTactic extends React.Component<Props>  {
  container: Element;
  children: Element[];

  constructor(props: Props) {
    super(props);
    autoBind(this);

    director.registerMotions(this, props.actorName,
      {
        enter: this.enter,
        enable: this.enable,
        disable: this.disable,
        exit: simpleExit(this, '-=10', 0),
      }
    );
  }

  enter(dur: number, tl: TimelineLite) {
    const now = tl.time();
    const { top, left, rootTop, rootLeft } = this.props;
    const xAmt = Math.round((rootLeft - left) * 0.03);
    const yAmt = Math.round((rootTop - top) * 0.03);
    tl.from(this.container, dur * 0.8, {
      height: 0,
      x: `+=${xAmt}`,
      y: `+=${yAmt + 20}`,
      opacity: 0,
      ease: Power1.easeOut
    }, now);
  }

  enable(dur: number, tl: TimelineLite) {
    const now = tl.time();
    tl.to(this.container, dur, {
      backgroundColor: '#FFF',
      color: '#0A3350',
      scale: 1,
      ease: Power1.easeInOut
    }, now);
  }

  disable(dur: number, tl: TimelineLite) {
    const now = tl.time();
    tl.to(this.container, dur, {
      backgroundColor: '#2e6c85',
      color: '#0a335070',
      scale: 0.8,
      ease: Power1.easeInOut
    }, now);
  }

  render() {
    const {
      left,
      top,
      title,
      items,
      disabled
    } = this.props;
    return (
      <Outer
        innerRef={ref => { if (ref) this.container = ref; }}
        top={top}
        left={left}
        disabled={disabled}
      >
        <Title>{title}</Title>
        {items.map((it, i) => <Item key={i}>{it}</Item>)}
      </Outer >
    );
  }
}

export default UseCaseTactic;



