import director from './';
import config from '../constants/config';
import wobbler from '../motions/wobbler';
import { order } from '../utils';
import autoBind from 'auto-bind';

const playbtn = document.getElementById('playbtn') as HTMLElement;

export const setPlayBtn = (ctrl: Controller) => {
  playbtn.innerHTML = ctrl.playing ? 'PAUSE' : 'PLAY';
};

// TODO: clean up this mess
export class Controller {
  public started: boolean;
  public playing: boolean;
  public onCompleteCallbacks: any[];
  public onStartCallbacks: any[];

  private pauseForTimeoutId: any;

  constructor() {
    autoBind(this);
    this.started = false;
    this.playing = false;
    this.onCompleteCallbacks = [];
    this.onStartCallbacks = [];

    director.onComplete = () => {
      this.playing = false;
      this.started = false;
      this.onCompleteCallbacks.forEach(fn => fn());
    };

    director.onStart = () => {
      this.playing = true;
      this.started = true;
      this.onStartCallbacks.forEach(fn => fn());
    };
  }

  playPause() {
    if (!this.playing) {
      if (!this.started) {
        director.begin();
      } else {
        director.tl.resume();
        director.playActorTimelines();
        this.playing = true;
        this.started = true;
        setPlayBtn(this);
      }
    } else {
      wobbler.stop();
      this.playing = false;
      setPlayBtn(this);
      director.tl.pause();
      director.pauseActorTimelines();
    }
  }

  play() {
    if (!this.playing) {
      if (!this.started) {
        director.begin();
      } else {
        director.tl.resume();
        director.playActorTimelines();
        this.playing = true;
        this.started = true;
        setPlayBtn(this);
      }
    }
  }

  pause() {
    if (this.playing) {
      this.playing = false;
      setPlayBtn(this);
      director.tl.pause();
      director.pauseActorTimelines();
    }
  }

  pauseFor(seconds: number) {

    const scale = director.tl.timeScale();
    this.pause();

    if (this.pauseForTimeoutId)
      clearTimeout(this.pauseForTimeoutId);
    this.pauseForTimeoutId = setTimeout(this.play, Math.round((seconds * 1000) / scale));
  }

  restart() {
    wobbler.reset();
    director.begin();
    order.reset();
    setPlayBtn(this);
  }

  addOnStart(fn: () => any) {
    this.onStartCallbacks.push(fn);
  }

  addOnComplete(fn: () => any) {
    this.onCompleteCallbacks.push(fn);
  }
}

const controller = new Controller();
// Set all the actions for the controller

const restartbtn = document.getElementById('restartbtn') as HTMLElement;
const speedInput = document.getElementById('speedRange') as HTMLElement;
const speedOutput = document.getElementById('speedOutput') as HTMLElement;

speedOutput.textContent = `${config.timeScale}`;

playbtn.onclick = () => {
  controller.playPause();
};

restartbtn.onclick = () => {
  controller.restart();
  setPlayBtn(controller);
};

export const setSpeed = (speed: number) => {
  speedOutput.textContent = `${speed}`;
  director.tl.timeScale(speed);
};

speedInput.oninput = (e: any) => {
  const speed = e.target.value;
  setSpeed(speed);
};

controller.addOnComplete(() => setPlayBtn(controller));
controller.addOnStart(() => setPlayBtn(controller));

export default controller;



