import { Directive } from '../typings';
import { n7 } from '../stage/networks';
import { getPathEdges, getPathDrawActions } from '../utils/network';

const getInverseUnder = (length: number, exclude: number[]) => {
  const excludedIndexes: number[] = [];
  for (let y = 0; y < length; y++) {
    if (!exclude.includes(y))
      excludedIndexes.push(y);
  }
  return excludedIndexes;
};

const nodeChangeDur = 0.3;

const nodes0 = [16, 15, 13, 12, 11, 45, 9, 1, 8, 36, 43, 35, 31];
const nodes1 = [45, 46, 47, 48, 51];
const nodes2 = [1, 2, 5];
const nodes3 = [1, 10, 27];
const allNodes = [16, 15, 13, 12, 11, 45, 9, 1, 8, 36, 43, 35, 31, 46, 47, 48, 51, 2, 5, 10, 27];
const excludedNodes = getInverseUnder(n7.nodes.length, allNodes);
const allEdges = [26, 24, 22, 20, 21, 16, 5, 4, 13, 42, 41, 39, 49, 50, 51, 55, 2, 9, 6, 17];
const excludedEdges = getInverseUnder(n7.edges.length, allEdges);
const mainPath = getPathDrawActions(nodes0, [16, 13, 9, 36, 35], n7, 'network7', nodeChangeDur);
const branchPath1 = getPathDrawActions(nodes1, [], n7, 'network7', nodeChangeDur);
const branchPath2 = getPathDrawActions(nodes2, [2], n7, 'network7', nodeChangeDur);
const branchPath3 = getPathDrawActions(nodes3, [10], n7, 'network7', nodeChangeDur);

// const nodeDataEnter = 1;
const nodeExit = 2;

const lateralThreatHunt: Directive[] = [
  {
    wait: 0,
    actions: [
      { type: 'motion', payload: { actor: 'CardAgent1', motion: 'typeCode', duration: 13 } },
    ]
  },
  {
    wait: -10,
    stagger: 0.17,
    actions: [
      { type: 'motion', payload: { actor: 'network7', motion: 'toSmall', duration: nodeChangeDur, targets: [16], size: 23 } },
      {
        type: 'motion', payload: {
          actor: 'network7', motion: 'toInvisibleEdge', duration: nodeChangeDur,
          targets: [
            ...getPathEdges([16, 77], n7),
            ...getPathEdges([16, 78], n7),
            ...getPathEdges([16, 79], n7),
            ...getPathEdges([16, 80], n7),
            ...getPathEdges([16, 81], n7),
          ]
        }
      },
      {
        type: 'motion', payload: {
          actor: 'network7', motion: 'toInvisible', duration: nodeChangeDur,
          targets: [77, 78, 79, 80, 81]
        }
      },
      { type: 'motion', payload: { actor: 'network7', motion: 'toSmall', duration: nodeChangeDur, targets: [11], size: 23 } },
      {
        type: 'motion', payload: {
          actor: 'network7', motion: 'toInvisibleEdge', duration: nodeChangeDur,
          targets: [
            ...getPathEdges([11, 62], n7),
            ...getPathEdges([11, 63], n7),
            ...getPathEdges([11, 64], n7),
            ...getPathEdges([11, 65], n7),
            ...getPathEdges([11, 66], n7),
          ]
        }
      },
      {
        type: 'motion', payload: {
          actor: 'network7', motion: 'toInvisible', duration: nodeChangeDur,
          targets: [62, 63, 64, 65, 66]
        }
      },
      { type: 'motion', payload: { actor: 'network7', motion: 'toSmall', duration: nodeChangeDur, targets: [51], size: 23 } },
      {
        type: 'motion', payload: {
          actor: 'network7', motion: 'toInvisibleEdge', duration: nodeChangeDur,
          targets: [
            ...getPathEdges([51, 72], n7),
            ...getPathEdges([51, 73], n7),
            ...getPathEdges([51, 74], n7),
            ...getPathEdges([51, 75], n7),
            ...getPathEdges([51, 76], n7),
          ]
        }
      },
      {
        type: 'motion', payload: {
          actor: 'network7', motion: 'toInvisible', duration: nodeChangeDur,
          targets: [72, 73, 74, 75, 76]
        }
      },
      { type: 'motion', payload: { actor: 'network7', motion: 'toSmall', duration: nodeChangeDur, targets: [5], size: 23 } },
      {
        type: 'motion', payload: {
          actor: 'network7', motion: 'toInvisibleEdge', duration: nodeChangeDur,
          targets: [
            ...getPathEdges([5, 67], n7),
            ...getPathEdges([5, 68], n7),
            ...getPathEdges([5, 69], n7),
            ...getPathEdges([5, 70], n7),
            ...getPathEdges([5, 71], n7),
          ]
        }
      },
      {
        type: 'motion', payload: {
          actor: 'network7', motion: 'toInvisible', duration: nodeChangeDur,
          targets: [67, 68, 69, 70, 71]
        }
      },
      { type: 'motion', payload: { actor: 'network7', motion: 'toSmall', duration: nodeChangeDur, targets: [27], size: 23 } },
      {
        type: 'motion', payload: {
          actor: 'network7', motion: 'toInvisibleEdge', duration: nodeChangeDur,
          targets: [
            ...getPathEdges([27, 57], n7),
            ...getPathEdges([27, 58], n7),
            ...getPathEdges([27, 59], n7),
            ...getPathEdges([27, 60], n7),
            ...getPathEdges([27, 61], n7),
            ...getPathEdges([58, 87, 89], n7),
          ]
        }
      },
      {
        type: 'motion', payload: {
          actor: 'network7', motion: 'toInvisible', duration: nodeChangeDur,
          targets: [57, 58, 59, 60, 61, 87, 89]
        }
      },
      { type: 'motion', payload: { actor: 'network7', motion: 'toSmall', duration: nodeChangeDur, targets: [31], size: 23 } },
      {
        type: 'motion', payload: {
          actor: 'network7', motion: 'toInvisibleEdge', duration: nodeChangeDur,
          targets: [
            ...getPathEdges([31, 82], n7),
            ...getPathEdges([31, 83], n7),
            ...getPathEdges([31, 84], n7),
            ...getPathEdges([31, 85], n7),
            ...getPathEdges([31, 86], n7),
            ...getPathEdges([84, 88, 90], n7),
          ]
        }
      },
      {
        type: 'motion', payload: {
          actor: 'network7', motion: 'toInvisible', duration: nodeChangeDur,
          targets: [82, 83, 84, 85, 86, 88, 90]
        }
      },
    ]
  },
  {
    wait: -2,
    stagger: 0.6,
    actions: mainPath
  },
  {
    wait: -8,
    stagger: 0.6,
    actions: branchPath1
  },
  {
    wait: -3,
    stagger: 0.6,
    actions: branchPath2
  },
  {
    wait: -1,
    stagger: 0.6,
    actions: branchPath3
  },
  {
    wait: 0,
    stagger: 1.5,
    actions: [
      {
        type: 'motion', payload: {
          actor: 'network7', motion: 'toInvisibleEdge', duration: nodeExit,
          targets: excludedEdges
        }
      },
      {
        type: 'motion', payload: {
          actor: 'network7', motion: 'toInvisible', duration: nodeExit,
          targets: excludedNodes
        }
      },
    ]
  },
  {
    wait: 1,
    stagger: 0.3,
    actions: [
      { type: 'exit', payload: { actor: 'EVERYTHING', duration: nodeExit } },
    ]
  }
];

export default lateralThreatHunt;


