import * as React from 'react';
import director from '../director';
const autoBind = require('auto-bind');
import styled from 'styled-components';
import layers from '../constants/layers';
import { Power2, TimelineLite } from 'gsap';
import { simpleEnter, simpleExit } from '../motions';

const Outer = styled.div`
  width: 0;
  height: 0;
  position: relative;
  z-index: ${layers.foreground + 10};
`;

export interface Props {
  actorName: string;
  entryDuration?: number;
}

class UseCaseZoomContainer extends React.Component<Props> {
  container: Element;
  children: Element[];

  constructor(props: Props) {
    super(props);
    autoBind(this);
    director.registerMotions(this, props.actorName,
      {
        enter: simpleEnter(this, -50, 0),
        exit: simpleExit(this, 50, 0),
        transition: this.transition,
      });
  }

  transition(dur: number, tl: TimelineLite, payload: any) {
    console.log('GOT MOTION');
    const { vars } = payload;
    const now = tl.time();
    tl.to(this.container, dur, {
      ease: Power2.easeInOut,
      ...vars,
    }, now);
  }

  render() {
    return (
      <Outer innerRef={r => { if (r) this.container = r; }}>{this.props.children}</Outer>
    );
  }
}

export default UseCaseZoomContainer;



