import paramReference from '../constants/paramReference';

const addBtn = (id: string, inner: string) => {
  const btn = document.createElement('button');
  btn.setAttribute('class', 'controlBtn');
  btn.setAttribute('id', id);
  btn.innerHTML = inner;
  return btn;
};

export const addControllerElements = () => {
  const crosshairs = document.getElementById('crosshairs') as HTMLElement;
  crosshairs.classList.remove('crosshairsInstance');
  crosshairs.classList.add('crosshairsController');
  const wallSetup = document.getElementById('wallSetup') as HTMLElement;
  wallSetup.remove();
  wallSetup.classList.remove('wallSetupInstance');
  wallSetup.classList.add('wallSetupController');

  const play = addBtn('controlPlay', 'PLAY');
  const restart = addBtn('controlRestart', 'RESTART');
  const setup = addBtn('controlSetup', 'SETUP');
  const slower = addBtn('controlSlower', 'SLOWER');
  const faster = addBtn('controlFaster', 'FASTER');
  const displaySpeed = document.createElement('div');
  displaySpeed.setAttribute('id', 'controlDisplaySpeed');

  const outer = document.createElement('div');
  outer.setAttribute('class', 'controlsOuter');
  outer.appendChild(play);
  outer.appendChild(slower);
  outer.appendChild(faster);
  outer.appendChild(restart);
  outer.appendChild(setup);
  outer.appendChild(displaySpeed);
  outer.appendChild(wallSetup);
  paramReference.forEach(line => {
    let reference = document.createElement('div');
    reference.setAttribute('class', 'paramReference');
    reference.textContent = line;
    outer.appendChild(reference);
  });

  document.body.appendChild(outer);

  const oldInterface = document.getElementById('btns') as HTMLElement;
  oldInterface.remove();

  return {
    play, restart, setup, slower, faster, displaySpeed
  };
};



