import { Scene } from '../typings';
import ransomwareThreatHunt from './ransomwareThreatHunting';

const exploitCard = {
  staggerEnter: 0.4,
  staggerExit: 0.3,
  staggerFocus: 0.2,
  focus: 0.7,
  enter: 1.2,
  exit: 0.4,
};

const backgroundTitle = {
  enter: 6,
  exit: 1,
};

const infectedHostCard = {
  staggerEnter: 0.4,
  staggerBlock: 0.7,
  staggerFocus: 0.3,
  focus: 2.5,
  enter: 2.5,
  exit: 1.2,
  block: 2,
};

export const logos = {
  enter: 2,
  exit: 2,
};

const title = {
  enter: 2,
  exit: 2,
};
const swipeBetweenScene = 1.4;

export const directoryTreeEnter = 4;
const sidePanelMotions = 2.5;

const ransomwareFlow: Scene = {

  wait: 3,
  startActors: ['WorldMap', 'Countries', 'SidePanel', 'ThreatActors', 'WorldClocks', 'ClientLogo'],
  directives: [
    {
      wait: 0,
      action: { type: 'begin', payload: { actor: 'stage', duration: 0, } }
    },
    {
      wait: 0,
      stagger: 0.75,
      actions: [
        { type: 'enter', payload: { actor: 'RansomwareTitle', duration: title.enter, } },
      ]
    },
    {
      wait: 0,
      actions: [
        { type: 'enter', payload: { actor: 'FarBgNetwork', duration: 2 } },
        { type: 'enter', payload: { actor: 'network1', duration: 2 } },
        { type: 'enter', payload: { actor: 'network2', duration: 2 } },
        { type: 'enter', payload: { actor: 'network3', duration: 2 } },
        { type: 'enter', payload: { actor: 'network4', duration: 2 } },
        { type: 'enter', payload: { actor: 'network5', duration: 2 } },
        { type: 'enter', payload: { actor: 'network6', duration: 2 } },
      ]
    },
    {
      wait: 0,
      actions: [
        { type: 'enter', payload: { actor: 'StoryPhases', duration: 5 } },
      ]
    },
    {
      wait: 0,
      action: { type: 'exit', payload: { actor: 'RansomwareTitle', duration: title.exit, } }
    },
    {
      wait: 0,
      stagger: 3,
      actions: [
        { type: 'enter', payload: { actor: 'RansomwareTextBackground1', duration: backgroundTitle.enter, } },
      ]
    },
    {
      wait: -0.24 * backgroundTitle.enter,
      action: { type: 'motion', payload: { actor: 'SidePanel', motion: 'toPartialReveal', duration: sidePanelMotions } }
    },
    {
      wait: 0,
      action: { type: 'enter', payload: { actor: 'HostItemList', duration: 1.5, } }
    },
    {
      wait: 3,
      stagger: exploitCard.staggerEnter,
      actions: [
        { type: 'enter', payload: { actor: 'CardExploit0', duration: exploitCard.enter } },
        { type: 'enter', payload: { actor: 'CardExploit1', duration: exploitCard.enter } },
        { type: 'enter', payload: { actor: 'CardExploit2', duration: exploitCard.enter } },
        { type: 'enter', payload: { actor: 'CardExploit3', duration: exploitCard.enter } },
        { type: 'enter', payload: { actor: 'CardExploit4', duration: exploitCard.enter } },
        { type: 'enter', payload: { actor: 'CardExploit5', duration: exploitCard.enter } },
        { type: 'enter', payload: { actor: 'CardExploit6', duration: exploitCard.enter } },
        { type: 'enter', payload: { actor: 'CardExploit7', duration: exploitCard.enter } },
        { type: 'enter', payload: { actor: 'CardExploit8', duration: exploitCard.enter } },
        { type: 'enter', payload: { actor: 'CardExploit9', duration: exploitCard.enter } },
        { type: 'enter', payload: { actor: 'CardExploit10', duration: exploitCard.enter } },
      ]
    },
    {
      wait: 2,
      stagger: exploitCard.staggerFocus,
      actions: [
        { type: 'motion', payload: { actor: 'CardExploit5', motion: 'focus', duration: exploitCard.focus } },
        { type: 'motion', payload: { actor: 'CardExploit1', motion: 'focus', duration: exploitCard.focus } },
        { type: 'motion', payload: { actor: 'CardExploit2', motion: 'focus', duration: exploitCard.focus } },
        { type: 'motion', payload: { actor: 'CardExploit0', motion: 'focus', duration: exploitCard.focus } },
        { type: 'motion', payload: { actor: 'CardExploit3', motion: 'focus', duration: exploitCard.focus } },
        { type: 'motion', payload: { actor: 'CardExploit4', motion: 'focus', duration: exploitCard.focus } },
      ]
    },
    {
      wait: 2,
      action: { type: 'motion', payload: { actor: 'CardExploit5', motion: 'focusRed', duration: 2, } }
    },
    {
      wait: 1,
      stagger: exploitCard.staggerExit,
      actions: [
        { type: 'exit', payload: { actor: 'CardExploit2', duration: exploitCard.exit } },
        { type: 'exit', payload: { actor: 'CardExploit0', duration: exploitCard.exit } },
        { type: 'exit', payload: { actor: 'CardExploit6', duration: exploitCard.exit } },
        { type: 'exit', payload: { actor: 'CardExploit4', duration: exploitCard.exit } },
        { type: 'exit', payload: { actor: 'CardExploit8', duration: exploitCard.exit } },
        { type: 'exit', payload: { actor: 'CardExploit1', duration: exploitCard.exit } },
        { type: 'exit', payload: { actor: 'CardExploit3', duration: exploitCard.exit } },
        { type: 'exit', payload: { actor: 'CardExploit10', duration: exploitCard.exit } },
        { type: 'exit', payload: { actor: 'CardExploit7', duration: exploitCard.exit } },
        { type: 'exit', payload: { actor: 'CardExploit9', duration: exploitCard.exit } },
      ]
    },
    {
      wait: -.5,
      action: { type: 'exit', payload: { actor: 'HostItemList', duration: 1.5, } }
    },
    {
      wait: -1,
      actions: [
        { type: 'motion', payload: { actor: 'CardExploit5', motion: 'moveToFocus', duration: 2 } },
        { type: 'motion', payload: { actor: 'SidePanel', motion: 'toPartialCover', duration: sidePanelMotions } },
        { type: 'exit', payload: { actor: 'RansomwareTextBackground1', duration: backgroundTitle.exit, } },
      ]
    },
    {
      wait: -0.5,
      actions: [
        { type: 'motion', payload: { actor: 'CardExploit5', motion: 'expand', duration: 1, } }
      ]
    },
    {
      wait: 0,
      actions: [
        { type: 'enter', payload: { actor: 'RansomwareTextBackground2', duration: backgroundTitle.enter, } },
      ]
    },
    {
      wait: 0,
      action: { type: 'motion', payload: { actor: 'CardExploit5', motion: 'showDetails', duration: 2 } }
    },
    {
      wait: 0,
      stagger: infectedHostCard.staggerEnter,
      actions: [
        { type: 'enter', payload: { actor: 'CardInfectedHost0', duration: infectedHostCard.enter } },
        { type: 'enter', payload: { actor: 'CardInfectedHost1', duration: infectedHostCard.enter } },
        { type: 'enter', payload: { actor: 'CardInfectedHost2', duration: infectedHostCard.enter } },
        { type: 'enter', payload: { actor: 'CardInfectedHost3', duration: infectedHostCard.enter } },
        { type: 'enter', payload: { actor: 'CardInfectedHost4', duration: infectedHostCard.enter } },
        { type: 'enter', payload: { actor: 'CardInfectedHost5', duration: infectedHostCard.enter } },
        { type: 'enter', payload: { actor: 'CardInfectedHost6', duration: infectedHostCard.enter } },
        { type: 'enter', payload: { actor: 'CardInfectedHost7', duration: infectedHostCard.enter } },
        { type: 'enter', payload: { actor: 'CardInfectedHost8', duration: infectedHostCard.enter } },
        { type: 'enter', payload: { actor: 'CardInfectedHost9', duration: infectedHostCard.enter } },
        { type: 'enter', payload: { actor: 'CardInfectedHost10', duration: infectedHostCard.enter } },
      ]
    },
    {
      wait: 0,
      stagger: infectedHostCard.staggerFocus,
      actions: [
        { type: 'motion', payload: { actor: 'CardInfectedHost3', motion: 'focus', duration: infectedHostCard.focus } },
        { type: 'motion', payload: { actor: 'CardInfectedHost5', motion: 'focus', duration: infectedHostCard.focus } },
        { type: 'motion', payload: { actor: 'CardInfectedHost0', motion: 'focus', duration: infectedHostCard.focus } },
        { type: 'motion', payload: { actor: 'CardInfectedHost1', motion: 'focus', duration: infectedHostCard.focus } },
        { type: 'motion', payload: { actor: 'CardInfectedHost4', motion: 'focus', duration: infectedHostCard.focus } },
        { type: 'motion', payload: { actor: 'CardInfectedHost2', motion: 'focus', duration: infectedHostCard.focus } },
      ]
    },
    {
      wait: 0,
      action: { type: 'exit', payload: { actor: 'RansomwareTextBackground2', duration: backgroundTitle.exit, } }
    },
    {
      wait: 0,
      stagger: 1,
      actions: [
        { type: 'enter', payload: { actor: 'RansomwareTextBackground3', duration: backgroundTitle.enter, } },
        { type: 'motion', payload: { actor: 'StoryPhases', motion: 'nextPhase', duration: 2, } },
      ]
    },
    {
      wait: 1,
      stagger: infectedHostCard.staggerBlock,
      actions: [
        { type: 'motion', payload: { actor: 'CardInfectedHost3', motion: 'showBlocked', duration: infectedHostCard.block, } },
        { type: 'motion', payload: { actor: 'CardInfectedHost2', motion: 'showBlocked', duration: infectedHostCard.block, } },
        { type: 'motion', payload: { actor: 'CardInfectedHost1', motion: 'showBlocked', duration: infectedHostCard.block, } },
        { type: 'motion', payload: { actor: 'CardInfectedHost5', motion: 'showBlocked', duration: infectedHostCard.block, } },
        { type: 'motion', payload: { actor: 'CardInfectedHost4', motion: 'showBlocked', duration: infectedHostCard.block, } },
        { type: 'motion', payload: { actor: 'CardInfectedHost0', motion: 'showBlocked', duration: infectedHostCard.block, } },
      ]
    },
    {
      wait: 7,
      action: { type: 'exit', payload: { actor: 'RansomwareTextBackground3', duration: backgroundTitle.exit, } }
    },
    {
      wait: -0.5 * backgroundTitle.exit,
      stagger: 0.2,
      actions: [
        { type: 'motion', payload: { actor: 'CardExploit5', motion: 'swipeUp', duration: swipeBetweenScene } },
        { type: 'exit', payload: { actor: 'CardInfectedHost0', duration: infectedHostCard.exit } },
        { type: 'exit', payload: { actor: 'CardInfectedHost1', duration: infectedHostCard.exit } },
        { type: 'exit', payload: { actor: 'CardInfectedHost2', duration: infectedHostCard.exit } },
        { type: 'exit', payload: { actor: 'CardInfectedHost3', duration: infectedHostCard.exit } },
        { type: 'exit', payload: { actor: 'CardInfectedHost4', duration: infectedHostCard.exit } },
        { type: 'exit', payload: { actor: 'CardInfectedHost5', duration: infectedHostCard.exit } },
        { type: 'exit', payload: { actor: 'CardInfectedHost6', duration: infectedHostCard.exit } },
        { type: 'exit', payload: { actor: 'CardInfectedHost7', duration: infectedHostCard.exit } },
        { type: 'exit', payload: { actor: 'CardInfectedHost8', duration: infectedHostCard.exit } },
        { type: 'exit', payload: { actor: 'CardInfectedHost9', duration: infectedHostCard.exit } },
        { type: 'exit', payload: { actor: 'CardInfectedHost10', duration: infectedHostCard.exit } },
        { type: 'motion', payload: { actor: 'SidePanel', motion: 'toFullReveal', duration: sidePanelMotions } },
      ]
    },
    {
      wait: 0,
      action: { type: 'exit', payload: { actor: 'CardExploit5', duration: 0 } }
    },
    {
      wait: 0,
      stagger: 1,
      actions: [
        { type: 'enter', payload: { actor: 'RansomwareTextBackground4', duration: backgroundTitle.enter, } },
        { type: 'motion', payload: { actor: 'StoryPhases', motion: 'nextPhase', duration: 2, } },
      ]
    },
    {
      wait: 0,
      action: { type: 'enter', payload: { actor: 'CrowdStrikeLogo', duration: logos.enter, } }
    },
    {
      wait: 0,
      stagger: directoryTreeEnter / 10,
      actions: [
        { type: 'enter', payload: { actor: 'DirectoryTree', duration: directoryTreeEnter, } },
        { type: 'enter', payload: { actor: 'DirectoryIcons', duration: directoryTreeEnter, } },
        { type: 'enter', payload: { actor: 'DirectoryInfo', duration: directoryTreeEnter, } },
      ]
    },
    {
      wait: -1,
      stagger: 1,
      actions: [
        { type: 'enter', payload: { actor: 'DirectoryDetails1', duration: 2 } },
        { type: 'enter', payload: { actor: 'DirectoryDetails2', duration: 2 } },
      ]
    },
    {
      wait: 0,
      stagger: 0.75,
      actions: [
        { type: 'enter', payload: { actor: 'DirectoryInfectedData', duration: 1.5 } },
        { type: 'motion', payload: { actor: 'DirectoryInfectedData', motion: 'lineEnterDraw', duration: 1.2 } },
        { type: 'motion', payload: { actor: 'DirectoryIcons', motion: 'infect', duration: 1.5 } },
        { type: 'motion', payload: { actor: 'DirectoryInfo', motion: 'infect', duration: 1.5 } },
      ]
    },
    {
      wait: 3,
      actions: [
        { type: 'exit', payload: { actor: 'DirectoryTree', duration: 1, } },
        { type: 'exit', payload: { actor: 'DirectoryIcons', duration: 1, } },
        { type: 'exit', payload: { actor: 'DirectoryInfo', duration: 1, } },
        { type: 'exit', payload: { actor: 'DirectoryInfectedData', duration: 1, } },
        { type: 'exit', payload: { actor: 'DirectoryDetails1', duration: 1, } },
        { type: 'exit', payload: { actor: 'DirectoryDetails2', duration: 1, } },
        { type: 'exit', payload: { actor: 'CrowdStrikeLogo', duration: logos.exit, } },
      ]
    },
    {
      wait: 0,
      actions: [
        { type: 'exit', payload: { actor: 'RansomwareTextBackground4', duration: backgroundTitle.exit, } },
      ]
    },
    {
      wait: 0,
      actions: [
        { type: 'enter', payload: { actor: 'RansomwareTextBackground5', duration: backgroundTitle.enter, } },
      ]
    },
    {
      wait: 0,
      actions: [
        { type: 'motion', payload: { actor: 'network6', motion: 'moveFront', duration: 5 } },
        { type: 'motion', payload: { actor: 'network6', motion: 'toOpaque', duration: 5 } },
      ]
    },
    {
      wait: 0,
      action: { type: 'enter', payload: { actor: 'CardAgent0', duration: 1 } }
    },
    {
      wait: 1,
      action: { type: 'motion', payload: { actor: 'CardAgent0', motion: 'typeCode', duration: 5 } }
    },
    ...ransomwareThreatHunt,
    {
      wait: -2,
      stagger: 0.2,
      actions: [
        { type: 'exit', payload: { actor: 'RansomwareTextBackground5', duration: backgroundTitle.exit, } },
        { type: 'exit', payload: { actor: 'CardAgent0', duration: backgroundTitle.exit, } },
        { type: 'exit', payload: { actor: 'network1', duration: backgroundTitle.exit, } },
        { type: 'exit', payload: { actor: 'network2', duration: backgroundTitle.exit, } },
        { type: 'exit', payload: { actor: 'network6', duration: 3, } },
        { type: 'exit', payload: { actor: 'network3', duration: backgroundTitle.exit, } },
        { type: 'exit', payload: { actor: 'network4', duration: backgroundTitle.exit, } },
        { type: 'exit', payload: { actor: 'network5', duration: backgroundTitle.exit, } },
        { type: 'exit', payload: { actor: 'FarBgNetwork', duration: backgroundTitle.exit } },
        { type: 'exit', payload: { actor: 'StoryPhases', duration: 3, } },
        { type: 'exit', payload: { actor: 'ClientLogo', duration: 3, } },
      ]
    },
    {
      wait: -0.5,
      action: { type: 'enter', payload: { actor: 'OperationsTextBackground', duration: backgroundTitle.enter } }
    },
    {
      wait: 0,
      stagger: 0.3,
      actions: [
        { type: 'enter', payload: { actor: 'OperationsTable', duration: 6, } },
        { type: 'enter', payload: { actor: 'OperationsCards', duration: 6, } },
      ]
    },
    {
      wait: 0,
      stagger: 0.3,
      actions: [
        { type: 'enter', payload: { actor: 'OperationsTable', duration: 6, } },
        { type: 'enter', payload: { actor: 'OperationsCards', duration: 6, } },
      ]
    },
    {
      wait: 6,
      stagger: 0.7,
      actions: [
        { type: 'exit', payload: { actor: 'OperationsTable', duration: 3, } },
        { type: 'exit', payload: { actor: 'OperationsCards', duration: 3, } },
      ]
    },
    {
      wait: -2,
      stagger: 0.6,
      actions: [
        { type: 'exit', payload: { actor: 'OperationsTextBackground', duration: 3 }, },
        { type: 'exit', payload: { actor: 'WorldClocks', duration: 1.5 }, },
        { type: 'exit', payload: { actor: 'ThreatActors', duration: 1.5 }, },
        { type: 'exit', payload: { actor: 'Countries', duration: 1.5 }, },
        { type: 'exit', payload: { actor: 'WorldMap', duration: 1.5, } },
      ]
    },
    {
      wait: 0,
      stagger: 2,
      actions: [
        { type: 'end', payload: { actor: 'stage', duration: 0, } },
        { type: 'exit', payload: { actor: 'SidePanel', duration: 1.5, } },
      ]
    },
    {
      wait: 5,
      action: { type: 'enter', payload: { actor: 'nothingHere', duration: 0, } }
    },
  ]
};

export default ransomwareFlow;


