
export default [
'-- URL Param Reference --',
'Note: these are all optional and have defaults.',
'controlGroup - (number) used to associate instances of the wall with each other for syncing and control. Instances will only sync with others with the same control group.', 
'isController - (true/false) sets up one instance as a \'master\' controller for its group, which alters the UI a bit. You are currently viewing a controller.', 
'timeScale - (number) default is 1 - scales the speed of the presentation - higher is faster.', 
'offsetMs - (number) milliseconds to "scooch" one instance ahead (negative number) or behind (positive number).',
'scrollLeft - (number) left-scroll pixel offset to start the window at, making it easier to put the wall on multiple screens and match the edges up.', 
'resize - (horizontal/vertical/none) scale the wall to fit viewing window\'s width or height, or "none" for manual scaling.',
'rotationsPerDegree - (int) how many times to redraw a rotating graph per degree of rotation, default of 6. Lower for performance issues.',
'rotationTimeScale - (int) scalar to change how long it takes to rotate the graphs, default of 2.5. As opposed to timeScale, higher is slower here (oops!).',
'wobbleMax - (number) maximum distance to wobble \'floaty\' elements around, such as nodes and labels, default of 14.',
'wobbleDur - (number) how long each wobble takes, default of 5, meaning 5 seconds.',
'numWobbles - (int) how many wobbles there are in a full wobble sequence before it loops - fewer appears less random, default of 5.',
'wobbleGroups - (int) how many different wobble sequences are happening at a time - fewer appears less random, default of 23.',
'fps - (int) adjusts the default of 60fps. Use to address performance issues.',
];


