import localForage from 'localforage';
import config, { getVertical, getPresentation, getDomain } from '../constants/config';
import { setPresentation } from '../index';

// for setting up the client logo image, placement, and vertical
const toggle = document.querySelector('#uploadbtn') as HTMLInputElement;
const container = document.querySelector('#wallSetup') as HTMLInputElement;
const fileInput = document.querySelector('#fileInput') as HTMLInputElement;
const imgPreview = document.querySelector('#imgPreview') as HTMLImageElement;
const selectVertical = document.querySelector('#selectVertical') as HTMLSelectElement;
const domainInput = document.querySelector('#inputClientDomain') as HTMLInputElement;
const selectPresentation = document.querySelector('#selectPresentation') as HTMLSelectElement;
const btnSave = document.querySelector('#setupSaveBtn') as HTMLButtonElement;
const widthSelect = document.querySelector('#imgWidthSelect') as HTMLInputElement;
const leftSelect = document.querySelector('#imgLeftSelect') as HTMLInputElement;
const topSelect = document.querySelector('#imgTopSelect') as HTMLInputElement;

// set the values in SETUP when we recieve updates from a controller or grouped peer instance.
export const setCurrentSetupSelections = () => {
  selectVertical.value = getVertical();
  selectPresentation.value = getPresentation();
  domainInput.value = getDomain();
};

const tempConfig = {
  width: 700,
  offsetLeft: 0,
  offsetTop: 0,
  src: '',
  vertical: getVertical(),
  domain: getDomain(),
  presentation: getPresentation(),
};

export const clientSetupConfig = {
  width: 700,
  offsetLeft: 0,
  offsetTop: 0,
  src: '',
  vertical: getVertical(),
  domain: getDomain(),
  presentation: getPresentation(),
};

// get a config change from another instance in the control group
export const syncFromGroup = (socketMsg: ClientInfo) => {
  console.log('SYNC FROM CONTROL GROUP');
  console.dir(socketMsg);
  imgPreview.src = socketMsg.clientLogo;
  clientSetupConfig.src = socketMsg.clientLogo;
  tempConfig.src = socketMsg.clientLogo;

  widthSelect.value = `${socketMsg.clientLogoWidth}`;
  imgPreview.style.width = `${socketMsg.clientLogoWidth}px`;
  clientSetupConfig.width = socketMsg.clientLogoWidth;
  tempConfig.width = socketMsg.clientLogoWidth;

  leftSelect.value = `${socketMsg.clientLogoLeft}`;
  imgPreview.style.left = `${socketMsg.clientLogoLeft}px`;
  clientSetupConfig.offsetLeft = socketMsg.clientLogoLeft;
  tempConfig.offsetLeft = socketMsg.clientLogoLeft;

  topSelect.value = `${socketMsg.clientLogoTop}`;
  imgPreview.style.top = `${socketMsg.clientLogoTop}px`;
  clientSetupConfig.offsetTop = socketMsg.clientLogoTop;
  tempConfig.offsetTop = socketMsg.clientLogoTop;

  tempConfig.vertical = socketMsg.clientVertical;
  clientSetupConfig.vertical = socketMsg.clientVertical;
  config.vertical = socketMsg.clientVertical;
  selectVertical.value = socketMsg.clientVertical;

  tempConfig.presentation = socketMsg.presentation;
  clientSetupConfig.presentation = socketMsg.presentation;
  selectPresentation.value = socketMsg.presentation;

  tempConfig.domain = socketMsg.clientDomain;
  clientSetupConfig.domain = socketMsg.clientDomain;
  domainInput.value = socketMsg.clientDomain;
  hideAndSave(false);
};

// send config changes to the rest of the control group
export const syncToGroup = (imgsrc: string, left: number, top: number, width: number, vertical: string, presentation: string, domain: string) => {
  const info: ClientInfo = {
    clientVertical: vertical,
    clientLogo: imgsrc,
    clientLogoTop: top,
    clientLogoLeft: left,
    clientLogoWidth: width,
    clientDomain: domain,
    presentation: presentation,
  };
  if (!config.socket) return console.log('no group, didnt sync');

  config.socket.send({
    type: 'clientConfigChange',
    info: info,
  });
};

function hideAndSave(sync?: boolean) {
  toggle.textContent = 'SETUP';
  container.style.visibility = 'hidden';
  if (tempConfig.width !== undefined)
    clientSetupConfig.width = tempConfig.width;
  if (tempConfig.offsetTop !== undefined)
    clientSetupConfig.offsetTop = tempConfig.offsetTop;
  if (tempConfig.offsetLeft !== undefined)
    clientSetupConfig.offsetLeft = tempConfig.offsetLeft;
  clientSetupConfig.src = tempConfig.src;
  if (tempConfig.vertical !== undefined) {
    clientSetupConfig.vertical = tempConfig.vertical;
    config.vertical = tempConfig.vertical;
  }
  if (tempConfig.domain !== undefined) {
    clientSetupConfig.domain = tempConfig.domain;
    config.domain = tempConfig.domain;
  }
  if (tempConfig.presentation !== undefined) {
    clientSetupConfig.presentation = tempConfig.presentation;
  }
  saveToLocal();
  if (sync)
    syncToGroup(
      tempConfig.src,
      tempConfig.offsetLeft,
      tempConfig.offsetTop,
      tempConfig.width,
      tempConfig.vertical,
      tempConfig.presentation,
      tempConfig.domain,
    );
}

toggle.addEventListener('click', function () {
  const text = toggle.textContent;
  if (text === 'EXIT SETUP') {
    toggle.textContent = 'SETUP';
    container.style.visibility = 'hidden';
    container.style.maxHeight = '0';
  } else {
    toggle.textContent = 'EXIT SETUP';
    container.style.visibility = 'visible';
    container.style.maxHeight = '1000px';
  }
});

btnSave.addEventListener('click', () => {
  hideAndSave(true);
  const controlBtn = document.getElementById('controlSetup');
  if (!controlBtn) return;
  controlBtn.innerHTML = 'SETUP';
  container.style.maxHeight = '0';
});

fileInput.addEventListener('change', function () {

  if (fileInput.files && fileInput.files.length === 0)
    return;
  if (!fileInput.files) return;
  // gets first selected file
  var file = fileInput.files[0];

  // Using FileReader to display the image content
  var reader = new FileReader();
  reader.onload = function (e: any) {
    imgPreview.src = e.target.result;
    tempConfig.src = e.target.result;
  };
  reader.readAsDataURL(file);
}, false);

domainInput.addEventListener('input', function (e: any) {
  const val = e.target.value;
  tempConfig.domain = val;
}, false);

widthSelect.addEventListener('input', (e: any) => {
  const val = e.target.value;
  imgPreview.style.width = `${val}px`;
  tempConfig.width = parseInt(val, 10);
});

leftSelect.addEventListener('input', (e: any) => {
  const val = e.target.value;
  imgPreview.style.left = `${val}px`;
  tempConfig.offsetLeft = parseInt(val, 10);
});

topSelect.addEventListener('input', (e: any) => {
  const val = e.target.value;
  imgPreview.style.top = `${val}px`;
  tempConfig.offsetTop = parseInt(val, 10);
});
selectVertical.addEventListener('change', (e: any) => {
  const val = e.target.value;
  tempConfig.vertical = val;
});
selectPresentation.addEventListener('change', (e: any) => {
  const val = e.target.value;
  tempConfig.presentation = val;
});

function saveToLocal() {
  const src = clientSetupConfig.src;
  if (src) {
    localForage.setItem('clientLogo', src)
      .catch(err => console.error(err.message));
  }
  if (clientSetupConfig.width !== undefined)
    localForage.setItem('clientLogoWidth', clientSetupConfig.width)
      .catch(err => console.error(err.message));
  if (clientSetupConfig.offsetLeft !== undefined)
    localForage.setItem('clientLogoLeft', clientSetupConfig.offsetLeft)
      .catch(err => console.error(err.message));
  if (clientSetupConfig.offsetTop !== undefined)
    localForage.setItem('clientLogoTop', clientSetupConfig.offsetTop)
      .catch(err => console.error(err.message));
  if (clientSetupConfig.vertical !== undefined)
    localForage.setItem('clientVertical', clientSetupConfig.vertical)
      .catch(err => console.error(err.message));
  if (clientSetupConfig.domain !== undefined)
    localForage.setItem('clientDomain', clientSetupConfig.domain)
      .catch(err => console.error(err.message));
  if (clientSetupConfig.presentation !== undefined) {
    setPresentation(clientSetupConfig.presentation);
    localForage.setItem('presentation', clientSetupConfig.presentation)
      .catch(err => console.error(err.message));
  }
}

function retrieveLocal() {
  localForage.getItem('clientLogo')
    .then((val: string) => {
      if (val === null || val === undefined) return;
      imgPreview.src = val;
      clientSetupConfig.src = val;
      tempConfig.src = val;
    })
    .catch(err => console.error(err.message));
  localForage.getItem('clientLogoWidth')
    .then(() => {
      const val = 510;
      widthSelect.value = `${val}`;
      imgPreview.style.width = `${val}px`;
      clientSetupConfig.width = val;
      tempConfig.width = val;
    })
    .catch(err => console.error(err.message));
  localForage.getItem('clientLogoLeft')
    .then(() => {
      const val = 110;
      leftSelect.value = `${val}`;
      imgPreview.style.left = `${val}px`;
      clientSetupConfig.offsetLeft = val;
      tempConfig.offsetLeft = val;
    })
    .catch(err => console.error(err.message));
  localForage.getItem('clientLogoTop')
    .then(() => {
      const val = -90;
      topSelect.value = `${val}`;
      imgPreview.style.top = `${val}px`;
      clientSetupConfig.offsetTop = val;
      tempConfig.offsetTop = val;
    })
    .catch(err => console.error(err.message));
  localForage.getItem('clientVertical')
    .then(() => {
      const val = getVertical();
      tempConfig.vertical = val;
      clientSetupConfig.vertical = val;
      config.vertical = val;
      selectVertical.value = val;
    })
    .catch(err => console.error(err.message));
  localForage.getItem('clientDomain')
    .then(() => {
      const val = getDomain();
      tempConfig.domain = val;
      clientSetupConfig.domain = val;
      config.domain = val;  
      domainInput.value = val;
    })
    .catch(err => console.error(err.message));
  localForage.getItem('presentation')
    .then(() => {
      const val = getPresentation();
      tempConfig.presentation = val;
      clientSetupConfig.presentation = val;
      selectPresentation.value = val;
      setPresentation(val);
    })
    .catch(err => console.error(err.message));
}

interface ClientInfo {
  clientVertical: string;
  clientLogo: string;
  clientLogoTop: number;
  clientLogoLeft: number;
  clientLogoWidth: number;
  clientDomain: string;
  presentation: string;
}

retrieveLocal();


