import * as React from 'react';
import styled from 'styled-components';
import director from '../director';
import autoBind from 'auto-bind';
import HostItem from './HostItem';
import layers from '../constants/layers';
import spacing from '../constants/spacing';
import { staggerEnterFrom, staggerExitTo } from '../motions';
import strings from '../constants/strings';
import { TimelineLite, Power2 } from 'gsap';

const Outer = styled.div`
  width: 400px;
  position: absolute;
  left: ${(p: { left: number, top: number }) => p.left}px;
  top: ${p => p.top}px;
  display: flex;
  flex-flow: column nowrap;
  z-index: ${layers.foreground};
`;

const Title = styled.div`
  position: absolute;
  left: -20px;
  top: -41px;
  font-size: 44px;
  font-weight: 600;
  white-space: nowrap;
  z-index: ${layers.foreground + 30};
`;

interface Props {
  actorName: string;
  entryDuration: number;
  title?: string;
  overlay?: boolean;
  left?: number;
  top?: number;
}

class HostItemList extends React.Component<Props>  {
  container: Element;
  children: Element[];
  constructor(props: Props) {
    super(props);
    this.children = [];
    autoBind(this);
    director.registerMotions(this, props.actorName,
      {
        enter: staggerEnterFrom(this, 0, 100),
        transition: this.transition,
        exit: staggerExitTo(this, 0, -100),
      }
    );
  }

  addChildRef(ref: Element) {
    if (ref)
      this.children.push(ref);
  }

  transition(dur: number, tl: TimelineLite, payload: any) {
    const { vars } = payload;
    const now = tl.time();
    tl.to(this.container, dur, {
      ...vars,
      ease: Power2.easeInOut,
    }, now);
  }
  
  render() {
    const { actorName, entryDuration, top, left, overlay, title } = this.props;
    const hosts = strings.ransomware.hostItems;
    const topPos = top !== undefined ? top : 80;
    const leftPos = left !== undefined ? left : spacing.leftPanelEndAlt2 + 100;
    return (
      <Outer
        top={topPos}
        left={leftPos}
      >
        {title && <Title innerRef={this.addChildRef}>{title}</Title>}
        {hosts.map((h, i) => (<HostItem
          delay={((i / hosts.length) * entryDuration) / 2}
          key={i}
          name={h.name}
          vulnerabilities={h.vulnerabilities}
          browser={h.browser}
          role={h.role}
          actorName={actorName}
          entryDuration={entryDuration * 0.5}
          setRef={this.addChildRef}
          overlay={overlay}
        />
        ))
        }
      </Outer>
    );
  }
}

export default HostItemList;



