import * as React from 'react';
import styled from 'styled-components';
import director from '../director';
import IconLaptop from 'react-icons/lib/md/laptop-chromebook';
import IconMdBlock from 'react-icons/lib/md/block';
import { TimelineLite, Power1, Power2 } from 'gsap';
import autoBind from 'auto-bind';
import layers from '../constants/layers';
import { simpleEnter, simpleExit, moveTo } from '../motions';
import theme from '../constants/theme';

const _width = 575;
const _height = 125;

const Root = styled.div`
  position: absolute;
  left: ${(p: { top: number, left: number }) => p.left}px;
  top: ${(p) => p.top}px;
  width: ${_width}px;
  height: ${_height}px;
  z-index: ${layers.foreground};
  filter: drop-shadow(5px 12px 17px ${theme.cardDropShadow});
  transform: scale(0.7);
  opacity: 0.5;
`;

const FrontSection = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  z-index: ${layers.foreground + 2};
`;

const _iconWidth = 100;

const IconOuter = styled.div`
  position: absolute;
  top: 29px;
  left: -50px;
  width: ${_iconWidth}px;
  height: ${_iconWidth}px;
  border-radius: 50%;
  background: ${(p: { color: string }) => p.color};
  z-index: ${layers.foreground + 1};
  display: flex;
  justify-content: center;
  align-items: center;
`;

const IconBlock = styled(IconMdBlock) `
  transform: rotate(45deg);
`;

const IconCircle = styled.div`
  width: ${_iconWidth - 16}px;
  height: ${_iconWidth - 16}px;
  border-radius: 50%;
  border: 4px solid white;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const CardRect = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: ${_width}px;
  height: ${_height}px;
  padding: 20px;
  padding-left: 50px;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  background: ${theme.cardBackground};
  border: 5.2px solid ${theme.cardBackground};
`;

const _leftText = 70;
const IpAddress = styled.div`
  position:absolute;
  top: 16px;
  left: ${_leftText}px;
  font-size: 43px; font-weight: 500;
`;

const Detail = styled.div`
  position:absolute;
  top: 73px;
  height: 70px;
  width: 544px;
  display: flex;
  flex-flow: row-wrap;
  justify-content: flex-start;
  align-items: center;
  left: ${_leftText}px;
  color: ${theme.cardInfectedHostDetail1}; font-size: 31px; font-weight: 500;
`;

const BlockedTitle = styled.div`
  position:absolute;
  top: 29px;
  left: ${_leftText}px;
  font-size: 36px; font-weight: 600;
`;

const BlockedDetail = styled.div`
  position:absolute;
  top: 88px;
  left: ${_leftText}px;
  font-size: 34px; font-weight: 500; color: ${theme.cardInfectedHostDetailBlocked};
`;

interface Props {
  actorName: string;
  entryDuration?: number;
  position: {
    initial: {
      top: number;
      left: number;
    };
    focused?: {
      top: number;
      left: number;
    };
  };
  address: string;
  detail: string;
  blockedTitle: string;
}

class CardInfectedHost extends React.Component<Props>  {
  container: Element;
  children: Element[];
  icon: Element;
  card: Element;
  blocker: Element;

  constructor(props: Props) {
    super(props);
    autoBind(this);
    director.registerMotions(this, props.actorName,
      {
        enter: simpleEnter(this, 50, 0),
        focus: this.focus,
        showBlocked: this.showBlocked,
        exit: simpleExit(this, 0, -50),
      }
    );
  }

  focus(dur: number, tl: TimelineLite) {
    const { focused, initial } = this.props.position;
    const left = focused ? focused.left : initial.left;
    const top = focused ? focused.top : initial.top;
    const now = tl.time();
    moveTo(this, top, left)(dur, tl);
    tl.to(this.container, dur * 0.6, {
      scale: 1,
      opacity: 1,
      zIndex: layers.foreground + 5,
      ease: Power2.easeInOut,
    }, now)
      .to(this.card, dur * 0.3, {
        borderColor: theme.cardInfectedHostDetailBlocked,
        ease: Power2.easeInOut,
      }, now + dur * 0.7);
  }

  showBlocked(dur: number, tl: TimelineLite) {
    const now = tl.time();
    tl.to(this.blocker, dur * 0.2, {
      opacity: 1,
    }, now + 0.3)
      .to(this.container, dur * .2, {
        scale: 1.03,
        ease: Power1.easeInOut
      }, now + dur * 0.2)
      .to(this.container, dur * .3, {
        scale: 1,
        ease: Power1.easeInOut
      }, now + dur * 0.5);
  }

  render() {
    const { address, detail, blockedTitle, position } = this.props;
    return (
      <Root
        innerRef={ref => { if (ref) this.container = ref; }}
        top={position.initial.top}
        left={position.initial.left}
      >
        <IconOuter
          color={theme.cardInfectedHostIconOuter}
          innerRef={ref => { if (ref) this.icon = ref; }}
        >
          <IconCircle>
            <IconLaptop size="50px" color={theme.cardInfectedHostIcon} />
          </IconCircle>
        </IconOuter>
        <CardRect
          innerRef={ref => { if (ref) this.card = ref; }}
        >
          <IpAddress>{address}</IpAddress>
          <Detail><div>{detail}</div></Detail>
        </CardRect>
        <FrontSection innerRef={ref => { if (ref) this.blocker = ref; }}>
          <IconOuter
            color={theme.cardInfectedHostDetailBlocked}
          >
            <IconCircle>
              <IconBlock size="50px" color={theme.cardInfectedHostIcon} />
            </IconCircle>
          </IconOuter>
          <CardRect>
            <BlockedTitle>{blockedTitle}</BlockedTitle>
            <BlockedDetail>{'LKJEWOIGHEWFW'}</BlockedDetail>
          </CardRect>
        </FrontSection>
      </Root>
    );
  }
}

export default CardInfectedHost;



