import { Scene } from '../typings';
import { getVertical } from '../constants/config';

const worldMap = {
  enter: 2,
  toSmall: 3,
  exit: 1.5
};

const verticals = {
  enter: 2,
  exit: 2,
  scroll: 3.2,
};

const threatActors = {
  enter: 2,
  exit: 2,
  removeNonFocused: 2,
  toFocus: 3,
  toSmall: 4,
  showLines: 2,
};

const countries = {
  enter: 3,
};

const worldClocks = {
  enter: 3,
  exit: 3,
};

const threatActorTree = {
  enter: threatActors.enter,
  exit: 1.5
};

const storyIntro: Scene = {

  wait: 0,
  startActors: [],
  persistActors: ['SidePanel', 'WorldMap', 'ThreatActors', 'WorldClocks', 'Countries', 'ClientLogo'],
  directives: [
    {
      wait: 0.5,
      actions: [
        { type: 'enter', payload: { actor: 'WorldMap', duration: worldMap.enter } },
        { type: 'enter', payload: { actor: 'CrowdStrikeLogoIntro', duration: worldMap.enter }, }
      ]
    },
    {
      wait: 0.5,
      action: { type: 'enter', payload: { actor: 'ThreatActors', duration: threatActors.enter }, }
    },
    {
      wait: 0,
      action: { type: 'enter', payload: { actor: 'Verticals', duration: verticals.enter }, }
    },
    {
      wait: 1,
      stagger: verticals.scroll * 1.1,
      actions: [
        { type: 'motion', payload: { actor: 'Verticals', motion: 'scrollTo', duration: verticals.scroll, getTarget: getVertical } },
      ]
    },
    {
      wait: 2,
      action: { type: 'motion', payload: { actor: 'ThreatActors', motion: 'removeNonFocused', duration: threatActors.removeNonFocused } }
    },
    {
      wait: -0.5 * threatActors.removeNonFocused,
      actions: [
        { type: 'enter', payload: { actor: 'ThreatActorTree', duration: threatActorTree.enter }, },
        { type: 'motion', payload: { actor: 'ThreatActors', motion: 'toFocusPositions', duration: threatActors.toFocus } }
      ]
    },
    {
      wait: 2,
      stagger: 0.5,
      actions: [
        { type: 'exit', payload: { actor: 'Verticals', duration: verticals.exit }, },
        { type: 'exit', payload: { actor: 'CrowdStrikeLogoIntro', duration: verticals.exit }, },
      ]
    },
    {
      wait: -1,
      action: { type: 'exit', payload: { actor: 'ThreatActorTree', duration: threatActorTree.exit }, },
    },
    {
      wait: threatActorTree.exit * -0.5,
      actions: [
        {
          type: 'motion', payload: {
            actor: 'WorldMap', motion: 'transition', duration: worldMap.toSmall, vars: {
              x: -1860 - 100,
              y: -30,
              scale: .61,
              opacity: 0.35,
            }
          }
        },
        {
          type: 'motion', payload: {
            actor: 'ThreatActors', motion: 'transition', duration: worldMap.toSmall, vars: {
              x: -1830 - 90,
              y: 0,
              scale: .61,
            }
          }
        },
      ]
    },
    {
      wait: -1,
      stagger: 0.7,
      actions: [
        { type: 'motion', payload: { actor: 'ThreatActors', motion: 'toSmallPositions', duration: worldMap.toSmall }, },
        { type: 'enter', payload: { actor: 'Countries', duration: countries.enter }, },
        { type: 'motion', payload: { actor: 'ThreatActors', motion: 'showLines', duration: threatActors.showLines }, },
        { type: 'enter', payload: { actor: 'WorldClocks', duration: worldClocks.enter }, },
      ]
    },
  ]
};

export default storyIntro;


