import { verticalToThreatActor, threatActorToCountry, selectionNames, selectionNameToVerticalName } from '../constants/threatActors';
import random from './prng';

const _numIntroThreatActors = 10;
export const _maxThreatActors = 6;

// get a reduced set of threat actors from a vertical 
export const getThreatActorsForVertical = (selectionName: string) => {
  const vertname = selectionNameToVerticalName.get(selectionName) as string;
  let taNames = verticalToThreatActor.get(vertname) as string[];
  if (!taNames) {
    console.log(vertname);
    console.error('invalid Vertical Name!');
    return [];
  }
  if (taNames.length > _maxThreatActors) {
    taNames = taNames.slice(0, _maxThreatActors);
  }
  return taNames;
};

// a preset 'random' order of the threat actors
const _newOrder = new Array(_numIntroThreatActors).fill(0).map((_, x) => x);
// shuffle the order
for (let i = 0; i < _numIntroThreatActors - 2; i++) {
  const j = random.between(i, _numIntroThreatActors);
  [_newOrder[j], _newOrder[i]] = [_newOrder[i], _newOrder[j]];
}

// get a set of threat actors to show on the large world map, 
// before it's narrowed down to the threat actors for a specific Vertical.
export const getIntroThreatActors = (selectionName: string) => {
  const vertThreatActors = getThreatActorsForVertical(selectionName) as string[];
  const actors = [...vertThreatActors];
  const taNames = Array.from(threatActorToCountry.keys());
  while (actors.length < _numIntroThreatActors) {
    const ta = taNames[random.between(0, taNames.length)];
    if (!actors.includes(ta))
      actors.push(ta);
  }
  const shuffled = new Array(_numIntroThreatActors).fill(0);
  // shuffle the list of actor names
  for (let i = 0; i < _numIntroThreatActors; i++) {
    shuffled[_newOrder[i]] = actors[i];
  }
  return shuffled;
};

export const newOrder = _newOrder;

export const addVerticalOptionsToSelect = () => {
  const el = document.getElementById('selectVertical');
  if (!el) return;
  selectionNames.forEach((name) => {
    const op = document.createElement('option');
    op.textContent = name;
    op.value = name;
    el.appendChild(op);
  });
};


