import * as React from 'react';
import styled from 'styled-components';
import director from '../director';
import { TimelineLite, Power2 } from 'gsap';
import autoBind from 'auto-bind';
import layers from '../constants/layers';

const Div = styled.div`
  position: absolute;
  left: ${(p: { left: number, color: string, overlay?: boolean, fontSize?: number, zIndexAddition: number }) => p.left}px; 
  white-space: nowrap;
  bottom: 430px;
  font-size: ${p => p.fontSize !== undefined ? p.fontSize : 180}px;
  font-weight: 300;
  color: ${p => p.color};
  z-index: ${p => layers.foreground + 55 + p.zIndexAddition};
  ${p => p.overlay ? `
  background-color: rgba(247, 248, 252, 0.9);
  box-shadow: 0px 0px 49px 110px rgba(247, 248, 252, 0.9);
  border-radius: 140px;` : ''}
`;

interface Props {
  entryDuration: number;
  actorName: string;
  text: string;
  left: number;
  fontSize?: number;
  xShrunk: number;
  startSmall?: boolean;
  overlay?: boolean;
  color: string;
  toColor?: string;
  zIndexAddition?: number;
}

class TextBackground extends React.Component<Props> {
  container: Element;
  children: Element[];

  constructor(props: Props) {
    super(props);
    autoBind(this);

    director.registerMotions(this, props.actorName,
      {
        enter: props.startSmall ? this.enterSmall : this.enterBanner,
        exit: this.exit,
      });
  }

  enterBanner(dur: number, tl: TimelineLite) {
    const { overlay, toColor, xShrunk } = this.props;
    const changes: any = overlay
      ? {
        backgroundColor: 'rbga(247, 248, 252, 0)',
        boxShadow: '0px 0px 49px 110px rgba(247, 248, 252, 0)'
      } : {};
    if (toColor) {
      changes.color = toColor;
    }
    const now = tl.time();
    tl.from(this.container, dur * .3, {
      x: -50,
      opacity: 0,
      ease: Power2.easeOut
    }, now);
    tl.to(this.container, dur * .25, {
      x: xShrunk,
      y: 470,
      scale: 0.6,
      opacity: 0.7,
      ...changes,
      ease: Power2.easeInOut
    }, now + dur * .75);
  }

  enterSmall(dur: number, tl: TimelineLite) {
    const now = tl.time();
    tl.set(this.container, {
      x: this.props.xShrunk - 50,
      y: 470,
      scale: 0.6,
      opacity: 0,
    }, now);
    tl.to(this.container, dur * .3, {
      x: this.props.xShrunk,
      opacity: 1,
      ease: Power2.easeOut
    }, now);
    tl.to(this.container, dur * .25, {
      opacity: 0.7,
      ease: Power2.easeInOut
    }, now + dur * .75);
  }

  exit(dur: number, tl: TimelineLite) {
    tl.to(this.container, dur, {
      x: '-=50',
      opacity: 0,
      ease: Power2.easeIn,
    }, tl.time());
  }
  render() {
    const { left, color, overlay, fontSize, zIndexAddition } = this.props;
    return (
      <Div
        left={left}
        color={color}
        overlay={overlay}
        fontSize={fontSize}
        zIndexAddition={zIndexAddition || 0}
        innerRef={r => { if (r) this.container = r; }}
      >
        {this.props.text}
      </Div>
    );
  }
}

export default TextBackground;



