import CardHomograph from '../../actors/CardHomograph';
import s from '../../constants/strings';
import HomographList from '../../actors/HomographList';
import TextBackground from '../../actors/TextBackground';
import StoryTitle from '../../actors/StoryTitle';
import CardHostPhishing from '../../actors/CardHostPhishing';
import DrawTree from '../../actors/DrawTree';
import TextDuo from '../../actors/TextDuo';
import CardAgent from '../../actors/CardAgent';
import theme from '../../constants/theme';
import PhishingFileList from '../../actors/PhishingFileLIst';

const _shiftLeft = -175;
const _shiftTop = -30;
const _homographPositions = [
  { top: 120 + _shiftTop, left: 3100 + _shiftLeft },
  { top: 400 + _shiftTop, left: 2900 + _shiftLeft },
  { top: 710 + _shiftTop, left: 3200 + _shiftLeft },
  { top: 100 + _shiftTop, left: 4120 + _shiftLeft },
  { top: 440 + _shiftTop, left: 3900 + _shiftLeft },
  { top: 730 + _shiftTop, left: 4100 + _shiftLeft },
];

const _hostPositions = [
  { top: 440, left: 3720 - 150 },
  { top: 120, left: 3300 - 150 },
  { top: 670, left: 2880 - 150 }, 
];
const _hostSecondPositions = [
  { top: 370, left: 1750 },
  { top: 150, left: 1750 },
  { top: 590, left: 1750 },
];
const data = {
  'PhishingFileList': {
    component: PhishingFileList,
    props: {
      top: 200,
      left: 4330,
    }
  },
  'CardAgent2': {
    component: CardAgent,
    props: {
      ...s.phishing.agentCard,
      codeOffsets: [
        2, 2, 2, 2, 2, 2, 2, 2, 2
      ],
      top: 110,
      left: 1710,
      height: 415,
      codeStyle: {
        top: 180,
        fontWeight: 400,
        fontSize: 29,
        color: theme.warningRed,
      }
    }
  },
  'DirectoryInfectedDataPhishing': {
    component: TextDuo,
    props: {
      top: 335,
      left: 1900,
      width: 550,
      ...s.phishing.processThreatData,
      style1: { fontSize: 33, color: theme.textDuoPrimary, fontWeight: 700, marginBottom: 20 },
      style2: { fontSize: 32, color: theme.textDuoSecondary, fontWeight: 600 },
      typeTextEnter: false,
      line: {
        color: theme.textDuoPrimary,
        x1: 830, x2: 1220, y1: 320, y2: 320,
      },
    }
  },
  'HomographList': {
    component: HomographList,
    props: {
      left: 1790,
      top: 75,
    }
  },
  'PhishingTitle': {
    component: StoryTitle,
    props: {
      text: s.phishing.titles[0],
      icon: 'lock',
      style: {
        left: 2300
      }
    }
  },
  'PhishingTextBackground0': {
    component: TextBackground,
    props: {
      text: s.phishing.titles[1],
      left: 2450,
      xShrunk: -980,
      color: theme.textBackground,
      overlay: true,
    }
  },
  'PhishingTextBackground0B': {
    component: TextBackground,
    props: {
      text: s.phishing.titles[4],
      left: 2559,
      xShrunk: -1056,
      color: theme.textBackground,
      overlay: true,
    }
  },
  'PhishingTextBackground1': {
    component: TextBackground,
    props: {
      text: s.phishing.titles[2],
      left: 2200,
      xShrunk: -870,
      color: theme.textBackground,
      overlay: true,
    }
  },
  'PhishingTextBackground2': {
    component: TextBackground,
    props: {
      text: s.phishing.titles[3],
      left: 2220,
      fontSize: 154,
      xShrunk: -710,
      color: theme.textBackground,
      overlay: true,
    }
  },
  'HostLines': {
    component: DrawTree,
    props: {
      color: theme.lightGreenBlue,
      width: 4,
      svgWidth: 3000,
      left: 1600 - 125,
      top: 0,
      exitType: 'unDraw',
      lines: [
        ..._hostPositions.map((hp, i) => ({
          x1: hp.left - 1630 + 150,
          x2: 800,
          y1: hp.top + 80,
          y2: 244 + i * 269,
          start: 700 * i
        }))
      ]
    }
  }
};

_homographPositions.forEach((pos, i) => {
  data[`Homograph${i}`] = {
    component: CardHomograph,
    props: {
      top: pos.top,
      left: pos.left,
      homographIdx: i,
      detail1: s.phishing.blockedDomainDetails[i],
      detail2: s.phishing.blockedDomainText,
    }
  };
});

_hostPositions.forEach((pos, i) => {
  data[`HostPhishing${i}`] = {
    component: CardHostPhishing,
    props: {
      top: pos.top,
      left: pos.left,
      secondTop: _hostSecondPositions[i].top,
      secondLeft: _hostSecondPositions[i].left,
      title: s.phishing.hosts[i].name,
      detail1: 'IP Address',
      detail2: s.phishing.hosts[i].ip,
    }
  };
});

export default data;


