import * as React from 'react';
import director from '../director';
import { TimelineLite, Linear } from 'gsap';
import autoBind from 'auto-bind';
import layers from '../constants/layers';
import { simpleExit } from '../motions';
import Svg from './Svg';

interface Props {
  actorName: string;
  entryDuration?: number;
  color: string;
  strokeLinecap?: string;
  width?: number;
  svgWidth?: number;
  left: number;
  top?: number;
  exitType?: string;
  // line animations are calculated based on line length
  // (each line will be drawn at the same rate, so longer lines take longer)
  // and start point is an arbitrary number that could be thought of as
  // lines previously drawn up to that line
  lines?: ({
    x1: number;
    x2: number;
    y1: number;
    y2: number;
    start: number;
  })[];
  getLines?: () => ({
    x1: number;
    x2: number;
    y1: number;
    y2: number;
    start: number;
  })[];
}

class DrawTree extends React.Component<Props>  {
  container: Element;
  children: SVGLineElement[];
  lines: ({
    x1: number;
    x2: number;
    y1: number;
    y2: number;
    start: number;
  })[];
  lineLengths: number[];
  totalLength: number;
  card: Element;

  constructor(props: Props) {
    super(props);
    autoBind(this);
    this.children = [];

    director.registerMotions(this, props.actorName,
      {
        enter: this.enter,
        exit: props.exitType === 'unDraw'
          ? this.unDraw
          : props.exitType === 'reverse'
            ? this.reverse
            : simpleExit(this, 50, 0),
      }
    );
    const { lines, getLines } = props;
    this.lines = lines
      ? lines
      : getLines
        ? getLines()
        : [];
    this.lineLengths = this.lines.map(({ x1, x2, y1, y2 }, i) =>
      Math.sqrt(
        Math.pow(x1 - x2, 2) + Math.pow(y1 - y2, 2)
      ));

    this.totalLength = 0;
    // get the max 'length'
    this.lines.forEach((line, i) => {
      // total 'length': line pixel length plus arbitary start point.
      const lineAnimationLength = this.lineLengths[i] + line.start;
      if (lineAnimationLength > this.totalLength)
        this.totalLength = lineAnimationLength;
    });
  }

  enter(dur: number, tl: TimelineLite) {
    const { lineLengths, children } = this;

    const now = tl.time();
    children.forEach((line, i) => {
      const { start } = this.lines[i];
      const startPoint = now + dur * (start / this.totalLength);
      const durationPercent = lineLengths[i] / this.totalLength;
      tl.set(line, {
        strokeDasharray: lineLengths[i],
        strokeDashoffset: lineLengths[i],
      }, now)
        .set(line, {
          opacity: 1,
        }, startPoint)
        .to(line, dur * durationPercent, {
          strokeDashoffset: 0,
          ease: Linear.easeNone
        }, startPoint);

    });
  }
  // erase line tree in the same order (root first)
  unDraw(dur: number, tl: TimelineLite) {
    const { lineLengths, children } = this;

    const now = tl.time();
    children.forEach((line, i) => {
      const { start } = this.lines[i];
      const startPoint = now + dur * (start / this.totalLength);
      const endPoint = now + dur * ((this.totalLength + start) / this.totalLength);
      const durationPercent = lineLengths[i] / this.totalLength;
      tl.to(line, dur * durationPercent, {
        strokeDashoffset: lineLengths[i] * -1,
        ease: Linear.easeNone
      }, startPoint)
        .set(line, {
          opacity: 0,
        }, endPoint);
    });
  }

  // erase the tree from the leaves in, ending with the root
  reverse(dur: number, tl: TimelineLite) {
    const { lineLengths, children } = this;

    const now = tl.time();
    children.forEach((line, i) => {
      const { start } = this.lines[i];
      const startPoint = now + dur * ((this.totalLength - start - lineLengths[i]) / this.totalLength);
      const endPoint = now + dur * ((this.totalLength - start) / this.totalLength);
      const durationPercent = lineLengths[i] / this.totalLength;
      tl.to(line, dur * durationPercent, {
        strokeDashoffset: lineLengths[i],
        ease: Linear.easeNone
      }, startPoint)
        .set(line, {
          opacity: 0,
        }, endPoint);

    });
  }
  addChildRef(ref: SVGLineElement) {
    if (ref)
      this.children.push(ref);
  }
  render() {
    const { color, width, left, strokeLinecap, svgWidth, top } = this.props;

    return (
      <Svg
        left={left}
        top={top !== undefined ? top : 0}
        zIndex={layers.foreground}
        width={`${svgWidth !== undefined ? svgWidth : 3000}px`}
        height="1000px"
        innerRef={ref => { if (ref) this.container = ref; }}
      >
        {this.lines.map((line, i) => <line
          key={i}
          x1={line.x1}
          y1={line.y1}
          x2={line.x2}
          y2={line.y2}
          stroke={color}
          opacity={0}
          strokeLinecap={strokeLinecap !== undefined ? strokeLinecap as any : 'round'}
          strokeWidth={`${width !== undefined ? width : 4}px`}
          ref={this.addChildRef}
        />)}
      </Svg>
    );
  }
}

export default DrawTree;



