import { TimelineLite, Power1 } from 'gsap';
import random from '../utils/prng';
import autoBind from 'auto-bind';
import config from '../constants/config';
// a single wobbler (a position wobbling around with a timeline). used in Wobbler class below.
class SingleWobbler {
  private wobbleMax: number;
  private numWobbles: number;
  private wobbleDuration: number;
  private timeline: TimelineLite;
  public position: number;

  constructor(wobbleMax: number, numWobbles: number, wobbleDuration: number) {
    autoBind(this);
    // the fudge amount will make the wobble frequency vary, so they don't line up
    const fudge = random.between(-20, 21);
    this.wobbleMax = wobbleMax;
    this.numWobbles = numWobbles;
    this.wobbleDuration = wobbleDuration * ((80 + fudge) / 80);
    this.position = 0;

    this.createTimeline();

    this.timeline.pause();
    this.timeline.eventCallback('onComplete', () => {
      this.timeline.play(0);
    }, [], this);
  }

  createTimeline() {
    const tl = new TimelineLite();
    const { numWobbles, wobbleDuration, wobbleMax } = this;
    const max = 2 * wobbleMax + 1;

    for (let i = 0; i < numWobbles; i++) {
      tl.to(this, wobbleDuration, {
        position: random.between(0, max) - wobbleMax,
        ease: Power1.easeInOut
      }, '+=0');
    }
    tl.to(this, wobbleDuration, {
      position: 0,
      ease: Power1.easeInOut
    }, '+=0');
    this.timeline = tl;
  }

  reset() {
    this.timeline.pause();
    this.timeline.seek(0, true);
  }

  pause() {
    this.timeline.pause();
  }

  play() {
    this.timeline.play();
  }

  start(t: number) {
    this.timeline.play(t);
  }
}

// Class for multiple "wobbles"
class Wobbler {
  private wobbles: SingleWobbler[];
  private wobbleDuration: number;
  private timelinesStarted: boolean;
  private active: boolean;
  private lastRequest: number;
  constructor(wobbleMax: number, numWobbles: number, wobbleDuration: number, numGroups: number) {
    autoBind(this);
    this.wobbleDuration = wobbleDuration;
    this.wobbles = new Array(numGroups).fill(0).map(() =>
      new SingleWobbler(wobbleMax, numWobbles, wobbleDuration)
    );
    this.timelinesStarted = false;
    this.active = false;
  }

  private start() {
    const { wobbles, wobbleDuration } = this;
    if (!this.timelinesStarted) {
      const interval = wobbleDuration / wobbles.length;
      this.wobbles.forEach((wb, n) => {
        wb.start(interval * n);
      });
      this.timelinesStarted = true;
    } else {
      this.wobbles.forEach(wb => {
        wb.play();
      });
    }
    this.active = true;
    setTimeout(this.checkStopIfUnneeded, 500);
  }

  stop() {
    this.wobbles.forEach(wb => {
      wb.pause();
    });
    this.active = false;
  }

  reset() {
    this.wobbles.forEach(wb => {
      wb.reset();
    });
    this.timelinesStarted = false;
    this.active = false;
  }

  // stop the wobbles if no one appears to be using them
  checkStopIfUnneeded() {
    if (Date.now() - this.lastRequest > 20) {
      this.stop();
    } else {
      setTimeout(this.checkStopIfUnneeded, 500);
    }
  }

  getPosition(idx: number) {
    if (!this.active) {
      this.start();
    }
    this.lastRequest = Date.now();
    const { wobbles } = this;
    const n = wobbles.length;
    // this allows us to get n^2 apparent wobble 'groups' out of only n 'wobbling' numbers
    const w1 = wobbles[idx % n].position;
    const w2 = wobbles[(idx + Math.floor(idx / n)) % n].position;
    return {
      x: w1,
      y: w2,
    };
  }
}

export default new Wobbler(
  config.wobbleMax,
  config.numWobbles,
  config.wobbleDur,
  config.wobbleGroups,
);


