// definitions for wall space real estate
const _scaleX = 0.9;
const _origWidth = 5924.2;
const _fullWidth = _origWidth * 0.9;
const _fullHeight = 1000;

export default {
  leftPanelEnd: 0.268 * _origWidth,
  leftPanelEndAlt1: 0.38 * _origWidth,
  leftPanelEndAlt2: 0.69 * _origWidth,
  rightPanelStart: 0.78 * _origWidth,
  fullWidth: _fullWidth,
  fullHeight: _fullHeight,
  aspectRatio: _fullWidth / _fullHeight,
  scaleX: _scaleX,
};


