import * as React from 'react';
import styled from 'styled-components';
import director from '../director';
import autoBind from 'auto-bind';
import layers from '../constants/layers';
import spacing from '../constants/spacing';
import { staggerEnterFrom, staggerExitTo } from '../motions';
import { TimelineLite, Power1 } from 'gsap';
import theme from '../constants/theme';

const Outer = styled.div`
  width: 405px;
  height: 655px;
  position: absolute;
  left: ${(p: { left: number, top: number }) => p.left}px;
  top: ${p => p.top}px;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  z-index: ${layers.foreground};
`;

interface Props {
  actorName: string;
  entryDuration: number;
  left?: number;
  top?: number;
}

class PhishingFileList extends React.Component<Props> {
  container: Element;
  children: Element[];
  files: Element[];
  details: Element[];

  constructor(props: Props) {
    super(props);
    this.children = [];
    this.files = [];
    this.details = [];

    autoBind(this);
    director.registerMotions(this, props.actorName,
      {
        enter: staggerEnterFrom(this, 0, 100),
        iconsToRed: this.iconsToRed,
        exit: staggerExitTo(this, 0, -100),
      }
    );
  }

  addChildRef(ref: Element) {
    if (ref)
      this.children.push(ref);
  }

  addFileRef(ref: Element) {
    if (ref)
      this.files.push(ref);
  }

  iconsToRed(dur: number, tl: TimelineLite) {
    const amt = dur / 3;
    const now = tl.time();
    tl.staggerTo([this.files[0], this.files[3], this.files[5]], amt * 2, {
      color: theme.warningRed,
      ease: Power1.easeInOut,
    }, amt, now);
    tl.staggerTo([this.details[0], this.details[3], this.details[5]], amt * 2, {
      opacity: 1,
      ease: Power1.easeInOut,
    }, amt, now);
  }

  render() {
    const { top, left } = this.props;
    const topPos = top !== undefined ? top : 80;
    const leftPos = left !== undefined ? left : spacing.leftPanelEndAlt2 + 100;
    return (
      <Outer
        top={topPos}
        left={leftPos}
      />
    );
  }
}

export default PhishingFileList;



