import { Scene } from '../typings';
import { useCases, featuredUseCaseItems } from '../constants/usecases';

const clientLogo = {
  enter: 2,
  exit: 2,
  toSmall: 3,
};

const indexOfFeaturedUseCase = 2;

const useCaseFlow: Scene = {
  wait: 0,
  startActors: [],
  persistActors: ['SidePanel', 'WorldMap', 'ThreatActors', 'WorldClocks', 'Countries'],
  directives: [
    {
      wait: 0,
      action: { type: 'begin', payload: { actor: 'stage', duration: 0, } }
    },
    {
      wait: 0,
      actions: [
        { type: 'exit', payload: { actor: 'SidePanel', duration: 2, } },
        { type: 'exit', payload: { actor: 'WorldMap', duration: 2, } },
        { type: 'exit', payload: { actor: 'ThreatActors', duration: 2, } },
        { type: 'exit', payload: { actor: 'WorldClocks', duration: 2, } },
        { type: 'exit', payload: { actor: 'Countries', duration: 2, } },
      ]
    },
    {
      wait: 1,
      stagger: 1.3,
      actions: [
        { type: 'enter', payload: { actor: 'WelcomeTitle', duration: clientLogo.enter, } },
        { type: 'enter', payload: { actor: 'ClientLogo', duration: clientLogo.enter, } },
      ]
    },
    {
      wait: 7,
      stagger: 0.5,
      actions: [
        { type: 'exit', payload: { actor: 'WelcomeTitle', duration: clientLogo.exit, } },
        {
          type: 'motion', payload: {
            actor: 'ClientLogo', motion: 'transition', duration: clientLogo.toSmall, vars: {
              x: 2130,
              y: -380,
              scale: .75,
              opacity: 0.7,
            }
          }
        },
      ]
    },
    {
      wait: -1.5,
      actions: [
        { type: 'enter', payload: { actor: 'SidePanel', duration: 2, } },
        { type: 'enter', payload: { actor: 'network8', duration: clientLogo.enter, } },
      ]
    },
    {
      wait: 0,
      action: { type: 'enter', payload: { actor: 'UseCaseTitle0', duration: 2 } }
    },
    {
      wait: 2,
      stagger: 2,
      actions: [
        {
          type: 'motion', payload: {
            actor: 'UseCaseTitle0', motion: 'transition', vars: {
              y: '-=300',
              scale: 0.7,
              opacity: 0.8
            }, duration: 1.5
          }
        },
        { type: 'enter', payload: { actor: 'UseCaseList', duration: 10 } }
      ]
    },
    {
      wait: 2,
      stagger: 0.5,
      actions: [
        { type: 'exit', payload: { actor: 'UseCaseTitle0', duration: 3 } },
        { type: 'exit', payload: { actor: 'UseCaseList', duration: 3 } },
      ]
    },
    {
      wait: 0,
      actions: [
        { type: 'enter', payload: { actor: 'UseCasesCombine', duration: 10 } }
      ]
    },
    {
      wait: 4,
      actions: [
        { type: 'exit', payload: { actor: 'UseCasesCombine', duration: 3 } },
      ]
    },
    {
      wait: -1,
      stagger: 0.3,
      actions: [
        ...(useCases.map((_, i) => ({
          type: 'enter',
          payload: {
            actor: `UseCase${i}`,
            duration: 1.2,
          }
        })))
      ]
    },
    {
      wait: 5,
      stagger: 0.2,
      actions: [
        ...(useCases.map((_, i) => ({
          type: i === indexOfFeaturedUseCase ? 'motion' : 'exit',
          payload: {
            actor: `UseCase${i}`,
            motion: i === indexOfFeaturedUseCase ? 'toSecondPosition' : undefined,
            duration: 2,
          }
        })))
      ]
    },
    {
      wait: -1.3,
      actions: [
        { type: 'enter', payload: { actor: 'UseCasesTree', duration: 4, } },
      ]
    },
    {
      wait: -1.6,
      stagger: 0.14,
      actions: [
        ...(featuredUseCaseItems.map((_, i) => ({
          type: 'enter',
          payload: {
            actor: `LateralMvmtItem${featuredUseCaseItems.length - 1 - i}`,
            duration: 0.8,
          }
        })))
      ]
    },
    {
      wait: -3,
      action: { type: 'enter', payload: { actor: 'UseCaseTitle1', duration: 2 } }
    },
    {
      wait: 6,
      stagger: 1.5,
      actions: [
        { type: 'exit', payload: { actor: 'UseCaseTitle1', duration: 2 } },
        { type: 'enter', payload: { actor: 'UseCaseTitle2', duration: 2 } },
      ]
    },
    {
      wait: 0,
      stagger: 0.75,
      actions: [
        ...(featuredUseCaseItems.map((lmi, i) => ({
          type: 'motion',
          payload: {
            actor: `LateralMvmtItem${i}`,
            motion: lmi.disabled ? 'enable' : 'doNothing',
            duration: 0.8,
          }
        })))
      ]
    },
    {
      wait: 1,
      actions: [
        {
          type: 'motion',
          payload: {
            actor: 'UseCaseZoomContainer', motion: 'transition', duration: 10, vars: {
              x: 100,
              y: -320,
              scale: .5,
            }
          }
        },
      ]
    },
    {
      wait: 3,
      actions: [
        { type: 'exit', payload: { actor: 'UseCasesTree', duration: 3, } },
        { type: 'exit', payload: { actor: 'UseCaseTitle2', duration: 1.5, } },
      ]
    },
    {
      wait: -2.8,
      stagger: 0.1,
      actions: [
        ...(featuredUseCaseItems.map((_, i) => ({
          type: 'exit',
          payload: {
            actor: `LateralMvmtItem${i}`,
            duration: 0.5,
          }
        }))),
        { type: 'exit', payload: { actor: 'UseCase2', duration: 1, } },
        { type: 'exit', payload: { actor: 'network8', duration: 2 } },
      ]
    },
  ]
};

export default useCaseFlow;


