import * as React from 'react';
import styled from 'styled-components';
import director from '../director';
import autoBind from 'auto-bind';
import layers from '../constants/layers';
import { TimelineLite, Power1, Power2 } from 'gsap';

const Outer = styled.div`
  position: absolute;
  left: ${(p: { left: number, top: number }) => p.left}px;
  top: ${(p) => p.top}px;
  width: 350px;
  height: auto;
  z-index: ${layers.foreground + 5};
  padding: 37px 35px 46px 35px;
  box-shadow: 13px 12px 60px -8px rgba(50, 50, 50, 0.5);
  border-radius: 10px;
  border: 1px solid #777;
  background-color: #FFF;
  overflow: hidden;
  transform: scale(0.75);
`;

const Title = styled.div`
  font-size: 45px;
  font-weight: 700;
`;

const Item = styled.div`
  font-size: 33px;
  font-weight: 600;
  margin: 10px;
`;

interface Props {
  actorName: string;
  entryDuration?: number;
  title: string;
  items: string[];
  top: number;
  left: number;
  secondTop?: number;
  secondLeft?: number;
}

class UseCase extends React.Component<Props>  {
  container: Element;
  children: Element[];

  constructor(props: Props) {
    super(props);
    autoBind(this);

    director.registerMotions(this, props.actorName,
      {
        enter: this.enter,
        toSecondPosition: this.toSecondPosition,
        exit: this.exit,
      }
    );
  }

  enter(dur: number, tl: TimelineLite) {
    const now = tl.time();
    tl.from(this.container, dur, {
      height: 0,
      x: `-=20`,
      opacity: 0,
      ease: Power1.easeOut
    }, now);
  }

  exit(dur: number, tl: TimelineLite) {
    const now = tl.time();
    tl.to(this.container, dur, {
      scale: 0.5,
      opacity: 0,
      ease: Power1.easeIn
    }, now);
  }

  toSecondPosition(dur: number, tl: TimelineLite) {
    const { secondTop, secondLeft } = this.props;
    if (secondTop === undefined || secondLeft === undefined) return;
    const now = tl.time();
    tl.to(this.container, dur, {
      scale: 1,
      top: secondTop,
      left: secondLeft,
      ease: Power2.easeInOut
    }, now);
  }

  render() {
    const {
      left,
      top,
      title,
      items,
    } = this.props;
    return (
      <Outer
        innerRef={ref => { if (ref) this.container = ref; }}
        top={top}
        left={left}
      >
        <Title>{title}</Title>
        {items.map((it, i) => <Item key={i}>{it}</Item>)}
      </Outer >
    );
  }
}

export default UseCase;



