import spacing from '../constants/spacing';
import config from '../constants/config';

export const setResizing = () => {

  if (config.resize === 'none') return;
  const sizeToWindow = config.resize === 'horizontal'
    ? () => {
      var scale = window.innerWidth / spacing.fullWidth;
      const root = document.getElementById('root') as HTMLElement;
      root.style.transform = `scale(${scale})`;
      window.scrollTo(config.scrollLeft * scale, config.scrollTop * scale);

    }
    : () => { // resize by vertical height
      var scale = window.innerHeight / spacing.fullHeight;
      const root = document.getElementById('root') as HTMLElement;
      root.style.transform = `scale(${scale})`;
      window.scrollTo(config.scrollLeft * scale, config.scrollTop * scale);
    };

  // scale the whole dang thing to the height or vertically

  sizeToWindow();
};


