import React, { Component } from 'react';
import styled from 'styled-components';
import autoBind from 'auto-bind';
import director from '../director';
import { TimelineLite, Power2, Power1 } from 'gsap';
import { simpleExit, toOpacity } from '../motions';
import {
  NetworkEdge,
  NetworkNode,
  strokeWidth,
  edgeColor,
  nodeColor,
  Coord3D,
} from '../utils/network';
import { startTimeline, showCluster } from '../motions/networkMotions';
import spacing from '../constants/spacing';
import { ActionPayload } from '../typings';
import Svg from './Svg';
import theme from '../constants/theme';

const Outer = styled.div`
  position: absolute;
  height: 1000px;
  top: 0;
  left: ${(p: { left: number, opacity: number, z: number }) => p.left}px;
  opacity: ${p => p.opacity};
  z-index: ${p => p.z};
`;

const EdgeLine = styled.line`
  stroke-width: ${strokeWidth}px;
  stroke-linecap: butt;
`;

export interface Props {
  actorName: string;
  entryDuration?: number;
  baseZLayer: number;
  nodes: { x: number, y: number, z: number, size: number, color?: string, opacity?: number }[];
  edges: { src: number, dest: number, color?: string, opacity?: number }[];
  left: number;
  rotation?: {
    center: Coord3D;
    axis: 'x' | 'y' | 'z';
    duration: number;
  };
  startOpacity?: number;
}

export const getChildTargets = (nodesOrEdges: NetworkNode[] | NetworkEdge[], targets?: number[]) => {
  if (!targets) return { targets: [], refs: [] };
  return {
    targets: targets.map(idx => nodesOrEdges[idx % nodesOrEdges.length]),
    refs: targets.map(idx => nodesOrEdges[idx % nodesOrEdges.length].ref)
  };
};

class Network extends Component<Props> {
  container: Element;
  children: Element[];
  nodes: NetworkNode[];
  edges: NetworkEdge[];
  tl: TimelineLite;
  rotation: {
    degrees: number,
    matrix: number[],
    origin: Coord3D,
  };

  constructor(p: Props) {
    super(p);
    autoBind(this);
    director.registerMotions(this, p.actorName, {
      enter: startTimeline(this),
      moveFront: this.translate(0, 0, '+=1500'),
      moveBack: this.translate(0, 0, '-=1500'),
      // moveRight: this.translate('+=3000', 0, 0),
      // moveLeft: this.translate('-=3500', 0, 0),
      // moveUp: this.translate(0, '-=500', 0),
      // moveDown: this.translate(0, '+=500', 0),
      zoomCenter: this.zoomCenter,
      zoomNormal: this.zoomNormal,
      showCluster: showCluster(this, true),
      showNodeExtension: showCluster(this, false),
      toRedOutline: this.toRedOutline,
      toOpacity: this.toOpacity,
      toRed: this.toRed,
      toLarge: this.toLarge,
      toInfected: this.toInfected,
      toSmall: this.toSmall,
      toNormal: this.toNormal,
      toOpaque: toOpacity(this, 1),
      toInvisible: this.toInvisible,
      toInvisibleEdge: this.toInvisibleEdge,
      toRedEdge: this.toRedEdge,
      toTransparent: toOpacity(this, 0.4),
      toVeryTransparent: toOpacity(this, 0.2),
      exit: simpleExit(this, 0, 0)
    });
    // create nodes objects
    this.nodes = p.nodes.map(node => ({
      x: node.x, y: node.y, z: node.z,
      xRotated: node.x,
      yRotated: node.y,
      zRotated: node.z,
      xTrans: 0,
      yTrans: 0,
      zTrans: 0,
      curRotDegrees: 0,
      opacity: node.opacity === undefined ? 1 : node.opacity,
      size: node.size,
      screenX: node.x,
      screenY: node.y,
      screenZ: node.z,
      color: node.color || nodeColor,
    }));
    // get src/dest refereces for edges
    this.edges = p.edges.map(edge => {
      const source = this.nodes[edge.src];
      const destination = this.nodes[edge.dest];
      return {
        source, destination,
        z: source.screenZ,
        color: edge.color || edgeColor,
        opacity: edge.opacity === undefined ? 1 : edge.opacity,
        strokeWidth: strokeWidth
      };
    });
    this.tl = new TimelineLite();
    director.addActorTimeline(p.actorName, this.tl);
    this.rotation = {
      degrees: 0,
      matrix: [1, 0, 0, 0, 1, 0, 0, 0, 1],
      origin: p.rotation ? p.rotation.center : { x: spacing.fullWidth / 2, y: spacing.fullHeight / 2, z: 2000 }
    };
  }

  translate(x: string | number, y: string | number, z: string | number) {
    return (dur: number, masterTl: TimelineLite) => {
      const transforms: any = {};
      if (x) transforms.xTrans = x;
      if (y) transforms.yTrans = y;
      if (z) transforms.zTrans = z;
      masterTl.to(this.nodes, dur, {
        ...transforms,
        ease: Power2.easeInOut
      }, masterTl.time());
    };
  }

  zoomCenter(dur: number, tl: TimelineLite) {
    const now = tl.time();
    tl.to(this.container, dur, {
      scale: 2.6,
      x: '-=5000',
      y: '-=400',
      ease: Power2.easeInOut,
    }, now);
  }

  zoomNormal(dur: number, tl: TimelineLite) {
    const now = tl.time();
    tl.to(this.container, dur, {
      scale: 1,
      x: '+=5000',
      y: '+=400',
      ease: Power2.easeInOut,
    }, now);
  }

  toRedOutline(dur: number, tl: TimelineLite, payload: ActionPayload) {
    const { targets, refs } = getChildTargets(this.nodes, payload.targets);
    tl.to(targets, dur, {
      color: theme.networkWhite
      ,
    }, tl.time())
      .to(refs, dur, {
        strokeWidth: 10,
        stroke: theme.networkRed,
        ease: Power1.easeInOut,
      }, tl.time());
  }

  toRed(dur: number, tl: TimelineLite, payload: ActionPayload) {
    const { targets } = getChildTargets(this.nodes, payload.targets);
    tl.to(targets, dur, {
      color: theme.networkRed,
    }, tl.time());
  }

  toSmall(dur: number, tl: TimelineLite, payload: any) {
    const { targets } = getChildTargets(this.nodes, payload.targets);
    const size = payload.size || 20;
    tl.to(targets, dur, {
      size: size,
      ease: Power1.easeInOut,
    }, tl.time());
  }

  toLarge(dur: number, tl: TimelineLite, payload: ActionPayload) {
    const { targets } = getChildTargets(this.nodes, payload.targets);

    tl.to(targets, dur, {
      size: 34,
      ease: Power1.easeInOut,
    }, tl.time());
  }

  toInfected(dur: number, tl: TimelineLite, payload: ActionPayload) {
    this.toRedOutline(dur, tl, payload);
    this.toLarge(dur, tl, payload);
  }

  toNormal(dur: number, tl: TimelineLite, payload: ActionPayload) {
    const { targets, refs } = getChildTargets(this.nodes, payload.targets);

    tl.to(targets, dur, {
      size: 30,
      color: nodeColor,
      ease: Power1.easeInOut,
    }, tl.time())
      .to(refs, dur, {
        strokeWidth: 0,
      }, tl.time());
  }

  toRedEdge(dur: number, tl: TimelineLite, payload: ActionPayload) {
    const { targets } = getChildTargets(this.edges, payload.targets);

    tl.to(targets, dur, {
      color: theme.networkRed,
      strokeWidth: strokeWidth * 2
    }, tl.time());
  }

  toInvisibleEdge(dur: number, tl: TimelineLite, payload: ActionPayload) {
    const { refs } = getChildTargets(this.edges, payload.targets);
    tl.to(refs, dur, {
      opacity: 0,
    }, tl.time());
  }
  toOpacity(dur: number, tl: TimelineLite, payload: any) {
    const { refs: nodeRefs } = getChildTargets(this.nodes, payload.targets.nodes);
    const { refs: edgeRefs } = getChildTargets(this.edges, payload.targets.edges);
    const targetOpacity = payload.toOpacity === undefined ? 1 : payload.toOpacity;
    tl.to(nodeRefs, dur, {
      opacity: targetOpacity,
    }, tl.time());
    tl.to(edgeRefs, dur, {
      opacity: targetOpacity,
    }, tl.time());
  }
  toInvisible(dur: number, tl: TimelineLite, payload: ActionPayload) {
    const { refs } = getChildTargets(this.nodes, payload.targets);
    tl.to(refs, dur, {
      opacity: 0,
    }, tl.time());
  }

  render() {
    const { left, baseZLayer, startOpacity } = this.props;
    return (
      <Outer
        left={left}
        innerRef={ref => { if (ref) this.container = ref; }}
        opacity={startOpacity !== undefined ? startOpacity : 1}
        z={baseZLayer}
      >
        <Svg
          width={spacing.rightPanelStart}
          height="1000px"
          top={0}
          left={5}
          zIndex={baseZLayer}
        >
          {
            this.edges.map((edge, i) => {
              const { source, destination } = edge;
              // const z = source.screenZ < destination.screenZ ? source.screenZ - 1 : destination.screenZ - 1;
              return (
                <EdgeLine
                  key={i}
                  x1={source.screenX}
                  y1={source.screenY}
                  x2={destination.screenX}
                  y2={destination.screenY}
                  innerRef={r => { if (r) edge.ref = r; }}
                />
              );
            })
          }
        </Svg>
        <Svg
          width={spacing.rightPanelStart}
          height="1000px"
          top={0}
          left={5}
          zIndex={baseZLayer + 1}
        >
          {this.nodes.map((node, i) => (
            <circle
              key={i}
              cx={node.screenZ}
              cy={node.screenY}
              ref={r => { if (r) node.ref = r; }}
              z={node.screenZ}
              stroke="FFFFFF"
              strokeWidth="0"
              onMouseOver={() => {
                (document.getElementById('nodeOutput') as HTMLElement).textContent = 'N: ' + i;
                (document.getElementById('nodePosOutput') as HTMLElement).textContent = `x: ${node.x}, y: ${node.y}, z: ${node.z}`;
              }}
            />
          ))}
        </Svg>
      </Outer >);
  }
}

export default Network;


