import UseCaseTactic from '../../actors/UseCaseTactic';
import UseCase from '../../actors/UseCase';
import {
  useCases,
  useCasePositions,
  featuredUseCasePositions,
  featuredUseCaseItems,
  rootPosition,
} from '../../constants/usecases';
import DrawTree from '../../actors/DrawTree';
import StoryTitle from '../../actors/StoryTitle';
import s from '../../constants/strings';
import BulletList from '../../actors/BulletList';

const data = {
  'UseCaseList': {
    component: BulletList,
    props: {
      top: 382,
      left: 451,
      items: s.other.useCases,
      itemStyle: {
        fontSize: 78
      },
      height: 680,
      width: 3790
    }
  },
  'UseCasesCombine': {
    component: BulletList,
    props: {
      top: 330,
      left: 900,
      title: s.other.useCaseIntroTitle,
      items: s.other.useCaseDetails,
      itemStyle: {
        fontSize: 78,
        margin: 25,
      },
      alignLeft: true,
      height: 600,
      width: 2700
    }
  },
  'UseCaseTitle0': {
    component: StoryTitle,
    props: {
      text: s.other.useCaseTitles[0],
      style: {
        left: 900,
      }
    }
  },
  'UseCaseTitle1': {
    component: StoryTitle,
    props: {
      text: s.other.useCaseTitles[1],
      style: {
        top: 17,
        left: 150,
        color: '#FFF',
        fontSize: 100,
        fontWeight: 600
      }
    }
  },
  'UseCaseTitle2': {
    component: StoryTitle,
    props: {
      text: s.other.useCaseTitles[2],
      style: {
        top: 17,
        left: 100,
        color: '#FFF',
        fontSize: 100,
        fontWeight: 600
      }
    }
  },
  'UseCasesTree': {
    component: DrawTree,
    renderIn: 'UseCaseZoomContainer',
    props: {
      color: '#cce4ff',
      left: 195,
      top: 95,
      lines: (featuredUseCasePositions.map((pos, i) => ({
        x1: rootPosition.left + 100,
        y1: rootPosition.top + 100,
        x2: pos.left,
        y2: pos.top - 20,
        start: (featuredUseCasePositions.length - 1 - i) * 400
      }))),
      exitType: 'reverse',
      width: 4,
      svgWidth: 4500,
      strokeLinecap: 'butt'
    }
  },
};

// add use cases and lateral mvmt items
useCases.forEach((useCase, i) => {
  data[`UseCase${i}`] = {
    component: UseCase,
    renderIn: 'UseCaseZoomContainer',
    props: {
      ...useCase,
      ...useCasePositions[i]
    },
};
});

featuredUseCaseItems.forEach((lateralMvmtItem, i) => {
  data[`LateralMvmtItem${i}`] = {
    component: UseCaseTactic,
    renderIn: 'UseCaseZoomContainer',
    props: {
      ...lateralMvmtItem,
      ...featuredUseCasePositions[i],
      rootLeft: rootPosition.left,
      rootTop: rootPosition.top,
    }
  };
});

export default data;


