import * as React from 'react';
import styled from 'styled-components';
import director from '../director';
import autoBind from 'auto-bind';
import layers from '../constants/layers';
import { simpleExit, simpleEnter } from '../motions';
import LockIcon from 'react-icons/lib/md/lock';
import { TimelineLite, Power2 } from 'gsap';
import theme from '../constants/theme';

const Div = styled.div`
  position: absolute;
  -webkit-font-smoothing: antialiased;
`;

const defaultStyle = {
  left: 2370,
  top: 300,
  fontSize: 240,
  fontWeight: 300,
  color: theme.storyTitleDefault,
  zIndex: layers.foreground
} as any;

interface Props {
  entryDuration: number;
  actorName: string;
  text: string;
  style: any;
  icon?: string;
  enterPos?: { x: number, y: number };
  exitPos?: { x: number, y: number };
}

class StoryTitle extends React.Component<Props> {
  container: Element;
  children: Element[];

  constructor(props: Props) {
    super(props);
    autoBind(this);

    director.registerMotions(this, props.actorName,
      {
        enter: props.enterPos
          ? simpleEnter(this, `+=${props.enterPos.x}`, `+=${props.enterPos.y}`)
          : simpleEnter(this, '-=50', '+=0'),
        exit: props.exitPos
          ? simpleExit(this, `+=${props.exitPos.x}`, `+=${props.exitPos.y}`)
          : simpleExit(this, '+=50', '+=0'),
        transition: this.transition,
      });
  }

  transition(dur: number, tl: TimelineLite, payload: any) {
    const { vars } = payload;
    const now = tl.time();
    tl.to(this.container, dur, {
      ...vars,
      ease: Power2.easeInOut,
    }, now);
  }
  
  render() {
    const { icon } = this.props;
    const style = this.props.style
      ? { ...defaultStyle, ...this.props.style } : defaultStyle;
    return (
      <Div innerRef={r => { if (r) this.container = r; }} style={style}>
        {icon === 'lock' && (
          <LockIcon size={200} color={style.color} style={{ marginRight: 40, paddingBottom: 40 }} />
        )}
        {this.props.text}
      </Div>
    );
  }
}

export default StoryTitle;



