import s from '../../constants/strings';
import theme from '../../constants/theme';

const data = {
  'OperationsCards': {
    component: OperationsCards,
    props: {}
  },
  'OperationsTable': {
    component: OperationsTable,
    props: {}
  },
  'OperationsTextBackground': {
    component: TextBackground,
    props: {
      text: s.operations.titles[0],
      left: 2320,
      xShrunk: -1700,
      color: theme.textBackground,
      toColor: '#FFF'
    }
  },
};
export default data;


