import * as React from 'react';
import styled from 'styled-components';
import director from '../director';
import { simpleExit, staggerEnterFrom } from '../motions';
import autoBind from 'auto-bind';
import layers from '../constants/layers';
import s from '../constants/strings';
import theme from '../constants/theme';
import moment from 'moment-timezone';

const ops = s.operations;
const Outer = styled.div`
  position: absolute;
  top: 80px;
  left: 3100px;
  z-index: ${layers.foreground};
`;

const CardRect = styled.div`
  width: 600px;
  height: 110px;
  padding: 20px;
  padding-left: 50px;
  background: ${(p: { color: string }) => p.color};
  box-shadow: 3px 7px 30px 1px ${theme.operationsCardsBoxShadow};
  border-radius: 5px;
`;

const Heading = styled.div`
  position: relative;
  left: 701px;
  top: -62px;
  width: 630px;
  font-size: 60px;
  font-weight: 600;
  white-space: nowrap;
  color: ${theme.operationsCardsHeading};
`;

const Column = styled.div`
  position: absolute;
  top: ${(p: { top: number, left: number }) => p.top}px;
  left: ${(p: { top: number, left: number }) => p.left}px;
  display: flex;
  height: 855px;
  flex-flow: column nowrap;
  justify-content: space-between;
`;

const ColumnName = styled.div`
  position: relative;
  left: 33px;
  top: -5px;
  width: 630px;
  font-size: 38px;
  font-weight: 600;
`;

const Dot = styled.div`
  position: absolute;
  left: 30px;
  top: 103px;
  width: 20px;
  height: 20px;
  background: ${(p: { color: string }) => p.color};
  border-radius: 50%;
`;

const Title = styled.div`
  position: absolute;
  left: 30px;
  top: 13px;
  width: 603px;
  font-size: 25px;
  font-weight: 600;
`;

const Date = styled.div`
  position: absolute;
  right: 30px;
  top: 90px;
  font-size: 20px;
  font-weight: 500;
`;

const Time = styled.div`
  position: absolute;
  right: 30px;
  top: 110px;
  font-size: 20px;
  font-weight: 500;
`;

const Detail1 = styled.div`
  position: absolute;
  left: 64px;
  top: 97px;
  width: 630px;
  font-size: 27px;
  font-weight: 500;
`;
const Detail2 = styled.div`
  position: absolute;
  left: 260px;
  top: 97px;
  width: 630px;
  font-size: 27px;
  font-weight: 500;
`;

interface Props {
  actorName: string;
  entryDuration?: number;
}

class OperationsCards extends React.Component<Props>  {
  container: Element;
  card: Element;
  children: Element[];

  constructor(props: Props) {
    super(props);
    autoBind(this);
    this.children = [];

    director.registerMotions(this, props.actorName,
      {
        enter: staggerEnterFrom(this, '+=0', '+=20', 0.15),
        exit: simpleExit(this),
      }
    );
  }

  addChildRef(ref: Element) {
    if (ref)
      this.children.push(ref);
  }

  render() {
    const today = moment().tz('America/New_York').format('MM/DD/YYYY');
    return (
      <Outer
        innerRef={ref => { if (ref) this.container = ref; }}
      >
        <Heading innerRef={this.addChildRef}>{ops.titles[2]}</Heading>
        {ops.columns.map((col, i) => (
          <Column top={28} left={i * 720} key={i}>
            <ColumnName innerRef={this.addChildRef}>
              {ops.columnNames[i]}
            </ColumnName>
            {col.map((item, j) => (
              <CardRect color={item.cardColor} key={j} innerRef={this.addChildRef}>
                <Title>{item.name}</Title>
                <Date>{today}</Date>
                <Time>{item.time}</Time>
                <Dot color={item.dotColor} />
                <Detail1>{item.detail1}</Detail1>
                <Detail2>{item.detail2}</Detail2>
              </CardRect>
            ))}
          </Column>
        ))}
      </Outer>
    );
  }
}

export default OperationsCards;



