import params from '../utils/urlParams';

const {
  rotationsPerDegree,
  rotationTimeScale,
  timeScale,
  fps,
  scrollLeft,
  scrollTop,
  resize,
  wobbleGroups,
  numWobbles,
  wobbleMax,
  wobbleDur,
  controlGroup,
  isController,
  offsetMs,
} = params;

const timezones = [
  {
    key: 'America/Creston',
    display: 'PHX',
  },
  {
    key: 'America/Chicago',
    display: 'MSP',
  },
  {
    key: 'America/New_York',
    display: 'ATL',
  },
  {
    key: 'Etc/Zulu',
    display: 'UTC',
  },
  {
    key: 'Europe/Paris',
    display: 'CH',
  },
];

const g = {
  rotationsPerDegree: rotationsPerDegree === undefined ? 6 : rotationsPerDegree,
  rotationTimeScale: rotationTimeScale === undefined ? 1 : rotationTimeScale * 2.5,
  timeScale: timeScale === undefined ? 1 : timeScale,
  fps: fps === undefined ? 60 : fps,
  scrollLeft: scrollLeft === undefined ? 0 : scrollLeft,
  scrollTop: scrollTop === undefined ? 0 : scrollTop,
  resize: resize === undefined ? 'horizontal' : resize,
  wobbleMax: wobbleMax === undefined ? 14 : parseInt(wobbleMax, 10),
  numWobbles: numWobbles === undefined ? 5 : parseInt(numWobbles, 10),
  wobbleDur: wobbleDur === undefined ? 5 : parseFloat(wobbleDur),
  wobbleGroups: wobbleGroups === undefined ? 23 : parseInt(wobbleGroups, 10),
  vertical: 'Energy/Resources', // random vertical
  timezones: timezones,
  controlGroup: controlGroup === undefined ? -1 : controlGroup, // -1 by default to signify 'no group'
  isController: isController === undefined ? false : isController === 'true',
  socket: undefined as undefined | SocketIOClient.Socket,
  presentation: 'lateral',
  domain: 'www.weylandyu.biz',
  timeOffset: offsetMs === undefined ? 0 : parseInt(offsetMs, 10) / 1000,
};

export const getVertical = () => 'Energy/Resources';

export const getPresentation = () => 'lateral';   

export const getDomain = () => g.domain;

const _letters = ['i', 'o', 'l', 'n', 'g', 's', '9', 'p', 'e', '8', 'i', 'b', 'o', 'z', 'e', 'a', 'o'];
const _glyphs = ['1', '0', '1', 'η', '9', '5', 'g', 'ρ', 'ё', 'B', 'ї', '8', 'ӧ', '2', 'ε', 'æ', 'œ'];

export const getNthHomograph = (n: number) => {
  const matches: any = [];
  const arr = Array.from(g.domain);
  arr.forEach((lettera, idxA) => {
    _letters.forEach((letterb, idxB) => {
      if (lettera === letterb) {
        if (!matches.find(m => m[0] === lettera && m[1] === idxA && m[2] === idxB)) {
          matches.push([lettera, idxA, idxB]);
        }
      }
    });
  });
  if (!matches.length) return g.domain;
  const match = matches[n % matches.length];
  arr[match[1]] = _glyphs[match[2]];
  return arr.join('');
};

export default g;


