import * as React from 'react';
import styled from 'styled-components';
import director from '../director';
import autoBind from 'auto-bind';
import CircleDot from 'react-icons/lib/md/adjust';
import ExclamationCircle from 'react-icons/lib/fa/exclamation-circle';
import layers from '../constants/layers';
import spacing from '../constants/spacing';
import { staggerEnterFrom, staggerExitTo } from '../motions';
import { getNthHomograph } from '../constants/config';
import { TimelineLite, Power1 } from 'gsap';
import theme from '../constants/theme';

const Outer = styled.div`
  width: 445px;
  height: 765px;
  position: absolute;
  left: ${(p: { left: number, top: number }) => p.left}px;
  top: ${p => p.top}px;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
  z-index: ${layers.foreground};
`;

const Title = styled.div`
  position: absolute;
  left: -20px;
  top: -41px;
  font-size: 44px;
  font-weight: 600;
  white-space: nowrap;
  z-index: ${layers.foreground + 30};
`;

const Container = styled.div`
  height: 90px;
  width: auto;
  position: relative;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-between;
`;

const DomainName = styled.div`
  font-size: 43px;
  font-weight: 500;
  white-space: nowrap;
  z-index: ${layers.foreground + 30};
`;

const Detail1 = styled.div`
  font-size: 29px;
  font-weight: 300;
  white-space: nowrap;
  z-index: ${layers.foreground + 30};
`;

const Dot = styled.div`
  position: absolute;
  top: 9px;
  right: -68px;
  opacity: 0;
`;

const Icon = styled(ExclamationCircle) `
  position: absolute;
  left: -70px;
  top: 9px;
  color: ${theme.homographListIcon};
`;

interface Props {
  actorName: string;
  entryDuration: number;
  title?: string;
  left?: number;
  top?: number;
}

class HomographList extends React.Component<Props> {
  container: Element;
  children: Element[];
  dots: Element[];
  constructor(props: Props) {
    super(props);
    this.children = [];
    this.dots = [];
    autoBind(this);
    director.registerMotions(this, props.actorName,
      {
        enter: staggerEnterFrom(this, 0, 100),
        showDots: this.showDots,
        exit: staggerExitTo(this, 0, -100),
      }
    );
  }

  addChildRef(ref: Element) {
    if (ref)
      this.children.push(ref);
  }

  addDotRef(ref: Element) {
    if (ref)
      this.dots.push(ref);
  }

  showDots(dur: number, tl: TimelineLite) {
    const amt = dur / this.dots.length;
    tl.staggerTo(this.dots, amt * 2, {
      opacity: 1,
      ease: Power1.easeInOut,
    }, amt, tl.time());
  }

  render() {
    const { top, left, title } = this.props;
    const topPos = top !== undefined ? top : 80;
    const leftPos = left !== undefined ? left : spacing.leftPanelEndAlt2 + 100;
    const homographs = [0, 1, 2, 3, 4, 5].map(getNthHomograph);
    return (
      <Outer
        top={topPos}
        left={leftPos}
      >
        {title && <Title innerRef={this.addChildRef}>{title}</Title>}
        {homographs.map((hg, i) => (
          <Container key={i} innerRef={this.addChildRef}>
            <DomainName>{hg}</DomainName>
            <Detail1>{'LKJEWOIGUOWIEUF'}</Detail1>
            <Icon size={50} />
            <Dot innerRef={this.addDotRef} >
              <CircleDot size={50} color={theme.homographListDot} />
            </Dot>
          </Container>
        ))}
      </Outer>
    );
  }
}

export default HomographList;



