import * as React from 'react';
import styled from 'styled-components';
import director from '../director';
import FirewallIcon from './IconFirewall';
import autoBind from 'auto-bind';
import layers from '../constants/layers';
import { simpleEnter, simpleExit } from '../motions';
import { getNthHomograph } from '../constants/config';
import theme from '../constants/theme';
import { TimelineLite, Power1 } from 'gsap';

const _width = 610;
const _height = 175;

const Root = styled.div`
  position: absolute;
  left: ${(p: { top: number, left: number }) => p.left}px;
  top: ${(p) => p.top}px;
  width: ${_width}px;
  height: ${_height}px;
  z-index: ${layers.foreground};
  filter: drop-shadow(5px 12px 17px ${theme.cardHomographDropShadow});
`;

const _iconWidth = 100;

const IconOuter = styled.div`
  position: absolute;
  top: 29px;
  left: -50px;
  width: ${_iconWidth}px;
  height: ${_iconWidth}px;
  border-radius: 50%;
  background: ${(p: { color: string }) => p.color};
  z-index: ${layers.foreground + 1};
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
`;

const IconCircle = styled.div`
  width: ${_iconWidth - 16}px;
  height: ${_iconWidth - 16}px;
  border-radius: 50%;
  border: 4px solid white;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const CardRect = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: ${_width}px;
  height: ${_height}px;
  padding: 10px;
  padding-left: 70px;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-around;
  background: ${theme.cardBackground};
  border: 5.2px solid ${theme.cardBackground};
`;

const Title = styled.div`
  font-size: 43px; font-weight: 500;
`;

const Detail1 = styled.div`
  width: 544px;
  color: ${theme.cardHomographDetail1}; font-size: 31px; font-weight: 500;
`;

const Detail2 = styled.div`
  width: 544px;
  color: ${theme.cardHomographDetail2}; font-size: 35px; font-weight: 500;
  opacity: 0;
`;

interface Props {
  actorName: string;
  entryDuration?: number;
  top: number;
  left: number;
  homographIdx: number;
  detail1: string;
  detail2: string;
}

class CardHomograph extends React.Component<Props>  {
  container: Element;
  children: Element[];
  icon: Element;
  card: Element;
  blocker: Element;

  constructor(props: Props) {
    super(props);
    autoBind(this);
    director.registerMotions(this, props.actorName,
      {
        enter: simpleEnter(this, 50, 0),
        showBlocked: this.showBlocked,
        exit: simpleExit(this, 0, -50),
      }
    );
  }

  showBlocked(dur: number, tl: TimelineLite) {
    const now = tl.time();
    tl.to(this.blocker, dur * 0.2, {
      opacity: 1,
    }, now + 0.3)
    .to(this.icon, dur * 0.2, {
      opacity: 1,
    }, now + 0.3)
      .to(this.container, dur * .2, {
        scale: 1.03,
        ease: Power1.easeInOut
      }, now + dur * 0.2)
      .to(this.container, dur * .3, {
        scale: 1,
        ease: Power1.easeInOut
      }, now + dur * 0.5);
  }

  render() {
    const { detail1, detail2, homographIdx, top, left } = this.props;
    const title = getNthHomograph(homographIdx);
    return (
      <Root
        innerRef={ref => { if (ref) this.container = ref; }}
        top={top}
        left={left}
      >
        <IconOuter
          color={theme.cardHomographIconOuter}
          innerRef={ref => { if (ref) this.icon = ref; }}
        >
          <IconCircle>
            <FirewallIcon />
          </IconCircle>
        </IconOuter>
        <CardRect
          innerRef={ref => { if (ref) this.card = ref; }}
        >
          <Title>{title}</Title>
          <Detail1>{detail1}</Detail1>
          <Detail2 innerRef={r => { if (r) this.blocker = r; }}><div>{detail2}</div></Detail2>
        </CardRect>
      </Root>
    );
  }
}

export default CardHomograph;



