import * as React from 'react';
import styled from 'styled-components';
import director from '../director';
import autoBind from 'auto-bind';
import layers from '../constants/layers';
import { simpleExit, simpleEnter } from '../motions';
import { TimelineLite, Power1 } from 'gsap';
import strings from '../constants/strings';

const Root = styled.div`
  position: absolute;
  left: 0;
  top: 0;
  width: 1000px;
  height: 1000px;
  color: #FFF;
  z-index: ${layers.foreground + 10};
`;

const _titleHeight = 120;
const _itemHeight = 100;
const _startPosition = 305;
const _crossHairWidth = 500;

const Title = styled.div`
  position: absolute;
  left: 0;
  top: 0;
  font-size: 70px;
  padding-left: 80px;
  padding-right: 80px;
  line-height: ${_titleHeight}px;
  border-bottom: 10px solid white;
  height: ${_titleHeight}px;
  font-weight: 600;
`;

const Scroller = styled.div`
  position: absolute;
  top: 120px; 
  left: 180px;
  height: ${1000 - _titleHeight}px;
  width: 1000px;
  overflow: hidden;
  opacity: 0.8;
  z-index: ${layers.foreground + 15};
`;

const Vertical = styled.div`
  height: ${_itemHeight}px;
  display: inline-block;
  padding-left: 25px;
  font-size: 44px;
  font-weight: 200;
  white-space: nowrap;
  z-index: ${layers.foreground + 15};
`;

const CrossHair = styled.div`
  position: absolute;
  top: 360px;
  left: 0;
  border: 5px solid #FFF;
  border-radius: 15px;
  width: ${_crossHairWidth}px;
  height: ${_itemHeight * 0.8}px;
  opacity: 0;
  z-index: ${layers.foreground + 20};
`;

const LineMask = styled.div`
  position: absolute;
  top: 482px;
  left: 185px;
  z-index: ${layers.foreground + 11};
  width: ${_crossHairWidth + 5}px;
  height: ${_itemHeight * 0.8 + 5}px;
  background-color: #044159;
  opacity: 0;
  border-radius: 15px;
`;

interface Props {
  actorName: string;
  entryDuration?: number;
  verticals: string[];
}

class Verticals extends React.Component<Props>  {
  container: Element;
  scroller: Element;
  crosshair: Element;
  lineMask: Element;
  children: Element[];

  constructor(props: Props) {
    super(props);
    autoBind(this);
    director.registerMotions(this, props.actorName,
      {
        enter: simpleEnter(this, -50, 0),
        scrollTo: this.scrollTo,
        exit: simpleExit(this, -50, 0),
      }
    );
    this.children = [];
  }

  scrollTo(dur: number, tl: TimelineLite, payload: any) {
    const vertical = payload.getTarget();
    const itemIdx = this.props.verticals.findIndex(it => it === vertical);
    if (itemIdx === -1) { console.error('Vertical selection not found!'); return; }
    const now = tl.time();
    const width = vertical.length * 22 + 20;
    const scrollPos = _startPosition - itemIdx * _itemHeight;

    const w = width > _crossHairWidth ? width : _crossHairWidth;
    tl.set([this.crosshair, this.lineMask], { width: w }, now);

    tl.to(this.scroller, dur, {
      y: scrollPos,
      ease: Power1.easeInOut
    }, now)
      .to([this.crosshair, this.lineMask], dur * 0.5, {
        opacity: 1,
        ease: Power1.easeInOut
      }, now);
  }

  addChildRef(r: Element) {
    if (r) this.children.push(r);
  }

  render() {
    const { verticals } = this.props;
    return (
      <Root
        innerRef={ref => { if (ref) this.container = ref; }}
      >
        <Title>{strings.intro.verticalsTitle}</Title>
        <LineMask innerRef={r => { if (r) this.lineMask = r; }} />
        <Scroller>
          <CrossHair innerRef={r => { if (r) this.crosshair = r; }} />
          <div
            style={{ position: 'absolute', top: 70, width: '100%' }}
            ref={r => { if (r) this.scroller = r; }}
          >
            {verticals.map((vertical, i) => (
              <div key={i} style={{ zIndex: layers.foreground + 15 }}>
                <Vertical innerRef={this.addChildRef} >{vertical}</Vertical>
              </div>
            ))}
          </div>
        </Scroller>
      </Root>
    );
  }
}

export default Verticals;



