import * as React from 'react';
import styled from 'styled-components';
import director from '../director';
import { TimelineLite, Power2 } from 'gsap';
import autoBind from 'auto-bind';
import { simpleExit } from '../motions';
import layers from '../constants/layers';
import { getPositionedThreatActors, Coord2D } from '../utils/positioning';
import {
  countryToFlag,
  threatActorIntroSpots,
  threatActorToCountry,
  threatActorFocusSpots,
  getThreatActorImg,
} from '../constants/threatActors';
import { getIntroThreatActors } from '../utils/threatActors';
import Svg from './Svg';
import { newOrder } from '../utils/threatActors';

export const cardWidth = 540;
export const cardHeight = 130;

const Outer = styled.div`
  position: absolute;
  left: 1200px;
  top: 0;
  width: 2400px;
  height: 1000px;
  z-index: ${layers.foreground + 28};
  filter: drop-shadow(6px 6px 7px rgba(30, 30, 50, 0.5));
`;

const Line = styled.line`
  stroke-width: 4px;
  stroke: #FFF;
  opacity: 0;
`;

const ActorImg = styled.img`
  position: absolute;
  left: -60px;
  top: ${(p: { top: number, height: number, left: number }) => p.top}px;
  left: ${p => p.left}px;
  height: ${p => p.height}px;
  width: auto;
  z-index: ${layers.foreground + 31};
  border-radius: 50%;
`;

const CardRect = styled.div`
  position: absolute;
  left: 0;
  top: 0;
  width: ${cardWidth}px;
  height: ${cardHeight}px;
  padding-left: 50px;
  background: #FFF;
  z-index: ${layers.foreground + 30};
  opacity: 0;
`;

const CardOuter = styled.div`
  position: absolute;
  left: ${(p: { left: number, top: number }) => p.left}px;
  top: ${p => p.top}px;
  z-index: ${layers.foreground + 30};
`;

const Contents = styled.div`
  position: absolute;
  left: 110px;
  top: 0;
  height: 100%;
  width: 470px;
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-around;
  align-items: flex-start;
`;

const ActorName = styled.div`
  font-size: 38px;
  font-weight: 400;
`;

const ActorDesc = styled.div`
  font-size: 18px;
  font-weight: 500;
  margin-bottom: 10px;
`;

const CardImg = styled.img`
  position: absolute;
  right: 0;
  top: 0;
  width: 85px;
  height: 53px;
`;

const ImgCover = styled.div`
  position: absolute;
  right: 6px;
  top: 29.4px;
  width: 100px;
  height: 50px;
  transform: rotate(25deg);
  background: #FFF;
`;

export interface ThreatActorsProps {
  actorName: string;
  entryDuration?: number;
  getVertical: () => string;
}

class ThreatActors extends React.Component<ThreatActorsProps>  {
  container: Element;
  children: Element[];
  threatActors: {
    name: string;
    country: string;
    startPos: Coord2D;
    remains: boolean;
    endPos?: {
      country: Coord2D;
      actor: Coord2D;
    };
    ref?: Element;
    cardRef?: Element;
    lineRef?: Element;
  }[];

  constructor(props: ThreatActorsProps) {
    super(props);
    autoBind(this);

    director.registerMotions(this, props.actorName,
      {
        enter: this.enter,
        transition: this.transition,
        toFocusPositions: this.toFocusPositions,
        removeNonFocused: this.removeNonFocused,
        toSmallPositions: this.toSmallPositions,
        showLines: this.showLines,
        exit: simpleExit(this, '-=50', 0),
      }
    );
    const vertical = this.props.getVertical();
    // split out the threat actors into those that remain after the map shrinks and those that don't;
    const threatActorsPositioned = getPositionedThreatActors(vertical);
    this.threatActors = getIntroThreatActors(vertical).map((actor, n) => {
      const taPositioned = threatActorsPositioned.find(a => a.name === actor);
      if (taPositioned)
        return {
          remains: true,
          name: actor,
          country: threatActorToCountry.get(actor) as string,
          startPos: threatActorIntroSpots[n] as Coord2D,
          endPos: {
            country: taPositioned.countryPosition as Coord2D,
            actor: taPositioned.actorPosition as Coord2D,
          }
        };
      else
        return {
          remains: false,
          name: actor,
          country: threatActorToCountry.get(actor) as string,
          startPos: threatActorIntroSpots[n] as Coord2D,
        };
    });
  }

  enter(duration: number, tl: TimelineLite) {
    const { threatActors } = this;
    const dur = 4 * duration / threatActors.length;
    const delay = (duration - dur) / threatActors.length;
    const refs: Element[] = [];

    newOrder.forEach(i => {
      refs.push(threatActors[i].ref as Element);
    });
    threatActors.map(ta => ta.ref);
    tl.staggerFrom(refs, dur, {
      opacity: 0,
      scale: 0.8,
      ease: Power2.easeOut,
    }, delay, tl.time());
  }

  transition(dur: number, tl: TimelineLite, payload: any) {
    const { vars } = payload;
    const now = tl.time();
    tl.to(this.container, dur, {
      ...vars,
      ease: Power2.easeInOut,
    }, now);
  }

  toFocusPositions(dur: number, tl: TimelineLite) {
    const { threatActors } = this;

    const now = tl.time();
    let posIdx = 0;
    const delay = dur / threatActors.length;
    const duration = dur * 0.3;
    threatActors.forEach(ta => {
      const { x, y, order } = threatActorFocusSpots[posIdx];
      if (!ta.ref || !ta.cardRef) return;
      if (ta.endPos) {
        tl.to(ta.ref, duration, {
          left: x,
          top: y,
          scale: 1.2,
          ease: Power2.easeInOut,
        }, now + order * delay)
          .set(ta.cardRef, { x: -20 }, now)
          .to(ta.cardRef, duration, {
            opacity: 1,
            x: 0,
            ease: Power2.easeOut,
          }, now + (order + 3) * delay);
        posIdx++;
      }
    });
  }

  removeNonFocused(dur: number, tl: TimelineLite) {
    const now = tl.time();
    this.threatActors.forEach(ta => {
      if (!ta.ref || !ta.cardRef) return;
      if (!ta.endPos) {
        tl.to(ta.ref, dur, {
          opacity: 0,
          scale: 0.2,
          ease: Power2.easeIn,
        }, now);
      }
    });
  }

  toSmallPositions(dur: number, tl: TimelineLite) {
    const { threatActors } = this;

    const now = tl.time();
    let posIdx = 0;
    const delay = dur / threatActors.length;
    const duration = dur * 0.3;
    threatActors.forEach(ta => {
      if (!ta.ref || !ta.cardRef || !ta.lineRef) return;
      if (ta.endPos) {
        const { order } = threatActorFocusSpots[posIdx];
        const { actor } = ta.endPos;
        tl.to(ta.ref, duration, {
          left: actor.x,
          top: actor.y,
          ease: Power2.easeInOut,
        }, now + order * delay);
        posIdx++;
      }
    });
  }

  showLines(dur: number, tl: TimelineLite) {
    const { threatActors } = this;

    const now = tl.time();
    let posIdx = 0;
    const delay = dur / threatActors.length;
    const duration = dur * 0.3;
    threatActors.forEach(ta => {
      if (!ta.ref || !ta.cardRef || !ta.lineRef) return;
      if (ta.endPos) {
        const { order } = threatActorFocusSpots[posIdx];
        tl.to(ta.lineRef, duration, {
          opacity: 1,
          ease: Power2.easeOut,
        }, now + order * delay);
        posIdx++;
      }
    });
  }

  render() {
    const { threatActors } = this;
    return (
      <Outer
        innerRef={ref => { if (ref) this.container = ref; }}
      >
        <Svg
          left={0}
          top={0}
          zIndex={layers.foreground + 29}
          width="3000px"
          height="1500px"
        >
          {threatActors.filter(ta => ta.endPos !== undefined).map((ta, i) => {
            if (!ta.endPos) return;
            return (
              <Line
                x1={ta.endPos.actor.x + cardWidth * .5}
                y1={ta.endPos.actor.y + cardHeight * .5}
                x2={ta.endPos.country.x}
                y2={ta.endPos.country.y}
                key={i}
                innerRef={r => { if (r) ta.lineRef = r; }}
              />
            );
          })}
        </Svg>
        {threatActors.map((ta, i) => {
          const { img, top, height, left } = getThreatActorImg(ta.name);
          return (
            <CardOuter
              key={i}
              left={ta.startPos.x}
              top={ta.startPos.y}
              innerRef={r => { if (r) ta.ref = r; }}
            >
              <ActorImg src={img} top={top} left={left} height={height} />
              <CardRect innerRef={r => { if (r) ta.cardRef = r; }} >
                <CardImg src={countryToFlag.get(ta.country)} />
                <ImgCover />
                <Contents>
                  <ActorName>{'Item Name Here'}</ActorName>
                  <ActorDesc>{'Item Description Here - something really good and witty, but descriptive too.'}</ActorDesc>
                </Contents>
              </CardRect>
            </CardOuter>
          );
        })}
      </Outer>
    );
  }
}

export default ThreatActors;



