const cardInfectedHost = {
  blockedTitle: s.ransomware.hostBlockedTitle,
  left: 100,
  top: 0,
};

const shiftNodeDetailsX = -100;

// initial parameters for the directory tree in the Ransomware flow
const dirTree: any = {
  lengths: [1450, 900, 200],
  ratio: 3.1, // the slope (as dx/dy) of the lines
};
// add the root, branch, and 'leaf' lines for the directory tree
dirTree.root = getLine(0, -2, dirTree.lengths[0], 4, 0, dirTree.ratio);
dirTree.leftBranch = getLine(dirTree.root.x2, dirTree.root.y2, dirTree.lengths[1], 3, dirTree.lengths[0], dirTree.ratio);
dirTree.rightBranch = getLine(dirTree.root.x2, dirTree.root.y2, dirTree.lengths[1], 1, dirTree.lengths[0], dirTree.ratio);
dirTree.leaves = [0, 1, 2, 3, 4, 5, 6, 7].map(n => {
  const totalX = dirTree.rightBranch.x2 - dirTree.leftBranch.x2;
  const totalY = dirTree.rightBranch.y2 - dirTree.leftBranch.y2;
  const x = dirTree.leftBranch.x2 + (n / 7) * totalX;
  const y = dirTree.leftBranch.y2 + (n / 7) * totalY;

  const timeStart = distanceFrom(dirTree.root.x2, dirTree.root.y2, x, y);
  return getLine(x,
    y,
    dirTree.lengths[2],
    4,
    dirTree.lengths[0] + timeStart,
    dirTree.ratio);
});

