
export const getLine = (x1: number, y1: number, length: number, quadrant: number, start: number, ratio: number) => {
  const dy = length / Math.sqrt((ratio * ratio) + 1);
  const dx = ratio * dy;

  const x2 = (quadrant === 2 || quadrant === 3) ? x1 - dx : x1 + dx;
  const y2 = (quadrant === 1 || quadrant === 2) ? y1 - dy : y1 + dy;
  return { x1, y1, x2, y2, start };
};

export const getLines = (
  ratio: number,
  lineParams: number[][]
) => {
  return lineParams.map(l => {
    const [x1, y1, length, quadrant, start] = l as any;
    return getLine(x1, y1, length, quadrant, start, ratio);
  });
};

export const distanceFrom = (x1: number, y1: number, x2: number, y2: number) => {
  return Math.sqrt(Math.pow(x2 - x1, 2) + Math.pow(y2 - y1, 2));
};


