
import {
  threatActorToCountry,
  countryToPosition,
  threatActorSpots,
} from '../constants/threatActors';
import { getThreatActorsForVertical } from '../utils/threatActors';
import { cardWidth, cardHeight } from '../actors/ThreatActors';

const _maxLineLength = 500;

export interface Coord2D {
  x: number;
  y: number;
}

interface ThreatActor {
  name: string;
  country: string;
  position: Coord2D;
}

// ThreatActor and Spot wrapped with details for the matching algorithm
interface ThreatActorMatchable {
  self: ThreatActor;
  current?: Coord2D;
  proposals: Coord2D[];
  prefs: Coord2D[];
}
interface SpotMatchable {
  self: Coord2D;
  current?: ThreatActor;
  prefs: ThreatActor[];
}

// what comes out at the end: a threat actor, positioned near its country
export interface ThreatActorPositioned {
  name: string;
  country: string;
  countryPosition: Coord2D;
  actorPosition: Coord2D;
}

const getDistance = (from: Coord2D, to: Coord2D) =>
  Math.sqrt(
    Math.pow(from.x - to.x + (cardWidth / 2), 2) +
    Math.pow(from.y - to.y + (cardHeight / 2), 2)
  );

const getUnitMagnitude = (from: Coord2D, to: Coord2D) => {
  const dist = getDistance(from, to);
  const scalar = 1 / dist;
  return {
    x: (to.x - from.x) * scalar,
    y: (to.y - from.y) * scalar,
  };
};

const cmpPositions = (a: any, b: any) => {
  if (a.dist > b.dist) return 1;
  if (b.dist > a.dist) return -1;
  return 0;
};

// get an actor's 'preferences' of what spots it's closest to 
const getThreatActorPrefs = (ta: ThreatActor, spots: Coord2D[]): Coord2D[] => {
  const prefs = spots.map(pos => ({
    dist: getDistance(pos, ta.position),
    pos
  }));
  prefs.sort(cmpPositions);
  // console.log(prefs.map(p => p.pos));
  return prefs.map(p => p.pos);
};
// get a spot's preferences of what actors it's closest to the county of
const getSpotPrefs = (spot: Coord2D, threatActors: ThreatActor[]): ThreatActor[] => {
  const prefs = threatActors.map(ta => ({
    dist: getDistance(spot, ta.position),
    ta: ta,
  }));
  prefs.sort(cmpPositions);
  // console.log(prefs.map(p => p.ta));
  return prefs.map(p => p.ta);
};

const getPairs = (threatActors: ThreatActor[], spots: Coord2D[]): { actorsWithPrefs: ThreatActorMatchable[], spotsWithPrefs: SpotMatchable[] } => {
  const actorsWithPrefs = threatActors.map(ta => ({
    self: ta,
    prefs: getThreatActorPrefs(ta, spots),
    current: undefined,
    proposals: [] as Coord2D[]
  }));
  const spotsWithPrefs = spots.map(sp => ({
    self: sp,
    prefs: getSpotPrefs(sp, threatActors),
    current: undefined,
  }));
  return { actorsWithPrefs, spotsWithPrefs };
};

const doGaleShapleyMatching = (threatActors: ThreatActor[]): ThreatActorPositioned[] => {
  
  const spotList: Coord2D[] = threatActorSpots.get(threatActors.length) as Coord2D[];
  
  const { actorsWithPrefs, spotsWithPrefs } = getPairs(threatActors, spotList);

  // while any actor doesn't have a spot,
  while (actorsWithPrefs.reduce((prev, cur) => cur.current === undefined || prev, false)) {

    // get first actor without a spot
    let actorWithPrefs = actorsWithPrefs.find(acp => acp.current === undefined) as ThreatActorMatchable;
    // actor 'proposes' to favorite spot not in its list of previous proposals
    const spot = actorWithPrefs.prefs.find(s => !actorWithPrefs.proposals.includes(s)) as Coord2D;
    const spotWithPrefs = spotsWithPrefs.find(swp => swp.self === spot) as SpotMatchable;
    actorWithPrefs.proposals.push(spot);
    // if she's not engaged, get engaged
    if (!spotWithPrefs.current) {
      actorWithPrefs.current = spot;
      spotWithPrefs.current = actorWithPrefs.self;
    } else {
      const otherActorAffinity = spotWithPrefs.prefs.findIndex(ac => ac === spotWithPrefs.current);
      const thisActorAffinity = spotWithPrefs.prefs.findIndex(ac => ac === actorWithPrefs.self);
      const otherActorWithPrefs = actorsWithPrefs.find(acp => acp.self === spotWithPrefs.current) as ThreatActorMatchable;
      // if she likes him more, they get engaged
      if (thisActorAffinity < otherActorAffinity) {
        otherActorWithPrefs.current = undefined;
        actorWithPrefs.current = spot;
        spotWithPrefs.current = actorWithPrefs.self;
      }
    }
  }
  return actorsWithPrefs.map(acp => ({
    name: acp.self.name,
    country: acp.self.country,
    countryPosition: acp.self.position as Coord2D,
    actorPosition: acp.current as Coord2D
  }));
};

// force-direct all the actor positions away from eachother
export const forceDirect = (pActors: ThreatActorPositioned[], scale: number, power: number = 2): ThreatActorPositioned[] => {

  const alteredPActors: ThreatActorPositioned[] = [];

  pActors.forEach((pActor, n) => {
    const alteration = { x: 0, y: 0 };
    pActors.forEach(((otherPActor, m) => {
      if (m === n) return;
      const dist = getDistance(pActor.actorPosition, otherPActor.actorPosition) * 0.01;
      const unit = getUnitMagnitude(otherPActor.actorPosition, pActor.actorPosition);
      const scalar = 1 / Math.pow(dist, power);
      alteration.x += unit.x * scalar;
      alteration.y += unit.y * scalar;
    }));

    let alteredX = pActor.actorPosition.x + alteration.x * scale;
    let alteredY = pActor.actorPosition.y + alteration.y * scale;

    // keep the actors from flying too far away from their country
    const lineLength = getDistance({ x: alteredX, y: alteredY }, pActor.countryPosition);
    if (lineLength > _maxLineLength) {
      const scalar = _maxLineLength / lineLength;
      const xDist = pActor.countryPosition.x - alteredX;
      const yDist = pActor.countryPosition.y - alteredY;
      alteredX = pActor.countryPosition.x + xDist * scalar;
      alteredY = pActor.countryPosition.y + yDist * scalar;
    }
    alteredPActors.push({
      ...pActor,
      actorPosition: {
        x: (alteredX < 200 || alteredX > 1900) ? pActor.actorPosition.x : alteredX,
        y: (alteredY < 10 || alteredY > 950) ? pActor.actorPosition.y : alteredY,
      }
    });
  });
  return alteredPActors;
};

// get a set of pretty good positions for a threat Actors in a vertical
export const getPositionedThreatActors = (selectionName: string) => {
  let taNames = getThreatActorsForVertical(selectionName) as string[];
  
  const threatActors = taNames
    .map(name => ({ name, country: (threatActorToCountry.get(name) as string) }))
    .map(({ name, country }) => ({
      name, country,
      position: countryToPosition.get(country) as Coord2D,
    }));
  
  let pActors = doGaleShapleyMatching(threatActors);

  return pActors;
};


