import * as React from 'react';
import styled from 'styled-components';
import director from '../director';
import IconLaptop from 'react-icons/lib/md/laptop-chromebook';
import autoBind from 'auto-bind';
import layers from '../constants/layers';
import { simpleEnter, simpleExit } from '../motions';
import { TimelineLite, Power2, Power1 } from 'gsap';
import theme from '../constants/theme';

const _width = 575;
const _height = 148;

const Root = styled.div`
  position: absolute;
  left: ${(p: { top: number, left: number }) => p.left}px;
  top: ${(p) => p.top}px;
  width: ${_width}px;
  height: ${_height}px;
  z-index: ${layers.foreground + 10};
  filter: drop-shadow(5px 12px 17px ${theme.cardDropShadow});
`;

const _iconWidth = 100;

const IconOuter = styled.div`
  position: absolute;
  top: 29px;
  left: -50px;
  width: ${_iconWidth}px;
  height: ${_iconWidth}px;
  border-radius: 50%;
  background: ${(p: { color: string }) => p.color};
  z-index: ${layers.foreground + 1};
  display: flex;
  justify-content: center;
  align-items: center;
`;

const IconCircle = styled.div`
  width: ${_iconWidth - 16}px;
  height: ${_iconWidth - 16}px;
  border-radius: 50%;
  border: 4px solid white;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const CardRect = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: ${_width}px;
  height: ${_height}px;
  background: ${theme.cardBackground};
  border: 5.2px solid ${theme.cardBackground};
`;

const Title = styled.div`
  position: absolute;
  left: 70px;
  top: 20px;
  font-size: 43px; font-weight: 500;
`;

const Detail1 = styled.div`
  position: absolute;
  left: 70px;
  top: 85px;
  color: ${theme.cardHostPhishingDetail1}; font-size: 33px; font-weight: 500;
`;

const Detail2 = styled.div`
  position: absolute;
  left: 230px;
  top: 85px;
  font-size: 33px; font-weight: 500;
`;

interface Props {
  actorName: string;
  entryDuration?: number;
  top: number;
  left: number;
  secondLeft: number;
  secondTop: number;
  title: string;
  detail1: string;
  detail2: string;
}

class CardHostPhishing extends React.Component<Props>  {
  container: Element;
  children: Element[];
  icon: Element;
  card: Element;
  blocker: Element;

  constructor(props: Props) {
    super(props);
    autoBind(this);
    director.registerMotions(this, props.actorName,
      {
        enter: simpleEnter(this, -30, 0),
        toSecondPosition: this.toSecondPosition,
        focus: this.focus,
        exit: simpleExit(this, 0, -50),
      }
    );
  }

  toSecondPosition(dur: number, tl: TimelineLite) {
    tl.to(this.container, dur, {
      left: this.props.secondLeft,
      top: this.props.secondTop,
      ease: Power2.easeInOut
    }, tl.time());
  }

  focus(dur: number, tl: TimelineLite) {
    const now = tl.time();
    tl.to(this.card, dur * 0.2, {
      borderColor: theme.warningRed
    }, now + 0.3)
      .to(this.container, dur * .2, {
        scale: 1.03,
        ease: Power1.easeInOut
      }, now + dur * 0.2)
      .to(this.container, dur * .3, {
        scale: 1,
        ease: Power1.easeInOut
      }, now + dur * 0.5);
  }

  render() {
    const { detail1, detail2, title, top, left } = this.props;
    return (
      <Root
        innerRef={ref => { if (ref) this.container = ref; }}
        top={top}
        left={left}
      >
        <IconOuter
          color={theme.cardHostPhishingIconOuter}
          innerRef={ref => { if (ref) this.icon = ref; }}
        >
          <IconCircle>
            <IconLaptop size="50px" color={theme.cardHostPhishingIcon} />
          </IconCircle>
        </IconOuter>
        <CardRect
          innerRef={ref => { if (ref) this.card = ref; }}
        >
          <Title>{title}</Title>
          <Detail1>{detail1}</Detail1>
          <Detail2><div>{detail2}</div></Detail2>
        </CardRect>
      </Root>
    );
  }
}

export default CardHostPhishing;



