import Network from '../../actors/Network';
import SidePanel from '../../actors/SidePanel';
import spacing from '../../constants/spacing';
import { n1, n2, n3, n4, n5, n6, n7, n8 } from '../networks';
import layers from '../../constants/layers';
import ImgActor from '../../actors/ImgActor';
import WorldImg from '../../assets/worldLarge.png';
import FarBgNodesImg from '../../assets/bgNodes.png';
import StorySteps from '../../actors/StorySteps';
import Verticals from '../../actors/Verticals';
import { selectionNames } from '../../constants/threatActors';
import ThreatActors from '../../actors/ThreatActors';
import { getVertical } from '../../constants/config';
import DrawTree from '../../actors/DrawTree';
import { getThreatActorLines } from '../../constants/threatActors';
import Countries from '../../actors/Countries';
import CrowdStrikeLogo from '../../assets/crowdstrike.svg';
import WorldClocks from '../../actors/WorldClocks';
import StoryTitle from '../../actors/StoryTitle';
import ClientLogo from '../../actors/ClientLogo';
import s from '../../constants/strings';
import Test from '../../actors/Test';
import theme from '../../constants/theme';

export default {
  'WelcomeTitle': {
    component: StoryTitle,
    props: {
      text: s.intro.welcomeTitle,
      style: {
        fontWeight: 300,
        left: 2130,
        top: 160,
        color: theme.textBackground,
        fontSize: 110,
      },
      exitPos: { x: 0, y: -50 }
    }
  },
  'WorldClocks': {
    component: WorldClocks,
    props: {
      left: 69,
      top: 40,
    }
  },
  'ClientLogo': {
    component: ClientLogo,
    props: {
      startOpacity: 0.9,
      zIndex: layers.foreground,
      enterPos: { x: -20, y: 0 },
      exitPos: { x: 0, y: -20 },
    }
  },
  'CrowdStrikeLogoIntro': {
    component: ImgActor,
    props: {
      left: 4120,
      top: 320,
      width: 350,
      startOpacity: 0.3,
      imgSrc: CrowdStrikeLogo,
      zIndex: layers.foreground,
      enterPos: { x: 0, y: -20 },
      exitPos: { x: 0, y: -20 },
    }
  },
  'Countries': {
    component: Countries,
    props: {
      getVertical: getVertical,
    }
  },
  'ThreatActorTree': {
    component: DrawTree,
    props: {
      color: '#D9E9F7',
      left: 688,
      getLines: () => {
        return getThreatActorLines(getVertical());
      },
      exitType: 'unDraw',
      width: 5,
      strokeLinecap: 'butt'
    }
  },
  'ThreatActors': {
    component: ThreatActors,
    props: {
      getVertical: getVertical,
    }
  },
  'Verticals': {
    component: Verticals,
    props: {
      verticals: selectionNames
    }
  },
  'StoryPhases': {
    component: StorySteps,
    props: {
      phaseNames: s.other.sideGuidePhaseNames,
      left: 4905,
      top: 500,
    }
  },
  'FarBgNetwork': {
    component: ImgActor,
    props: {
      left: 1500,
      top: -200,
      width: 4340,
      startOpacity: 0.34,
      imgSrc: FarBgNodesImg,
      zIndex: layers.background + 1,
      enterPos: { x: 0, y: -20 },
      exitPos: { x: 0, y: -20 },
    }
  },
  'WorldMap': {
    component: ImgActor,
    props: {
      left: 1200,
      top: -90,
      width: 2540,
      startOpacity: 0.65,
      imgSrc: WorldImg,
      zIndex: layers.foreground - 10,
      enterPos: { x: -50, y: 0 },
      exitPos: { x: -20, y: 0 },
    }
  },
  'network1': {
    component: Network,
    props: {
      ...n1,
      baseZLayer: layers.background
    }
  },
  'network2': {
    component: Network,
    props: {
      ...n2,
      baseZLayer: layers.background
    }
  },
  'network3': {
    component: Network,
    props: {
      ...n3,
      baseZLayer: layers.background
    }
  },
  'network4': {
    component: Network,
    props: {
      ...n4,
      baseZLayer: layers.background
    }
  },
  'network5': {
    component: Network,
    props: {
      ...n5,
      baseZLayer: layers.background
    }
  },
  'network6': {
    component: Network,
    props: {
      ...n6,
      baseZLayer: layers.focusedNetwork,
      startOpacity: 0.2,
    }
  },
  'network7': {
    component: Network,
    props: {
      ...n7,
      baseZLayer: layers.focusedNetwork,
      startOpacity: 0.2,
    }
  },
  'network8': {
    component: Network,
    props: {
      ...n8,
      baseZLayer: layers.foreground + 10,
      startOpacity: 0.18,
    }
  },
  'SidePanel': {
    component: SidePanel,
    props: {
      width: spacing.rightPanelStart,
    }
  },
  'DiscoTenPrint': {
    component: Test,
    props: {},
  }
};



