import * as React from 'react';
import styled from 'styled-components';
import director from '../director';
import { simpleExit, simpleEnter, typeText } from '../motions';
import 'gsap/TextPlugin';
import autoBind from 'auto-bind';
import layers from '../constants/layers';
import { TimelineLite, Linear } from 'gsap';
import wobbler from '../motions/wobbler';
import Svg from './Svg';

const Outer = styled.div`
  position: absolute;
  left: ${(p: { left: number, top: number, width: number }) => p.left}px;
  top: ${(p) => p.top}px;
  width: ${(p) => p.width}px;
  z-index: ${layers.foreground + 5};
`;

const Line = styled.line`
  stroke-width: 4px;
  stroke-linecap: round;
  opacity: ${(p: { opacity: number }) => p.opacity};
`;

interface Props {
  actorName: string;
  entryDuration?: number;
  top: number;
  left: number;
  width: number;
  text1: string;
  text2: string;
  style1: React.StyleHTMLAttributes<any>;
  style2: React.StyleHTMLAttributes<any>;
  typeTextEnter?: boolean;
  wobbleGroup?: number;
  wobbleScale?: number;
  line?: {
    color: string;
    x1: number;
    x2: number;
    y1: number;
    y2: number;
    visible?: boolean;
  };
}

class TextDuo extends React.Component<Props>  {
  container: Element;
  children: Element[];
  lineRef: Element;
  tl: TimelineLite;

  constructor(props: Props) {
    super(props);
    autoBind(this);
    this.children = [];
    const { typeTextEnter } = props;
    director.registerMotions(this, props.actorName,
      {
        enter: typeTextEnter
          ? this.typeEnter
          : this.enterNormal,
        exit: simpleExit(this, 20, 0),
        lineEnterNormal: this.lineEnterNormal,
        lineEnterDraw: this.lineEnterDraw,
      }
    );
  }

  enterNormal(dur: number, tl: TimelineLite) {
    simpleEnter(this, -20, 0)(dur, tl);
    this.startWobble();
  }

  typeEnter(dur: number, tl: TimelineLite) {
    const { text1, text2 } = this.props;
    const { children } = this;
    const totalChars = text1.length + text2.length;
    const duration1 = dur * (text1.length / totalChars);
    const duration2 = dur * (text2.length / totalChars);
    typeText(this, children[0], text1, 0)(duration1, tl);
    typeText(this, children[1], text2, duration1)(duration2, tl);
    this.startWobble();
  }

  startWobble() {
    const { wobbleGroup, actorName, wobbleScale } = this.props;
    if (wobbleGroup === undefined) return;
    const scale = wobbleScale === undefined ? 0.8 : wobbleScale;
    const tl = new TimelineLite();
    tl.to(this.container, 10, {
      x: '+=0',
      y: '+=0',
      modifiers: {
        x: (xpos: number) => {
          return xpos + wobbler.getPosition(wobbleGroup).x * scale;
        },
        y: (ypos: number) => {
          return ypos + wobbler.getPosition(wobbleGroup).y * scale;
        }
      }
    }, 0);
    tl.eventCallback('onComplete', () => tl.play(0), [], this);
    this.tl = tl;
    director.addActorTimeline(actorName, tl);
  }

  lineEnterDraw(dur: number, tl: TimelineLite) {
    const { lineRef } = this;
    const { line } = this.props;
    if (!line) return;
    const lineLength = Math.sqrt(Math.pow(line.x2 - line.x1, 2) + Math.pow(line.y2 - line.y1, 2));
    const now = tl.time();

    tl.set(lineRef, {
      strokeDasharray: lineLength,
      strokeDashoffset: lineLength,
      opacity: 1,
    }, now)
      .to(lineRef, dur, {
        strokeDashoffset: 0,
        ease: Linear.easeNone
      }, now);
  }

  lineEnterNormal(dur: number, tl: TimelineLite) {
    tl.to(this.lineRef, dur, {
      opacity: 1,
    }, tl.time());
  }

  render() {
    const {
      left,
      top,
      width,
      style1,
      style2,
      text1,
      text2,
      typeTextEnter,
      line,
    } = this.props;
    return (
      <Outer
        innerRef={ref => { if (ref) this.container = ref; }}
        top={top}
        left={left}
        width={width}
      >
        <div
          ref={ref => { if (ref) this.children.push(ref); }}
          style={style1}
        >
          {!typeTextEnter && text1}
        </div>
        <div
          ref={ref => { if (ref) this.children.push(ref); }}
          style={style2}
        >
          {!typeTextEnter && text2}
        </div>
        {line && (
          <Svg
            top={-300}
            left={-300}
            zIndex={layers.foreground}
            width="1500px"
            height="800px"
            innerRef={ref => { if (ref) this.container = ref; }}
          >
            <Line
              x1={line.x1}
              y1={line.y1}
              x2={line.x2}
              y2={line.y2}
              opacity={line.visible ? 1 : 0}
              stroke={line.color}
              innerRef={ref => { if (ref) this.lineRef = ref; }}
            />
          </Svg>
        )
        }
      </Outer >
    );
  }
}

export default TextDuo;



