import * as React from 'react';
import styled from 'styled-components';
import director from '../director';
import { simpleExit, staggerEnterFrom } from '../motions';
import autoBind from 'auto-bind';
import layers from '../constants/layers';
import s from '../constants/strings';
import theme from '../constants/theme';

const ops = s.operations;

const getNumberColor = (n: number) => {
  if (n > 7)
    return theme.operationsTableHigh;
  if (n > 3)
    return theme.operationsTableMedHigh;
  if (n > 2)
    return theme.operationsTableMed;
  return theme.operationsTableLow;
};

const Outer = styled.div`
  position: absolute;
  top: 160px;
  left: 2124px;
  z-index: ${layers.foreground};
`;

const Heading = styled.div`
  position: relative;
  left: -289px;
  top: -142px;
  width: 630px;
  font-size: 60px;
  font-weight: 600;
  white-space: nowrap;
  color: ${theme.operationsTableHeading};
`;

const Row = styled.div`
  position: absolute;
  top: ${(p: { top: number, left: number }) => p.top}px;
  left: ${(p: { top: number, left: number }) => p.left}px;
  display: flex;
  width: 740px;
  height: 80px;
  flex-flow: row nowrap;
  justify-content: space-between;
`;

const ColumnName = styled.div`
  position: absolute;
  top: ${(p: { top: number, left: number }) => p.top}px;
  left: ${(p: { top: number, left: number }) => p.left}px;
  font-size: 22px;
  font-weight: 600;
  width: 100px;
  text-align: center;
  white-space: nowrap;
`;

const CompanyName = styled.div`
  position: absolute;
  left: -310px;
  top: 0;
  white-space: nowrap;
  font-size: 30px;
  font-weight: 600;
  width: 1200px;
  height: 50px;
`;

const Bar = styled.div`
  width: 1200px;
  height: 75px;
  background-color: ${theme.operationsTableRow};
  position: absolute;
  border-radius: 10px;
  top: -17px;
  left: -358px;
`;

const NumberItem = styled.div`
  font-size: 33px;
  font-weight: 600;
  color: ${(p: { num: number }) => getNumberColor(p.num)};
`;

interface Props {
  actorName: string;
  entryDuration?: number;
}

class OperationsTable extends React.Component<Props>  {
  container: Element;
  card: Element;
  children: Element[];

  constructor(props: Props) {
    super(props);
    autoBind(this);
    this.children = [];

    director.registerMotions(this, props.actorName,
      {
        enter: staggerEnterFrom(this, '+=0', '+=20', 0.15),
        exit: simpleExit(this),
      }
    );
  }

  addChildRef(ref: Element) {
    if (ref)
      this.children.push(ref);
  }

  render() {
    return (
      <Outer
        innerRef={ref => { if (ref) this.container = ref; }}
      >
        <Heading innerRef={this.addChildRef}>{ops.titles[1]}</Heading>
        <ColumnName top={-50} left={-300} innerRef={this.addChildRef}>{'Company Name'}</ColumnName>
        {ops.DevicePerformanceColumnNames.map((name, i) => (
          <ColumnName top={-50} left={145 * i - 46} key={i} innerRef={this.addChildRef}>{name}</ColumnName>
        ))}
        {ops.DevicePerformanceTable.map((row, i) => (
          <Row top={75 * i} left={0} key={i}>
            {i % 2 === 0 && <Bar innerRef={this.addChildRef} />}
            <CompanyName innerRef={this.addChildRef}>
              {row.name}
            </CompanyName>
            {row.data.map((val, j) => (
              <NumberItem innerRef={this.addChildRef} key={j} num={j === 5 ? 0 : val}>{val}</NumberItem>
            ))}
          </Row>
        ))}
      </Outer>
    );
  }
}

export default OperationsTable;



