import Color from 'color';
import spacing from '../constants/spacing';
import { Coord2D } from './positioning';
import { Action } from '../typings';

export interface Coord3D {
  x: number;
  y: number;
  z: number;
}

export interface NetworkNode {
  x: number;
  y: number;
  z: number;
  xTrans: number;
  yTrans: number;
  zTrans: number;
  curRotDegrees: number;
  xRotated: number;
  yRotated: number;
  zRotated: number;
  opacity: number;
  size: number;
  screenX: number;
  screenY: number;
  screenZ: number;
  color: string;
  ref?: Element;
}

export interface NetworkEdge {
  source: NetworkNode;
  destination: NetworkNode;
  ref?: Element;
  color: string;
  opacity: number;
  strokeWidth: number;
  z: number;
}

export interface SizedNode {
  x: number;
  y: number;
  z: number;
  size: number;
  color?: string;
  opacity?: number;
}

type Axis = 'x' | 'y' | 'z' | string;
export interface NetworkDef {
  nodes: SizedNode[];
  edges: { src: number, dest: number, color?: string, opacity?: number }[];
  left: number;
  center?: Coord3D;
  rotation?: {
    center: Coord3D;
    axis: Axis;
    duration: number;
  };
}

export const logNetwork = (n: any) => {
  console.log(`{
    left: ${n.left},
    rotation: {
      center: {x: ${n.rotation.center.x},y: ${n.rotation.center.y}, z: ${n.rotation.center.z} },
      axis: '${n.rotation.axis}',
      duration: ${n.rotation.duration}
    },
    nodes: [
      ${n.nodes.map(node => `{x: ${node.x}, y: ${node.y}, z: ${node.z}, size: ${node.size}}
      `)}
    ],
    edges: [
      ${n.edges.map(ed => `{src: ${ed.src}, dest: ${ed.dest}}
      `)}
    ],
  }`);
};

const _radMultiplier = Math.PI / 180; // super rad

export const areaCenter = { x: 2000, y: 500 };
export const nodeColor = '#808085';
export const edgeColor = '#85858A';
const fadedColor = Color('#707070');
// const fadedColor = Color('#7D7D7D');
export const nodeSize = 50;

export const colorBalance = 0.85;
export const sizeBalance = 0.6;
export const spaceBalance = 0.6;

export const strokeWidth = 10;
export const fieldZDepth = 4000;

export const getNearness = (z: number, balance: number = 0.5) => {
  const inverse = 1 - balance;
  const quadratic = Math.pow(z / fieldZDepth, 3);
  return quadratic * balance + inverse;
};

export const get2DPositionFromAngle = (from: Coord2D, deg: number, length: number): Coord2D => {
  const rad = deg * _radMultiplier;
  return {
    x: from.x + Math.cos(rad) * length,
    y: from.y + Math.sin(rad) * length,
  };
};

export const fadeColorByDepth = (col: Color | string, zDepth: number, balance: number = 0.5) => {
  if (typeof col === 'string') {
    col = Color(col);
  }
  const amt = (1 - getNearness(zDepth, balance));
  return col.mix(fadedColor, amt)
    .lighten(amt).toString();
};

export const getRotatedCoords = (rotMatrix: number[], node: NetworkNode, rotOrigin?: Coord3D) => {
  // rotate them around the given rotation origin
  if (!rotOrigin)
    return {
      x: node.x,
      y: node.y,
      z: node.z,
    };
  const xa = node.x - rotOrigin.x;
  const ya = node.y - rotOrigin.y;
  const za = node.z - rotOrigin.z;
  const x = (rotMatrix[0] * xa + rotMatrix[1] * ya + rotMatrix[2] * za) + rotOrigin.x;
  const y = (rotMatrix[3] * xa + rotMatrix[4] * ya + rotMatrix[5] * za) + rotOrigin.y;
  const z = (rotMatrix[6] * xa + rotMatrix[7] * ya + rotMatrix[8] * za) + rotOrigin.z;
  return { x, y, z };
};

export const setScreenCoords = (
  node: NetworkNode,
  rotation: {
    degrees: number,
    origin: Coord3D,
    matrix: number[],
  },
  wobblePos: { x: number, y: number }
) => {
  // don't do the rotation calculation if we don't have to

  if (node.curRotDegrees === rotation.degrees) {
    // stretch x and y values in or out depending on z
    // first translate previously rotated values
    const xTrans = node.xRotated + node.xTrans;
    const yTrans = node.yRotated + node.yTrans;
    const zTrans = node.zRotated + node.zTrans;
    const factor = getNearness(zTrans, spaceBalance);

    const xFromCenter = xTrans - areaCenter.x;
    const yFromCenter = yTrans - areaCenter.y;
    node.screenX = wobblePos
      ? (xFromCenter + wobblePos.x) * factor + areaCenter.x
      : xFromCenter * factor + areaCenter.x;
    node.screenY = wobblePos
      ? (yFromCenter + wobblePos.y) * factor + areaCenter.y
      : yFromCenter * factor + areaCenter.y;
    node.screenZ = zTrans;

    return;
  } else {
    const { matrix, origin, degrees } = rotation;
    node.curRotDegrees = degrees;
    // rotate base coords
    const { x, y, z } = getRotatedCoords(matrix, node, origin);
    node.xRotated = x;
    node.yRotated = y;
    node.zRotated = z;

    // stretch x and y values in or out depending on z
    const factor = getNearness(z + node.zTrans, spaceBalance);

    const xFromCenter = x + node.xTrans - areaCenter.x;
    const yFromCenter = y + node.yTrans - areaCenter.y;
    node.screenX = wobblePos
      ? (xFromCenter + wobblePos.x) * factor + areaCenter.x
      : xFromCenter * factor + areaCenter.x;
    node.screenY = wobblePos
      ? (yFromCenter + wobblePos.y) * factor + areaCenter.y
      : yFromCenter * factor + areaCenter.y;
    node.screenZ = z + node.zTrans;
  }
};

export const rotMatrixY = (degs: number) => {
  const rads = degs * _radMultiplier;
  const sin = Math.sin(rads);
  const cos = Math.cos(rads);

  return [
    cos, 0, sin,
    0, 1, 0,
    -sin, 0, cos,
  ];
};

export const rotMatrixZ = (degs: number) => {
  const rads = degs * _radMultiplier;
  const sin = Math.sin(rads);
  const cos = Math.cos(rads);

  return [
    cos, -sin, 0,
    sin, cos, 0,
    0, 0, 1,
  ];
};

export const rotMatrixX = (degs: number) => {
  const rads = degs * _radMultiplier;
  const sin = Math.sin(rads);
  const cos = Math.cos(rads);

  return [
    1, 0, 0,
    0, cos, -sin,
    0, sin, cos,
  ];
};

// merge nodes and edges of networks into one network
export const mergeNetworks = (networks: NetworkDef[]) => {
  const merged = {
    nodes: [],
    edges: [],
    rotation: networks[0].rotation,
    left: networks[0].left
  };
  let addEdgeIndexes = 0;

  networks.forEach(n => {
    merged.nodes = merged.nodes.concat(n.nodes as any);
    merged.edges = merged.edges.concat(
      (n.edges as any).map(edge => ({
        src: edge.src + addEdgeIndexes,
        dest: edge.dest + addEdgeIndexes
      }))
    );
    addEdgeIndexes += n.nodes.length;
  });
  logNetwork(merged);
  return merged;
};

// get 3d distance
export const getDist = (c1: SizedNode, c2: SizedNode) =>
  Math.sqrt(Math.pow(c1.x - c2.x, 2) + Math.pow(c1.y - c2.y, 2) + Math.pow(c1.z - c2.z, 2));

// stretch node from an origin in whatever directions
export const stretchNode = (factors: Coord3D, coord: SizedNode, center: Coord3D) => {
  return {
    ...coord,
    x: Math.round(((coord.x - center.x) * factors.x) + center.x),
    y: Math.round(((coord.y - center.y) * factors.y) + center.y),
    z: Math.round(((coord.z - center.z) * factors.z) + center.z),
  };
};

// translateNetwork around
export const translateNetwork = (n: NetworkDef, t: Coord3D, stretchFactors: Coord3D) => {
  const center = {
    x: n.rotation ? n.rotation.center.x + t.x : n.center ? n.center.x : spacing.fullWidth / 2,
    y: n.rotation ? n.rotation.center.y + t.y : n.center ? n.center.y : spacing.fullHeight / 2,
    z: n.rotation ? n.rotation.center.z + t.z : n.center ? n.center.z : 2000,
  };
  const newNet = {
    ...n,
    nodes: n.nodes.map(node => (stretchNode(stretchFactors, {
      ...node,
      x: node.x + t.x,
      y: node.y + t.y,
      z: node.z + t.z,
    }, center))),
  };
  if (n.rotation) {
    newNet.rotation = {
      ...n.rotation,
      center: center,
    };
  }
  return newNet;
};

// remove 'holes' in network node list and renumber the edges
// also, remove edges that go nowhere now
export const removeNetworkHoles = (network: any): NetworkDef => {
  let holeFound = true;
  let net = network;
  const numTries = network.nodes.length;
  let curTries = 0;
  while (holeFound && curTries < numTries) {
    curTries++;
    holeFound = false;
    let holeIdx = -1;
    // go until we find a hole
    for (let i = 0; i < net.nodes.length; i++) {
      if (net.nodes[i] === undefined) {
        holeIdx = i;
        holeFound = true;
        break;
      }
    }
    // if a hole was found, recreate network (filter out node and renumber edges)
    if (holeFound) {
      net = {
        ...net,
        nodes: net.nodes
          .filter((node, n) => n !== holeIdx),
        edges: net.edges
          .filter((edge) => edge.src !== holeIdx && edge.dest !== holeIdx)
          .map((edge, n) => {
            const src = edge.src < holeIdx ? edge.src : edge.src - 1;
            const dest = edge.dest < holeIdx ? edge.dest : edge.dest - 1;
            return { src, dest };
          })
      };
    }
  }
  return net;
};

// add a cluster of small red nodes around a node in a given direction
export function addCluster(fromIdx: number, angle: number, degreesBetween: number, network: NetworkDef): NetworkDef {
  const center = network.nodes[fromIdx];
  if (!center) return network;
  for (let i = 0; i < 5; i++) {
    const { x, y } = get2DPositionFromAngle({ x: center.x, y: center.y }, angle + i * degreesBetween, 85);
    const node: SizedNode = {
      ...center,
      x: Math.round(x),
      y: Math.round(y),
      size: 0.1,
      color: '#FE0E2B',
      opacity: 0
    };
    const nodeIdx = network.nodes.length;
    // add new node and line connecting it to the center node
    network.nodes.push(node);
    network.edges.push({
      src: fromIdx,
      dest: nodeIdx,
      color: '#FE0E2B',
      opacity: 0
    });
  }
  return network;
}

export function addNodeFrom(fromIdx: number, angle: number, size: number, color: string, opacity: number, length: number, network: NetworkDef): NetworkDef {
  const center = network.nodes[fromIdx];
  if (!center) return network;
  const { x, y } = get2DPositionFromAngle({ x: center.x, y: center.y }, angle, length);
  const node: SizedNode = {
    ...center,
    x: Math.round(x),
    y: Math.round(y),
    size, color, opacity,
  };
  const nodeIdx = network.nodes.length;
  // add new node and line connecting it to the center node
  network.nodes.push(node);
  network.edges.push({
    src: fromIdx,
    dest: nodeIdx,
    color,
    opacity,
  });
  return network;

}

// get indexes for edges along a path of nodes, given by index
export function getPathEdges(nodeIndexes: number[], network: NetworkDef) {
  const edgeIdxs: number[] = [];

  for (let i = 0; i < nodeIndexes.length - 1; i++) {
    const n1 = nodeIndexes[i];
    const n2 = nodeIndexes[i + 1];
    const edgeIdx = network.edges.findIndex((e, n) =>
      (e.src === n1 && e.dest === n2)
      || (e.src === n2 && e.dest === n1));

    if (edgeIdx >= 0) {
      edgeIdxs.push(edgeIdx);
    }
  }
  return edgeIdxs;
}

export function getPathDrawActions(nodeIndexes: number[], infectedNodes: number[], network: NetworkDef, actorName: string, nodeChangeDur: number): Action[] {
  const actions: Action[] = [];
  actions.push({
    type: 'motion',
    payload: {
      actor: actorName,
      motion: infectedNodes.includes(nodeIndexes[0]) ? 'toInfected' : 'toRed',
      targets: [nodeIndexes[0]],
      duration: nodeChangeDur,
    }
  });
  for (let i = 0; i < nodeIndexes.length - 1; i++) {
    const n1 = nodeIndexes[i];
    const n2 = nodeIndexes[i + 1];
    const edgeIdx = network.edges.findIndex((e, n) =>
      (e.src === n1 && e.dest === n2)
      || (e.src === n2 && e.dest === n1));
    if (edgeIdx >= 0) {
      actions.push({
        type: 'motion',
        payload: {
          actor: actorName,
          motion: 'toRedEdge',
          targets: [edgeIdx],
          duration: nodeChangeDur,
        }
      });
    }
    actions.push({
      type: 'motion',
      payload: {
        actor: actorName,
        motion: infectedNodes.includes(nodeIndexes[i + 1]) ? 'toInfected' : 'toRed',
        targets: [nodeIndexes[i + 1]],
        duration: nodeChangeDur,
      }
    });
  }
  return actions;
}


