
const getUrlParams = () => {
  const vars: any = {};
  window.location.href.replace(/[?&]+([^=&]+)=([^&]*)/gi,
    (m: string, key: string, value: string) => {
      vars[key] = value;
      return '';
    });
  return vars;
};

export default getUrlParams();


