import SyncTestItem from '../../actors/SyncTestItem';
import s from '../../constants/spacing';

export default {
  'SyncItem0': {
    component: SyncTestItem,
    props: {
      left: (s.fullWidth - 6000) / 2,
      top: (s.fullHeight - 5000) / 2,
      width: 6000,
      height: 5000,
      color: '#34AA2F'
    }
  },
  'SyncItem1': {
    component: SyncTestItem,
    props: {
      left: -500,
      top: -1 * s.fullHeight,
      width: s.fullWidth + 2000,
      height: s.fullHeight * 3.5,
      color: '#333',
      bgColor: '#000'
    }
  },
};



