import * as React from 'react';
import styled from 'styled-components';
import director from '../director';
import { TimelineLite, Power2 } from 'gsap';
import autoBind from 'auto-bind';
import { simpleExit, simpleEnter } from '../motions';
import { clientSetupConfig } from '../utils/clientSetup';

const _nItemsPerRow = 11;
const _arr = new Array(Math.floor((_nItemsPerRow * _nItemsPerRow) / 2)).fill(0);

const Outer = styled.div`
  position: absolute;
  left: ${(p: { left: number, top: number, width: number, height: number, color: string, bgColor?: string }) => p.left}px;
  top: ${p => p.top}px;
  width: ${p => p.width}px;
  height: ${p => p.height}px;
  display: flex;
  flex-flow: row wrap;
  color: ${p => p.color};
  overflow: visible;
  border-radius: 0;
  ${p => p.bgColor !== undefined ? `background-color: ${p.bgColor};` : ''}
`;

const Block = styled.div`
  width: ${Math.floor(100 / _nItemsPerRow)}%;
  height: ${Math.floor(100 / _nItemsPerRow)}%;
  background: ${(p: { color?: string }) => p.color === undefined ? 'currentColor' : p.color};
  border-radius: inherit;
`;

export interface Props {
  actorName: string;
  entryDuration?: number;
  left: number;
  top: number;
  width: number;
  height: number;
  color: string;
  bgColor?: string;
}

class ClientLogo extends React.Component<Props>  {
  container: Element;
  children: Element[];
  tl: TimelineLite;

  constructor(props: Props) {
    super(props);
    autoBind(this);

    director.registerMotions(this, props.actorName,
      {
        enter: simpleEnter(this, '+=300', '+=100'),
        transition: this.transition,
        exit: simpleExit(this, `-=300`, `+=100`),
      }
    );
    const { offsetLeft, offsetTop, width, src } = clientSetupConfig;
    this.state = {
      offsetLeft, offsetTop, width, src
    };
  }

  transition(dur: number, tl: TimelineLite, payload: any) {
    const { vars } = payload;
    const now = tl.time();
    tl.to(this.container, dur, {
      ease: Power2.easeInOut,
      ...vars,
    }, now);
  }

  addChildRef(ref: Element) {
    if (ref)
      this.children.push(ref);
  }

  render() {
    const { top, left, width, height, color, bgColor } = this.props;
    console.log('rendering synctest');
    return (
      <Outer
        left={left}
        top={top}
        width={width}
        height={height}
        color={color}
        bgColor={bgColor}
        innerRef={ref => { if (ref) this.container = ref; }}
      >
        {_arr.map(() => (
          <React.Fragment>
            <Block />
            <Block color={'rgba(0, 0, 0, 0)'} />
          </React.Fragment>
        ))}
      </Outer>
    );
  }
}

export default ClientLogo;



