export function toOpacity(actor: ActorComponent, opacity: number) {
  return (function (dur: number, tl: TimelineLite) {
    tl.to(actor.container, dur, {
      opacity,
      ease: Power1.easeInOut,
    }, tl.time());
  }).bind(actor);
}

export function swoop(actor: ActorComponent) {
  return (function (dur: number, tl: TimelineLite) {
    tl.fromTo(actor.container, dur, { y: -800 }, { y: 100 }, tl.time());
  }).bind(actor);
}

export function spinAndShrink(actor: ActorComponent) {
  return (function (dur: number, tl: TimelineLite) {
    tl.to(actor.container, dur, {
      rotation: 600,
      scale: 0,
      x: 500,
      ease: Power3.easeOut
    }, tl.time());
  }).bind(actor);
}

export function staggerEnterFrom(actor: ActorComponent, x: number | string = 50, y: number | string = 0, durScalar: number = 0.5) {
  return (function (dur: number, tl: TimelineLite) {
    const now = tl.time();
    tl.staggerFrom(actor.children, dur * durScalar, {
      x, y,
      opacity: 0,
      ease: Power1.easeOut
    }, (dur / actor.children.length) / 2,
      now);
  }).bind(actor);
}

export function staggerExitTo(actor: ActorComponent, x: number | string = 50, y: number | string = 0, durScalar: number = 0.5) {
  return (function (dur: number, tl: TimelineLite) {
    const now = tl.time();
    tl.staggerTo(actor.children, dur * durScalar, {
      x, y,
      opacity: 0,
      ease: Power1.easeIn
    }, (dur / actor.children.length) / 2,
      now);
  }).bind(actor);
}

export function moveTo(actor: ActorComponent, top: number | string, left: number | string, ease: Ease = Power2.easeInOut) {
  return (function (dur: number, tl: TimelineLite) {
    const now = tl.time();
    tl.to(actor.container, dur, {
      top,
      left,
      ease
    }, now);
  }).bind(actor);
}

export function moveBy(actor: ActorComponent, x: number | string, y: number | string, ease: Ease = Power2.easeInOut) {
  return (function (dur: number, tl: TimelineLite) {
    const now = tl.time();
    tl.to(actor.container, dur, {
      x,
      y,
      ease
    }, now);
  }).bind(actor);
}

export function growIn(actor: ActorComponent, scale: number = 0.5) {
  return (function (dur: number, tl: TimelineLite) {
    const now = tl.time();
    tl.from(actor.container, dur * .8, {
      scale,
      opacity: 0,
      ease: Power2.easeOut
    }, now + dur * .2);
  }).bind(actor);
}

export function simpleEnter(actor: ActorComponent, x: number | string = -50, y: number | string = 0) {
  return (function (dur: number, tl: TimelineLite) {
    const now = tl.time();
    tl.from(actor.container, dur * .8, {
      x,
      y,
      opacity: 0,
      ease: Power2.easeOut,
    }, now + dur * .2);
  }).bind(actor);
}

export function simpleExit(actor: ActorComponent, x: number | string = 50, y: number | string = 0) {
  return (function (dur: number, tl: TimelineLite) {
    const now = tl.time();
    tl.to(actor.container, dur * .8, {
      x,
      y,
      opacity: 0,
      ease: Power2.easeIn
    }, now + dur * .2);
  }).bind(actor);
}

export function typeText(actor: ActorComponent, textContainer: Element, text: string, delay: number = 0) {
  return (function (dur: number, tl: TimelineLite) {
    const start = tl.time() + delay;

    tl.to(textContainer, dur, {
      text,
      ease: Linear.easeNone,
    }, start);
  }).bind(actor);
}


