import * as React from 'react';
import styled from 'styled-components';
import director from '../director';
import autoBind from 'auto-bind';
import layers from '../constants/layers';
import { simpleExit } from '../motions';
import { TimelineLite, Power2, Power1 } from 'gsap';
import Svg from './Svg';
import theme from '../constants/theme';

const _width = 500;
const _height = 500;
const _bottomSpace = 50;
const _phasesArea = _height - _bottomSpace;
const _expandedScale = 1.2;

const Root = styled.div`
  position: absolute;
  left: ${(p: { top: number, left: number }) => p.left}px;
  top: ${(p) => p.top}px;
  width: ${_width}px;
  height: ${_height}px;
  z-index: ${layers.foreground};
`;

const Phase = styled.div`
  position: absolute;
  left: ${(p: { top: number, left: number }) => p.left}px;
  top: ${(p) => p.top}px;
  height: 100px;
  white-space: nowrap;
  transform-origin: left center;
`;

const PhaseNumber = styled.div`
  position: absolute;
  left: 0;  
  top: 20;
  font-size: 35px;
  color: ${theme.storyStepsPrimary};
`;

const PhaseName = styled.div`
  position: absolute;
  left: 0;
  top: 40px;
  font-size: 50px;
`;

interface Props {
  actorName: string;
  entryDuration?: number;
  top: number;
  left: number;
  phaseNames: string[];
}

class StoryStep extends React.Component<Props>  {
  container: Element;
  dot: Element;
  svgRef: Element;
  children: Element[];
  currentPhase: number;

  constructor(props: Props) {
    super(props);
    autoBind(this);
    director.registerMotions(this, props.actorName,
      {
        enter: this.enter,
        nextPhase: this.nextPhase,
        skipPhase: this.skipPhase,
        exit: simpleExit(this, 0, -50),
      }
    );
    this.currentPhase = 0;
    this.children = [];
  }

  enter(dur: number, tl: TimelineLite) {
    const now = tl.time();
    const childrenCopy = this.children.slice();
    childrenCopy.reverse();

    if (this.children[0])
      tl.set(this.children[0], {
        scale: _expandedScale,
      }, now);
    tl.from(this.svgRef, dur * 0.3, {
      y: _height,
      ease: Power1.easeOut,
    }, now);
    tl.staggerFrom(childrenCopy, dur * 0.2, {
      x: -15,
      opacity: 0,
      ease: Power1.easeOut,
    }, dur * 0.07, now + dur * 0.06);
  }

  nextPhase(dur: number, tl: TimelineLite) {
    const now = tl.time();
    const total = this.props.phaseNames.length;

    tl.to(this.children[this.currentPhase], dur, {
      scale: 1,
      ease: Power2.easeInOut,
    }, now)
      .to(this.children[this.currentPhase + 1], dur, {
        scale: _expandedScale,
        ease: Power2.easeInOut,
      }, now)
      .to(this.dot, dur, {
        y: `+=${_phasesArea / total}`,
        ease: Power2.easeInOut,
      }, now);

    this.currentPhase++;
  }

  skipPhase(dur: number, tl: TimelineLite) {
    const now = tl.time();
    const total = this.props.phaseNames.length;

    tl.to(this.children[this.currentPhase], dur, {
      scale: 1,
      ease: Power2.easeInOut,
    }, now)
      .to(this.children[this.currentPhase + 2], dur, {
        scale: _expandedScale,
        ease: Power2.easeInOut,
      }, now)
      .to(this.dot, dur, {
        y: `+=${(_phasesArea / total) * 2}`,
        ease: Power2.easeInOut,
      }, now);

    this.currentPhase++;
  }

  addChildRef(ref: Element) {
    if (ref) {
      this.children.push(ref);
    }
  }

  render() {
    const { top, left, phaseNames } = this.props;
    const total = phaseNames.length;
    return (
      <Root
        innerRef={ref => { if (ref) this.container = ref; }}
        top={top}
        left={left}
      >
        {phaseNames.map((pn, i) => (
          <Phase
            key={i}
            top={(i / total) * _phasesArea}
            left={20}
            innerRef={this.addChildRef}
          >
            <PhaseNumber>{`Stage ${i + 1}`}</PhaseNumber>
            <PhaseName>{pn}</PhaseName>
          </Phase>
        ))}
        <Svg
          height="800px"
          width="100px"
          left={-80}
          top={5}
          zIndex={layers.foreground}
          innerRef={r => { if (r) this.svgRef = r; }}
        >
          <line
            stroke={theme.storyStepsPrimary}
            strokeWidth="5px"
            x1="40"
            y1="40"
            x2="40"
            y2="700"
          />
          <circle
            cx="40"
            cy="40"
            r="23"
            fill={theme.storyStepsPrimary}
            stroke={theme.storyStepsPrimary}
            ref={r => { if (r) this.dot = r; }}
          />
        </Svg>
      </Root>
    );
  }
}

export default StoryStep;



