interface ThreatActorImg {
  img: string;
  height: number;
  top: number;
  left: number;
}
const _top = -15;
const _left = -70;
export const getThreatActorImg = (taName: string): ThreatActorImg => {
  if (taName.includes('Tiger')) {
    return {
      img: Tiger,
      height: 152,
      top: _top,
      left: _left,
    };
  }
  if (taName.includes('Bear')) {
    return {
      img: Bear,
      height: 150,
      top: _top,
      left: _left,
    };
  }
  if (taName.includes('Bat')) {
    return {
      img: Bat,
      height: 163,
      top: _top,
      left: _left,
    };
  }
  if (taName.includes('Jackal')) {
    return {
      img: Jackal,
      height: 170,
      top: _top,
      left: _left,
    };
  }
  if (taName.includes('Spider')) {
    return {
      img: Spider,
      height: 145,
      top: _top,
      left: _left,
    };
  }
  if (taName.includes('Panda')) {
    return {
      img: Panda,
      height: 157,
      top: _top,
      left: _left,
    };
  }
  if (taName.includes('Kitten')) {
    return {
      img: Kitten,
      height: 165,
      top: _top,
      left: _left,
    };
  }
  if (taName.includes('Leopard')) {
    return {
      img: Leopard,
      height: 165,
      top: _top,
      left: _left,
    };
  }
  return {
    img: Chollima,
    height: 158,
    top: _top,
    left: _left,
  };
};

