import * as React from 'react';
import styled from 'styled-components';
import director from '../director';
import { TimelineLite, Power2 } from 'gsap';
import autoBind from 'auto-bind';
import WYLogo from '../assets/weylanYutani.svg';
import { simpleExit, simpleEnter } from '../motions';
import { clientSetupConfig } from '../utils/clientSetup';

const Outer = styled.div`
  position: absolute;
  left: ${(p: { left: number, top: number, startOpacity: number, zIndex: number }) => p.left + 2510}px;
  top: ${p => p.top + 420}px;
  z-index: ${p => p.zIndex};
  opacity: ${p => p.startOpacity};
`;

export interface ClientLogoProps {
  actorName: string;
  entryDuration?: number;
  startOpacity: number;
  zIndex: number;
  enterPos: { x: number, y: number };
  exitPos: { x: number, y: number };
}

interface State {
  offsetLeft: number;
  offsetTop: number;
  width: number;
  src: any;
}

class ClientLogo extends React.Component<ClientLogoProps, State>  {
  container: Element;
  children: Element[];
  tl: TimelineLite;

  constructor(props: ClientLogoProps) {
    super(props);
    autoBind(this);

    director.registerMotions(this, props.actorName,
      {
        enter: this.enter,
        transition: this.transition,
        exit: simpleExit(this, `+=${props.exitPos.x}`, `+=${props.exitPos.y}`),
      }
    );
    const { offsetLeft, offsetTop, width, src } = clientSetupConfig;
    this.state = {
      offsetLeft, offsetTop, width, src
    };
  }

  enter(dur: number, tl: TimelineLite) {
    simpleEnter(this, `+=${this.props.enterPos.x}`, `+=${this.props.enterPos.y}`)(dur, tl);
    this.startImgUpdates();
  }
  transition(dur: number, tl: TimelineLite, payload: any) {
    const { vars } = payload;
    const now = tl.time();
    tl.to(this.container, dur, {
      ...vars,
      ease: Power2.easeInOut,
    }, now);
  }

  addChildRef(ref: Element) {
    if (ref)
      this.children.push(ref);
  }

  startImgUpdates() {
    const tl = new TimelineLite();
    tl.call(this.imgUpdateRAF, [], this, 3);
    tl.eventCallback('onComplete', tl.restart, [], tl);
    this.tl = tl;
  }

  imgUpdate() {
    const { offsetLeft, offsetTop, width, src } = clientSetupConfig;
    if (offsetLeft !== this.state.offsetLeft
      || offsetTop !== this.state.offsetTop
      || width !== this.state.width
      || src !== this.state.src) {
      this.setState({
        offsetLeft, offsetTop, width, src
      });
    }
  }

  imgUpdateRAF() {
    requestAnimationFrame(this.imgUpdate);
  }

  render() {
    const { startOpacity, zIndex } = this.props;
    const { offsetLeft, offsetTop, width, src } = this.state;
    return (
      <Outer
        left={offsetLeft}
        top={offsetTop}
        zIndex={zIndex}
        startOpacity={startOpacity}
        innerRef={ref => { if (ref) this.container = ref; }}
      >
        <img style={{ width: `${width}px` }} src={src || WYLogo} />
      </Outer>
    );
  }
}

export default ClientLogo;



