import * as React from 'react';
import styled from 'styled-components';
import director from '../director';
import autoBind from 'auto-bind';
import layers from '../constants/layers';
import { staggerEnterFrom, staggerExitTo } from '../motions';
import t from '../constants/theme';

const Outer = styled.div`
  width: 400px;
  position: absolute;
  left: ${(p: { left: number, top: number, height: number, width: number, alignLeft?: boolean }) => p.left}px;
  top: ${p => p.top}px;
  height: ${ p => p.height}px;
  width: ${ p => p.width}px;
  display: flex;
  flex-flow: column wrap;
  align-items: ${p => p.alignLeft ? 'flex-start' : 'center'};
  align-content: space-between;
  z-index: ${layers.foreground + 30};
`;

const Title = styled.div`
  position: absolute;
  left: 330px;
  top: -221px;
  font-size: 121px;
  font-weight: 200;
  opacity: 0.8;
  color: ${t.titlesOverPanel};
  white-space: nowrap;
`;

interface Props {
  actorName: string;
  entryDuration: number;
  title?: string;
  items: string[];
  left: number;
  top: number;
  itemStyle: any;
  height: number;
  width: number;
  alignLeft?: boolean;
}

class BulletList extends React.Component<Props>  {
  container: Element;
  children: Element[];
  constructor(props: Props) {
    super(props);
    this.children = [];
    autoBind(this);
    director.registerMotions(this, props.actorName,
      {
        enter: staggerEnterFrom(this, 0, 20, .1),
        exit: staggerExitTo(this, 0, -20, .1),
      }
    );
  }

  addChildRef(ref: HTMLDivElement) {
    if (ref)
      this.children.push(ref);
  }

  render() {
    let defaultItemStyle = {
      margin: 20,
      fontSize: 100,
      fontWeight: 500,
      color: t.titlesOverPanel,
      whiteSpace: 'nowrap',
    };
    const { top, left, title, items, itemStyle, height, width, alignLeft } = this.props;
    return (
      <Outer
        top={top}
        left={left}
        height={height}
        width={width}
        alignLeft={alignLeft}
      >
        {title && <Title innerRef={this.addChildRef}>{title}</Title>}
        {items.map((itemString, i) => (
          <div
            key={i}
            style={{ ...defaultItemStyle, ...itemStyle }}
            ref={this.addChildRef}
          >
            {itemString}
          </div>
        ))
        }
      </Outer>
    );
  }
}

export default BulletList;



