import io from 'socket.io-client';
import g from './constants/config';
import controller, { setPlayBtn, setSpeed } from './director/controller';
import director from './director';
import { syncFromGroup } from './utils/clientSetup';
import { addControllerElements } from './utils/addControllerElements';

const ignoreAdjustmentsNearLoopPoint = 5;

export const getApiPort = async () => {
  const response = await fetch('/api/port');
  const body = await response.json();

  if (response.status !== 200) throw Error(body.message);
  console.log(body.port ? 'process port: ' + body.port : 'no Port in API response!');
  return body;
};

function connectSocket() {
  if (g.socket) {
    g.socket = undefined;
    delete g.socket;
  }
  const socket = io({
    reconnection: true,
    reconnectionDelay: 1000,
    reconnectionDelayMax: 5000,
    reconnectionAttempts: Infinity
  });
  socket.on('connect', function () {
    console.log('socket reconnected');
    socket.send({
      type: 'instanceData',
      isController: g.isController,
      controlGroup: g.controlGroup,
    });
  });
  socket.on('disconnect', function () {
    console.log('socket disconnected');
  });
  socket.on('play', () => {
    controller.play();
  });
  socket.on('pause', controller.pause);
  socket.on('setSpeed', (m) => {
    setSpeed(m.speed);
  });
  socket.on('restart', () => {
    controller.restart();
    setPlayBtn(controller);
  });
  socket.on('clientConfigChange', (clientConfig) => {
    syncFromGroup(clientConfig);
  });
  socket.on('sync', (m) => {
    const elapsed = director.tl.time() + g.timeOffset;
    socket.send({ type: 'syncResponse', elapsed });
  });
  socket.on('adjust', (m) => {
    if (director.tl.paused()) return console.log('Paused, no adjustments applied');

    const untilEnd = director.tl.duration() - director.tl.time();
    const sinceBeginning = director.tl.time();

    if ((untilEnd < ignoreAdjustmentsNearLoopPoint) || (sinceBeginning < ignoreAdjustmentsNearLoopPoint)) {
      return console.log('Too close to loop point, no adjustments applied');
    }
    const seconds = m.seconds;
    // controller.pauseFor(seconds);
    if (seconds > 0) {
      console.log('jumping ahead ' + seconds.toFixed(5) + ' s');
      director.tl.seek(director.tl.time() + seconds, false);
    } else if (seconds < 0) {
      console.log('pausing for ' + Math.round(seconds * 1000) + ' ms');
      controller.pauseFor(Math.abs(seconds));
    }
  });
  g.socket = socket;
}

export const startSocket = () => {
  connectSocket();
};

export const startControllerSocket = () => {
  const cSocket = io({
    reconnection: true,
    reconnectionDelay: 2000,
    reconnectionDelayMax: 6000,
    reconnectionAttempts: Infinity
  });
  cSocket.send({
    type: 'instanceData',
    isController: g.isController,
    controlGroup: g.controlGroup,
  });

  const { play, restart, slower, faster, setup, displaySpeed } = addControllerElements();
  const wallSetupContainer = document.querySelector('#wallSetup') as HTMLInputElement;

  play.addEventListener('click', () => {
    const isPlaying = play.innerHTML === 'PLAY';
    if (isPlaying) {
      cSocket.send({ type: 'play' });
    } else {
      cSocket.send({ type: 'pause' });
    }
    play.innerHTML = isPlaying ? 'PAUSE' : 'PLAY';
  });

  restart.addEventListener('click', () => {
    cSocket.send({ type: 'restart' });
    play.innerHTML = 'PAUSE';
  });

  slower.addEventListener('click', () => {
    cSocket.send({ type: 'controlSpeedSlower' });
  });

  faster.addEventListener('click', () => {
    cSocket.send({ type: 'controlSpeedFaster' });
  });

  setup.addEventListener('click', () => {
    const isVis = setup.innerHTML === 'EXIT SETUP';
    if (isVis) {
      wallSetupContainer.style.visibility = 'hidden';
      wallSetupContainer.style.maxHeight = '0';
    } else {
      wallSetupContainer.style.visibility = 'visible';
      wallSetupContainer.style.maxHeight = '1000px';
    }
    setup.innerHTML = isVis ? 'SETUP' : 'EXIT SETUP';
  });

  cSocket.on('displaySpeed', (msg) => {
    displaySpeed.innerHTML = `current time scale: ${msg.speed}`;
  });

  g.socket = cSocket;
};


