import { TimelineLite, Linear, Power2, Back } from 'gsap';
import {
  fadeColorByDepth,
  colorBalance,
  getNearness,
  sizeBalance,
  setScreenCoords,
  rotMatrixX,
  rotMatrixY,
  rotMatrixZ,
} from '../utils/network';
import wobbler from './wobbler';
import Network from '../actors/Network';
import config from '../constants/config';

// start a Network actor's internal timeline, which constantly repositions and alters the nodes and edges.
export function startTimeline(actor: Network) {
  return (function (dur: number, masterTl: TimelineLite) {
    const { edges, nodes, tl, container, rotation } = actor;
    const rotationDur = actor.props.rotation ? actor.props.rotation.duration : 10;

    masterTl.from(container, dur, {
      opacity: 0,
      ease: Power2.easeInOut,
    }, masterTl.time());

    nodes.forEach((node, n) => {
      if (!node.ref) return;
      if (node.opacity < 1)
        masterTl.set(node.ref, { opacity: node.opacity }, masterTl.time());
      tl.to(node.ref, rotationDur, {
        attr: {
          cx: '+=0',
          cy: '+=0',
          fill: '#FFF',
          r: '+=0',
        },
        modifiers: {
          cx: (xpos: number) => {
            return node.screenX;
          },
          cy: (ypos: number) => {
            return node.screenY;
          },
          fill: (col: string) => {
            return fadeColorByDepth(node.color, node.screenZ, colorBalance);
          },
          r: (val: number) => {
            return getNearness(node.screenZ, sizeBalance) * node.size;
          },
        },
        onUpdate: () => {
          requestAnimationFrame(() => setScreenCoords(node, rotation, wobbler.getPosition(n)));
        },
        ease: Linear.easeNone,
      }, 0);
    });
    edges.forEach((edge, n) => {
      if (!edge.ref) return;
      if (edge.opacity < 1)
        masterTl.set(edge.ref, { opacity: edge.opacity }, masterTl.time());
      tl.to(edge.ref, rotationDur, {
        attr: {
          x1: '+=0',
          y1: '+=0',
          x2: '+=0',
          y2: '+=0',
          stroke: '#FFF',
        },
        strokeWidth: '+=0',
        modifiers: {
          x1: (xpos: number) => {
            return edge.source.screenX;
          },
          y1: (ypos: number) => {
            return edge.source.screenY;
          },
          x2: (xpos: number) => {
            return edge.destination.screenX;
          },
          y2: (ypos: number) => {
            return edge.destination.screenY;
          },
          stroke: (col: string) => {
            edge.z = edge.source.screenZ < edge.destination.screenZ
              ? edge.source.screenZ
              : edge.destination.screenZ;
            return fadeColorByDepth(edge.color, edge.z, colorBalance);
          },
          strokeWidth: (w: number) => {
            return `${getNearness(edge.z, sizeBalance) * edge.strokeWidth}px`;
          }
        },
        ease: Linear.easeNone,
      }, 0);
    });

    if (actor.props.rotation)
      beginRotation(actor);
    tl.eventCallback('onComplete', () => tl.play(0), [], actor);
  });
}

// begin a Network actor's continuous rotation (self-repeating)
function beginRotation(actor: Network) {
  const { tl, rotation } = actor;
  const dur = actor.props.rotation ? actor.props.rotation.duration : 10;
  const axis = actor.props.rotation ? actor.props.rotation.axis : 'x';
  const getRotMatrix = axis === 'x'
    ? rotMatrixX
    : axis === 'y'
      ? rotMatrixY
      : rotMatrixZ;
  tl.to(actor.rotation, dur, {
    degrees: 360,
    modifiers: {
      degrees: (degs: number) => {
        // interrupt tween value and update our rotation matrix
        const rounded = Math.round(degs * config.rotationsPerDegree) / config.rotationsPerDegree;
        if (rotation.degrees !== rounded)
          rotation.matrix = getRotMatrix(degs);
        return rounded;
      }
    },
    ease: Linear.easeNone,
  }, 0);
}

export function showCluster(actor: Network, colorCenter: boolean) {
  return (function (duration: number, masterTl: TimelineLite, payload: { target: number, toOpacity?: number }) {
    const targetOpacity = payload.toOpacity === undefined ? 1 : payload.toOpacity;
    const nodeIdx = payload.target;
    const center = actor.nodes[nodeIdx];
    if (!center) return;
    const now = masterTl.time();
    const edges = actor.edges.filter(e => // only animate invisible edges connected to the center
      (e.source === center && e.destination.opacity === 0)
      || (e.destination === center && e.source.opacity === 0)
    );
    const dur = duration / (edges.length + 2);
    const edgeRefs = edges.map(e => e.ref);
    const nodes = edges.map(e => {
      if (e.source === center)
        return e.destination;
      return e.source;
    });
    const nodeRefs = nodes.map(n => n.ref);
    if (colorCenter) {
      masterTl.to(center, dur * 2, { color: '#FE0E2B', size: 30 }, now);
    }
    masterTl.staggerTo(edgeRefs, dur * 2, {
      opacity: targetOpacity
    }, dur, now + dur);
    masterTl.staggerTo(nodeRefs, dur * 2, {
      opacity: targetOpacity,
    }, dur, now + dur);
    masterTl.staggerTo(nodes, dur * 2, {
      size: 15,
      ease: Back.easeOut,
    }, dur, now + dur);
  });
}


