import * as React from 'react';
import styled from 'styled-components';
import director from '../director';
import autoBind from 'auto-bind';
import layers from '../constants/layers';
import config from '../constants/config';
import moment from 'moment-timezone';
import { TimelineLite, Power1 } from 'gsap';
import spacing from '../constants/spacing';
import random from '../utils/prng';

const _BGCOLOR = '#444';
const Canvas = styled.canvas`
  position: absolute;
  left: 0;
  top: 0;
  background: ${_BGCOLOR};
  z-index: ${layers.foreground + 100};
  height: ${spacing.fullHeight}px;
  width: ${spacing.fullWidth}px;
  opacity: 0;
`;

interface Props {
  actorName: string;
  entryDuration?: number;
}

// setup for canvas
const _cellWidth = 26;
const WIDTH = Math.round(spacing.fullWidth / 4),
  HEIGHT = Math.round(spacing.fullHeight / 4);
const NCOLS = Math.round(WIDTH / _cellWidth),
  NROWS = Math.round(HEIGHT / _cellWidth);

const cellWidth = WIDTH / NCOLS,
  cellHeight = HEIGHT / NROWS;

const lineWidth = cellWidth / 4;

const grid: number[][] = [];
// initialize a grid of random zeros and ones
for (let i = 0; i < NROWS; i++) {
  const row: number[] = [];
  for (let j = 0; j < NCOLS; j++) {
    row.push(Math.round(random.nextFloat()));
  }
  grid.push(row);
}
// twiddle these params to make the whole thing dynamic
const dParams = {
  colorOffset: 0,
  borderOffset: 360,
  sinOffset: 0
};
const toValues = {
  colorOffset: 360,
  borderOffset: 0,
  sinOffset: 360
};
const cycles = {
  colorOffset: 8,
  borderOffset: 6,
  sinOffset: 2,
};

function getStyle(col: number, row: number) {
  const fillColor = `hsl(${Math.abs((col - row) * 10 + Math.round(dParams.colorOffset)) % 360}, 66%, 50%)`;
  const strokeColor = `hsl(${Math.abs((row + col) * 10 + Math.round(dParams.borderOffset)) % 360}, 60%, 50%)`;
  return {
    fillColor,
    strokeColor,
    offsetAmt: 0.25 + Math.sin((col + dParams.sinOffset) / 6) * 0.23
  };
}

function drawShape(points: number[][], style: any, ctx: CanvasRenderingContext2D, strokeWidth: number) {
  const { strokeColor, fillColor } = style;
  // const { strokeColor, fillColor, offsetAmt } = style;
  ctx.lineWidth = strokeWidth;
  ctx.fillStyle = style.fillColor;
  ctx.strokeStyle = style.strokeColor;
  ctx.beginPath();

  points.forEach((pt, i) => {
    if (i === 0) return ctx.moveTo(pt[0], pt[1]);
    ctx.lineTo(pt[0], pt[1]);
    if (strokeColor !== 'none') ctx.stroke();
  });
  if (fillColor !== 'none') ctx.fill();
}

function draw(col: number, row: number, type: number, ctx: CanvasRenderingContext2D) {
  const top = row * cellHeight + lineWidth / 2;
  const left = col * cellWidth + lineWidth / 2;
  const bottom = top + cellHeight - lineWidth;
  const right = left + cellWidth - lineWidth;
  const style = getStyle(col, row);
  const xOffset = cellWidth * style.offsetAmt,
    yOffset = cellHeight * style.offsetAmt;

  ctx.lineWidth = 0;
  ctx.fillStyle = _BGCOLOR;
  ctx.strokeStyle = _BGCOLOR;

  ctx.beginPath();
  ctx.rect(col * cellWidth, row * cellHeight, cellWidth, cellHeight);
  ctx.fill();
  if (type === 0) {
    drawShape(
      [
        [left, top],
        [left + xOffset, top],
        [right, bottom - yOffset],
        [right, bottom],
        [right - xOffset, bottom],
        [left, top + yOffset],
        [left, top],
        [left + xOffset, top]
      ],
      style,
      ctx,
      lineWidth,
    );
  } else {
    drawShape(
      [
        [right, top],
        [right - xOffset, top],
        [left, bottom - yOffset],
        [left, bottom],
        [left + xOffset, bottom],
        [right, top + yOffset],
        [right, top],
        [right - xOffset, top]
      ],
      style,
      ctx,
      lineWidth,
    );
  }
}

class DiscoTenPrint extends React.Component<Props>  {
  container: Element;
  ctx: CanvasRenderingContext2D;
  tl: TimelineLite;

  constructor(props: Props) {
    super(props);
    autoBind(this);
    director.registerMotions(this, props.actorName,
      {
        enter: this.enter,
        exit: this.exit,
      }
    );
  }

  enter(duration: number, tl: TimelineLite) {
    tl.to(this.container, duration, {
      opacity: 1,
    }, tl.time());
    this.startUpdates();
  }

  startUpdates() {
    const tl = new TimelineLite();

    const tlLength = 180;
    const nUpdates = Math.round(tlLength * 12);

    for (let i = 0; i < nUpdates; i++) {
      tl.call(this.updateOnce, [], this, tlLength * (i / nUpdates));
    }
    ['borderOffset',
      'colorOffset',
      'sinOffset'
    ].forEach(key => {
      const fromVal = dParams[key];
      const toValue = toValues[key];
      const nCycles = cycles[key];
      for (let i = 0; i < nCycles; i++) {
        const dur = tlLength / nCycles;
        const params: any = { ease: Power1.easeInOut};
        if (i % 2 === 0) {
          params[key] = toValue;
          tl.to(dParams, dur, params, tlLength * (i / nCycles));
        } else {
          params[key] = fromVal;
          tl.to(dParams, dur, params, tlLength * (i / nCycles));
        }
      }
    });
    tl.eventCallback('onComplete', tl.restart, [], tl);
    this.tl = tl;
    director.addActorTimeline(this.props.actorName, tl);
  }

  exit(duration: number, tl: TimelineLite) {
    tl.to(this.container, duration, {
      opacity: 0,
    }, tl.time());
  }

  timeUpdate() {
    const times = config.timezones.map((tz) => moment().tz(tz.key).format('HH:mm'));
    this.setState({ times });
  }

  updateOnce() {
    grid.forEach((row, i) => {
      row.forEach((val, j) => {
        draw(j, i, val, this.ctx);
      });
    });
  }

  setupCanvas(r: HTMLCanvasElement) {
    if (!r) return;
    // setup canvas
    const ctx = r.getContext('2d') as CanvasRenderingContext2D;
    this.container = r;
    this.ctx = ctx;

    r.setAttribute('width', `${WIDTH}`);
    r.setAttribute('height', `${HEIGHT}`);

    grid.forEach((row, i) => {
      row.forEach((val, j) => {
        draw(j, i, val, this.ctx);
      });
    });

  }
  render() {
    return (
      <Canvas
        innerRef={this.setupCanvas}
      />
    );
  }
}

export default DiscoTenPrint;



