import * as React from 'react';
import styled from 'styled-components';
import director from '../director';
import { TimelineLite, Power2 } from 'gsap';
import autoBind from 'auto-bind';
import { simpleExit } from '../motions';
import layers from '../constants/layers';
import { getPositionedThreatActors, ThreatActorPositioned } from '../utils/positioning';
import { getIntroThreatActors } from '../utils/threatActors';
import China from '../assets/countries/svg_china.svg';
import India from '../assets/countries/svg_india.svg';
import Indonesia from '../assets/countries/svg_indonesia.svg';
import Iran from '../assets/countries/svg_iran.svg';
import Russia from '../assets/countries/svg_russia.svg';
import NorthKorea from '../assets/countries/svg_n_korea.svg';
import Lebanon from '../assets/countries/svg_lebanon.svg';
import Pakistan from '../assets/countries/svg_pakistan.svg';
import Palestine from '../assets/countries/svg_palestine.svg';
import Tunisia from '../assets/countries/svg_tunisia.svg';
import Turkey from '../assets/countries/svg_turkey.svg';
import Ukraine from '../assets/countries/svg_ukraine.svg';
import Syria from '../assets/countries/svg_syria.svg';
import UAE from '../assets/countries/svg_uae.svg';
import USA from '../assets/countries/svg_usa.svg';
import Venezuela from '../assets/countries/svg_venezuela.svg';
import Vietnam from '../assets/countries/svg_vietnam.svg';

const _countries = [
  'China',
  'India',
  'Russia',
  'Indonesia',
  'Iran',
  'Lebanon',
  'North Korea',
  'Pakistan',
  'Palestine',
  'Syrian Arab Republic',
  'Tunisia',
  'Turkey',
  'Ukraine',
  'United Arab Emirates',
  'United States',
  'Venezuela',
  'Vietnam',
];

const _countryImgs = [
  China,
  India,
  Russia,
  Indonesia,
  Iran,
  Lebanon,
  NorthKorea,
  Pakistan,
  Palestine,
  Syria,
  Tunisia,
  Turkey,
  Ukraine,
  UAE,
  USA,
  Venezuela,
  Vietnam,
];

const Outer = styled.div`
  position: absolute;
  left: -894px;
  top: -175px;
  width: 2400px;
  height: 1000px;
  transform: scale(0.61);
  z-index: ${layers.foreground + 10};
`;

const Img = styled.img`
  position: absolute;
  left: 0;
  top: 0;
  width: 3125px;
  height: auto;
  transform: scaleY(0.96);
  opacity: 0;
`;

export interface Props {
  actorName: string;
  entryDuration?: number;
  getVertical: () => string;
}

class Countries extends React.Component<Props>  {
  container: Element;
  children: Element[];
  countryNames: string[];

  constructor(props: Props) {
    super(props);
    autoBind(this);

    director.registerMotions(this, props.actorName,
      {
        enter: this.enter,
        transition: this.transition,
        exit: simpleExit(this, '-=20', '+=0'),
      }
    );
    const vertical = props.getVertical();
    // get the names of the countries that will be drawn, in order of drawing
    const threatActorsPositioned = getPositionedThreatActors(vertical);
    this.countryNames = (getIntroThreatActors(vertical) as string[])
      .filter(actor => threatActorsPositioned.find(a => a.name === actor) !== undefined)
      .map((actor, n) => {
        const ac = threatActorsPositioned.find(a => a.name === actor) as ThreatActorPositioned;
        return ac.country;
      });
    this.children = [];
  }

  enter(duration: number, tl: TimelineLite) {
    const dur = duration * 0.3;
    const delay = duration / this.countryNames.length;

    tl.staggerTo(this.children, dur, {
      opacity: 0.7,
    }, delay, tl.time());
  }

  transition(dur: number, tl: TimelineLite, payload: any) {
    const { vars } = payload;
    const now = tl.time();
    tl.to(this.container, dur, {
      ...vars,
      ease: Power2.easeInOut,
    }, now);
  }

  addChildRef(ref: Element) {
    if (ref)
      this.children.push(ref);
  }

  render() {
    const { countryNames } = this;
    return (
      <Outer
        innerRef={ref => { if (ref) this.container = ref; }}
      >
        {
          _countries.map((country, i) => {
            if (countryNames.includes(country))
              return (
                <Img
                  key={i}
                  src={_countryImgs[i]}
                  innerRef={this.addChildRef}
                />
              );
            else return null;
          })
        }
      </Outer>
    );
  }
}

export default Countries;



