import { Script, Scene } from '../typings';
import ransomware from './ransomware';
import storyIntro from './storyIntro';
import lateralMovementFlow from './lateral';
import phishing from './phishing';
import useCases from './usecases';

// remove the persisted actors from the beginning of a scene
const removeIntroActorsResetSidePanel = (scene: Scene): Scene => {
  return {
    ...scene,
    startActors: ['SidePanel'],
    persistActors: [],
    directives: [
      ...scene.directives,
      { wait: 3, action: { type: 'motion', payload: { actor: 'SidePanel', motion: 'toFullCover', duration: 2 } } }
    ]
  };
};

const getScript = () => {
  const script: Script = new Map([
    ['useCases', useCases],
    ['threatActors', storyIntro],
    ['threatActors-alone', removeIntroActorsResetSidePanel(storyIntro)],
    ['ransomware', ransomware],
    ['lateral', lateralMovementFlow],
    ['phishing', phishing],
    ['ransomware-alone', removeIntroActorsResetSidePanel(ransomware)],
    ['lateral-alone', removeIntroActorsResetSidePanel(lateralMovementFlow)],
    ['phishing-alone', removeIntroActorsResetSidePanel(phishing)],
    ['test', useCases],
    ['disco', useCases],
  ]);
  return script;
};

export default getScript;


