import * as React from 'react';
import styled from 'styled-components';
import director from '../director';
import { TimelineLite, Power2 } from 'gsap';
import autoBind from 'auto-bind';
import layers from '../constants/layers';
import { simpleExit } from '../motions';
import theme from '../constants/theme';

const Outer = styled.div`
  position: absolute;
  left: ${(p: { left: number }) => p.left}px;
  z-index: ${layers.foreground + 2};
`;

const Info = styled.div`
  position: absolute;
  top: ${(p: { top: number, left: number }) => p.top}px;
  left: ${p => p.left}px;
  font-size: 33px;
  font-weight: 600;
  white-space: nowrap;
`;

const InfoInfected = styled.div`
  position: absolute;
  top: ${(p: { top: number, left: number }) => p.top}px;
  left: ${p => p.left}px;
  font-size: 33px;
  font-weight: 400;
  opacity: 0;
  white-space: nowrap;
`;

interface Props {
  actorName: string;
  entryDuration?: number;
  left: number;
  childEntryAmt: number;
  data: string[];
  positions: ({
    x: number;
    y: number;
    start: number;
  })[];
}

class DirectoryInfoArray extends React.Component<Props>  {
  container: Element;
  children: Element[];
  infoInfected: Element[];

  constructor(props: Props) {
    super(props);
    autoBind(this);
    this.children = [];

    director.registerMotions(this, props.actorName,
      {
        enter: this.enter,
        exit: simpleExit(this, 50, 0),
        infect: this.infect,
      }
    );
  }

  enter(dur: number, tl: TimelineLite) {
    const { children } = this;
    const { childEntryAmt, positions } = this.props;
    const childEntryDur = childEntryAmt * dur;
    const now = tl.time();

    positions.forEach((pos, i) => {
      tl.from(children[i], childEntryDur, {
        opacity: 0,
        x: -20,
        ease: Power2.easeOut,
      }, pos.start * dur + now);
    });

  }
  
  infect(dur: number, tl: TimelineLite) {
    const { children, infoInfected } = this;

    if (!children[3]) return;
    const now = tl.time();
    tl.to(children[3], dur, {
      scale: 1.2,
      transformOrigin: 'left center',
      color: theme.directoryInfoArrayRed,
      ease: Power2.easeInOut
    }, now);
    tl.to(infoInfected, dur / 2, {
      opacity: 1,
      ease: Power2.easeInOut
    }, now + dur / 2);
  }

  addChildRef(ref: SVGLineElement) {
    if (ref)
      this.children.push(ref);
  }
  render() {
    const { left, positions, data } = this.props;

    return (
      <Outer
        left={left}
        innerRef={ref => { if (ref) this.container = ref; }}
      >
        {positions.map((p, n) => (
          <Info
            left={p.x}
            top={p.y}
            innerRef={this.addChildRef}
            key={n}
          >
            {data[n]}
          </Info>
        ))}
        {positions[3] && <InfoInfected
          left={positions[3].x}
          top={positions[3].y - 50}
          innerRef={ref => { if (ref) this.infoInfected = ref; }}
        >
          {'OIJOIWJEFOIJGOIWEUF'}
        </InfoInfected>}
      </Outer>
    );
  }
}

export default DirectoryInfoArray;



