import * as React from 'react';
import styled from 'styled-components';
import director from '../director';
import { TimelineLite, Power2 } from 'gsap';
import autoBind from 'auto-bind';
import { simpleExit, simpleEnter } from '../motions';

const Outer = styled.div`
  position: absolute;
  left: ${(p: { left: number, top: number, startOpacity: number, zIndex: number }) => p.left}px;
  top: ${p => p.top}px;
  z-index: ${p => p.zIndex};
  opacity: ${p => p.startOpacity};
`;

export interface ImgActorProps {
  actorName: string;
  entryDuration?: number;
  top: number;
  left: number;
  width: number;
  startOpacity: number;
  zIndex: number;
  imgSrc: any;
  enterPos: { x: number, y: number};
  exitPos: { x: number, y: number};
}

class ImgActor extends React.Component<ImgActorProps>  {
  container: Element;
  children: Element[];

  constructor(props: ImgActorProps) {
    super(props);
    autoBind(this);

    director.registerMotions(this, props.actorName,
      {
        enter: simpleEnter(this, `+=${props.enterPos.x}`, `+=${props.enterPos.y}`),
        transition: this.transition,
        exit: simpleExit(this, `+=${props.exitPos.x}`, `+=${props.exitPos.y}`),
      }
    );
  }

  transition(dur: number, tl: TimelineLite, payload: any) {
    const { vars } = payload;
    const now = tl.time();
    tl.to(this.container, dur, {
      ...vars,
      ease: Power2.easeInOut,
    }, now);
  }
  
  addChildRef(ref: Element) {
    if (ref)
      this.children.push(ref);
  }

  render() {
    const { left, width, startOpacity, top, zIndex, imgSrc } = this.props;
    return (
      <Outer
        left={left}
        top={top}
        zIndex={zIndex}
        startOpacity={startOpacity}
        innerRef={ref => { if (ref) this.container = ref; }}
      >
        <img style={{ width }} src={imgSrc} />
      </Outer>
    );
  }
}

export default ImgActor;



