const autoBind = require('auto-bind');
import { TimelineLite } from 'gsap';
import { Script, Action, Directive, Scene } from '../typings';
import config from '../constants/config';
import prng from '../utils/prng';
// import g from '../constants/config';

class Director {
  public subscriptions: { actorName: string; actionHandler: ((action: Action) => void); }[];
  public tl: TimelineLite;
  public loop: boolean;

  private scenes: Scene[];
  private sceneOrder: string[];
  private sceneIdx: number;
  private actorTimelines: Map<string, TimelineLite>;

  onComplete: () => void;
  onStart: () => void;

  constructor() {
    autoBind(this);
    this.subscriptions = [];
    this.tl = new TimelineLite();
    this.tl.timeScale(config.timeScale);
    this.loop = true;
    this.actorTimelines = new Map<string, TimelineLite>();
  }

  setScript(script: Script, sceneOrder: string[]) {
    this.sceneOrder = sceneOrder;
    this.scenes = sceneOrder
      .map(sName => script.get(sName) as Scene);
    this.sceneIdx = 0;
    this.populateTimeline();
  }

  subscribe(subscriber: { actorName: string; actionHandler: ((action: Action) => void); }) {
    this.subscriptions.push(subscriber);
  }

  // in order to leave the 'persistent' actors between loops or restarts, cut down the subscriptions to those matching a set list of actor names
  cleanSubscriptions(subscribersToLeave: string[]) {
    const subs = this.subscriptions.filter(subscriber =>
      subscribersToLeave.includes(subscriber.actorName) || subscriber.actorName === 'Stage'
    );
    delete this.subscriptions;
    this.subscriptions = subs;
  }

  dispatch(action: Action) {
    this.subscriptions.forEach(subscriber => subscriber.actionHandler(action));
  }

  // signal the Stage to remove non-scene actors and add whatever actors aren't present yet
  createEntryDirective(scene: Scene) {
    const removeActors = {
      wait: 0,
      action: {
        type: 'removeNonSceneActors', payload: {
          startActors: scene.startActors || [], // actors that should be there before the scene starts
          persistActors: scene.persistActors || [], // actors that are allowed to stay if they're already present
        }
      }
    };
    if (scene.startActors)
      return [removeActors, {
        wait: 0,
        actions: scene.startActors.map((actorName) => ({
          type: 'enter',
          payload: {
            actor: actorName,
            duration: 2,
          }
        }))
      }] as Directive[];
    return [removeActors] as Directive[];
  }

  populateSceneTimeline(scene: Scene, sceneName: string) {
    // put each entry/exit/add/remove/motion into a timeline as functions
    const { dispatch } = this;
    let elapsed = 0;
    const tl = new TimelineLite();
    tl.call(prng.restart, [], prng, 0);
    tl.smoothChildTiming = true;
    const directives = this.createEntryDirective(scene).concat(scene.directives);
    // parse each script item's action(s) into the timeline
    directives.forEach((directive: Directive) => {
      const { wait, action, actions, stagger } = directive;
      if (wait)
        elapsed += wait;
      let actionList = actions !== undefined ? actions : [action];
      const staggerAmt = stagger || 0;
      let longestDur = 0;
      // place simultaneous action's events into the timeline
      actionList.forEach((a: Action, i: number) => {
        // keep longest action's duration to add to elapsed time later
        const delay = i * staggerAmt;
        const effectiveDur = a.payload.duration + delay;
        if (effectiveDur > longestDur)
          longestDur = effectiveDur;
        switch (a.type) {
          case 'enter': {
            return tl.call(dispatch as () => void,
              [{
                type: 'enter',
                payload: a.payload
              }],
              this,
              elapsed + delay
            );
          }
          case 'exit': {
            // add exit beginning and exit end (removal of actor)
            return tl.call(dispatch as () => void,
              [{
                type: 'exit',
                payload: a.payload
              }],
              this,
              elapsed + delay
            )
              .call(dispatch as () => void,
                [{
                  type: 'remove',
                  payload: a.payload
                }],
                this,
                elapsed + delay + a.payload.duration,
            );
          }
          case 'motion': {
            return tl.call(dispatch as () => void,
              [{
                type: 'motion',
                payload: a.payload
              }],
              this,
              elapsed + delay
            );
          }
          case 'removeNonSceneActors': {
            return tl.call(dispatch as () => void,
              [{
                type: a.type,
                payload: a.payload
              }],
              this,
              elapsed + delay
            );
          }
          case 'begin': {
            return tl.call(dispatch as () => void,
              [{
                type: a.type,
                payload: a.payload
              }],
              this,
              elapsed + delay
            );
          }
          case 'end': {
            return tl.call(dispatch as () => void,
              [{
                type: a.type,
                payload: a.payload
              }],
              this,
              elapsed + delay
            );
          }
          default:
            return;
        }
      });
      elapsed += longestDur;
    });
    tl.eventCallback('onComplete', this.onSceneComplete, [], this);

    if (this.tl.duration() === 0 && scene.wait < 0) {
      this.tl.add(sceneName, `+=0`);
      this.tl.add(tl, `+=0`);
    } else {
      this.tl.add(sceneName, `+=${scene.wait}`);
      this.tl.add(tl, `+=${scene.wait}`);
    }
  }

  populateTimeline() {
    this.clearTimelines();
    this.scenes.forEach((scene, i) => {
      this.populateSceneTimeline(scene, this.sceneOrder[i]);
    });
    this.tl.eventCallback('onComplete', this.onScriptComplete, [], this);
    this.tl.pause();
  }

  onSceneComplete() {
    const idx = this.sceneIdx + 1;
    this.sceneIdx = idx;
  }

  onScriptComplete() {
    if (this.onComplete)
      this.onComplete();
    if (this.loop) {
      this.populateTimeline();
      this.begin();
    }
  }

  goToScene(scene: string | number) {
    this.tl.pause();
    if (typeof scene === 'number') {
      this.sceneIdx = scene;
      this.tl.play(this.sceneOrder[scene]);
    } else {
      this.sceneIdx = this.sceneOrder.findIndex(s => s === scene);
      this.tl.play(scene);
    }
    if (this.sceneIdx === 0 && this.onStart)
      this.onStart();
  }

  begin() {
    this.goToScene(0);
    // if it's there, send a playSync to the controller
    // if (g.socket !== undefined) {
    //   const time = Date.now();
    //   g.socket.send({ type: 'syncPlay', time });
    // }
  }

  // create an onAction function for the actor and subscribe it to the Director
  registerMotions(actor: any, actorName: string, handlers: any) {  // replace these 'anys'
    const director = this;
    // register actor's entry motion
    actor.componentDidMount = (function () {
      if (handlers.enter) {
        handlers.enter(actor.props.entryDuration || 0, director.tl);
      }
    }).bind(actor);

    // register actor's timeline cleanup
    actor.componentWillUnmount = (function () {
      if (actor.tl) {
        director.removeActorTimeline(actorName);
        actor.tl.clear();
        actor.tl.eventCallback('onComplete', () => 0);
        delete actor.tl;
      }
    }).bind(actor);

    // register all the other action handlers
    const actionHandler = (function (action: Action) {
      const actionActor = action.payload ? action.payload.actor : '';

      if (actionActor === actorName) {
        if (action.type === 'exit' && handlers.exit) {
          return handlers.exit(action.payload.duration, director.tl, action.payload);
        }
        if (action.type === 'motion' && action.payload.motion in handlers) {
          const handler = handlers[action.payload.motion];
          handler(action.payload.duration, director.tl, action.payload);
        }
      }
    }).bind(actor);
    this.subscribe({ actorName, actionHandler });
  }

  // for the actor component's registered timelines.
  addActorTimeline(actorName: string, tl: TimelineLite) {
    this.actorTimelines.set(actorName, tl);
  }
  removeActorTimeline(actorName: string) {
    this.actorTimelines.delete(actorName);
  }
  clearTimelines() {
    this.tl.clear(true);
    this.actorTimelines.forEach((_, key) => {
      this.actorTimelines.delete(key);
    });
  }
  clearActors() {
    this.dispatch({ type: 'clearStage', payload: {} });
  }
  pauseActorTimelines() {
    this.actorTimelines.forEach((tl: TimelineLite, key: string) => {
      tl.pause();
    });
  }
  playActorTimelines() {
    this.actorTimelines.forEach((tl: TimelineLite) => {
      tl.play();
    });
  }
}

export default new Director();


